import { Suspense } from "react";
import { ChakraProvider, ColorModeScript } from "@chakra-ui/react";
import { ManagerProvider } from "./context/managerContext";
import {
  HashRouter as Router,
  Switch,
  Route,
  useRouteMatch,
} from "react-router-dom";
import theme from "./theme";

// ROUTES
import Manager from "./pages/Manager";
import Web3ReactManager from "./components/Web3/Web3ReactManager";
import { ApplicationProvider } from "./context/appContext";
import { QueryClient, QueryClientProvider } from "react-query";
import BlockUpdater from "./context/blockUpdater";
import NotificationsUpdater from "./context/NotificationsUpdater";
import Enviroment from "./pages/Enviroment";
import { Strategies } from "./components/Strategies";
import Agreement from "./pages/Agreement";
import CreatePortfolio from "./pages/CreatePortfolio";
import Swap from "./pages/Swap";
import Overview from "./components/Overview";

const queryClient = new QueryClient();

export default function App() {
  return (
    <Suspense fallback={null}>
      <Router>
        <ChakraProvider theme={theme}>
          <ColorModeScript initialColorMode="dark" />
          <QueryClientProvider client={queryClient}>
            <ApplicationProvider>
              <ManagerProvider>
                <BlockUpdater />
                <NotificationsUpdater />
                <Web3ReactManager>
                  <GlobalRoutes />
                </Web3ReactManager>
              </ManagerProvider>
            </ApplicationProvider>
          </QueryClientProvider>
        </ChakraProvider>
      </Router>
    </Suspense>
  );
}

function GlobalRoutes() {
  return (
    <Switch>
      <Route path="/app">
        <Manager>
          <ManagerRoutes />
        </Manager>
      </Route>
      <Route path="/">
        <Enviroment />
      </Route>
    </Switch>
  );
}

function ManagerRoutes() {
  let { path } = useRouteMatch();
  return (
    <>
      <Switch>
        <Route exact path={path}>
          <Overview />
        </Route>
        <Route exact path={`${path}/new`}>
          <CreatePortfolio />
        </Route>
        <Route exact path={`${path}/swap`}>
          <Swap />
        </Route>
        <Route path={`${path}/overview`}>
          <Overview />
        </Route>
        <Route path={`${path}/strategies`}>
          <StrategiesRoutes />
        </Route>
        <Route path={`${path}/agreement`}>
          <Agreement />
        </Route>
        <Route path={`${path}/:address`}>
          <Overview />
        </Route>
        <Route path={`${path}/:tab`}>
          <StrategiesRoutes />
        </Route>
      </Switch>
    </>
  );
}

function StrategiesRoutes() {
  let { path } = useRouteMatch();
  return (
    <Switch>
      <Route exact path={path}>
        <Strategies />
      </Route>
      <Route path={`${path}/:strategy`}>
        <Strategies />
      </Route>
    </Switch>
  );
}
