import {
  Menu,
  MenuButton,
  MenuList,
  MenuItem,
  Flex,
  IconButton,
  useColorModeValue,
  useColorMode,
  Box,
  MenuOptionGroup,
  MenuItemOption,
  useToast
} from "@chakra-ui/react";
import { SETTINGS } from "../../helpers/settings";
import { BsThreeDots } from "react-icons/bs";
import { FaMoon, FaSun } from "react-icons/fa";
import { RiAddBoxLine, RiFeedbackFill } from "react-icons/ri";
import { BiPlanet } from "react-icons/bi";
import { Link as RouterLink } from "react-router-dom";
import { SLIPPAGE_UI_OPTIONS, URL_FEEDBACK } from "../../constants";
import {
  useApplicationDispatch,
  useApplicationState,
} from "../../context/appContext";
import { mulBN } from "../../helpers";
import { useTranslation } from "react-i18next";
import { useStyles } from "../../hooks/useStyles";
import { Hr  } from "../strategy/Hr";

function HeaderMenu({ showWeb3 }) {
  const { t } = useTranslation();
  const { buttonBorderStyle, bgModal, colorModalTitle } = useStyles();

  const toast = useToast();

  const bgHover = useColorModeValue("#e2e8f0", "#ffffff29");

  const { toggleColorMode: toggleMode } = useColorMode();
  const SwitchIcon = useColorModeValue(FaMoon, FaSun);

  const openFeedback = () => {
    window.open(URL_FEEDBACK, "_blank");
    toast({
      title: `Opening feedback form`,
      ...SETTINGS.toast,
    });
  };

  return (
    <Menu>
      <MenuButton
        as={IconButton}
        aria-label="Options"
        icon={<BsThreeDots />}
        variant="ghost"
        color="current"
        fontSize="lg"
        rounded="full"
        border={buttonBorderStyle}
        _hover={{ background: bgHover }}
      />
      <MenuList rounded="md" bg={bgModal} p={0} mt={2} color={colorModalTitle}>
        <SlippageOptions />
        <Hr mt="0" />

        <MenuItem
          as={RouterLink}
          to="/app/new"
          h="4rem"
          onClick={() => console.log("manager")}
        >
          <MenuBtn icon={<RiAddBoxLine />} title={"New Portfolio"} />
        </MenuItem>
        <Hr mt="0" />

        {!showWeb3 && (
          <>
          <MenuItem
            as={RouterLink}
            to="/app"
            h="4rem"
            onClick={() => console.log("manager")}
          >
            <MenuBtn icon={<BiPlanet />} title={"Manager App"} />
          </MenuItem>
        <Hr mt="0" />
        </>

        )}
        <MenuItem h="4rem" onClick={openFeedback}>
          <MenuBtn icon={<RiFeedbackFill />} title={"Feedback"} />
        </MenuItem>
        <Hr mt="0" />


        <MenuItem h="4rem" onClick={toggleMode}>
          <MenuBtn icon={<SwitchIcon />} title={"Change theme"} />
        </MenuItem>
      </MenuList>
    </Menu>
  );
}

export default HeaderMenu;

const MenuBtn = ({ icon, title, color }) => {
  return (
    <Flex justify="space-between" w="full" align="center" color={color}>
      {title}
      <Box
        py="1rem"
        px={4}
        size="lg"
        fontSize="lg"
        bg="#ffffff08"
        variant="ghost"
        color="current"
      >
        {icon}
      </Box>
    </Flex>
  );
};

const SlippageOptions = () => {
  const { slippage } = useApplicationState();
  const { updateGlobalSlippage } = useApplicationDispatch();
  return (
    <MenuOptionGroup
      defaultValue={slippage}
      title="Global Slippage"
      type="radio"
      fontSize="lg"
      fontWeight="400"
    >
      {SLIPPAGE_UI_OPTIONS.map((option) => {
        return (
          <MenuItemOption
            onClick={() => updateGlobalSlippage(option)}
            value={option}
            key={option}
          >
            {mulBN(option, '100')}%
          </MenuItemOption>
        );
      })}
    </MenuOptionGroup>
  );
};
