import {
  Modal,
  ModalOverlay,
  ModalContent,
  ModalHeader,
  ModalFooter,
  ModalBody,
  ModalCloseButton,
  Button,
  useDisclosure, Flex
} from "@chakra-ui/react";
import PortfoliosEditor from "./PortfoliosEditor";
import { useStyles } from "../hooks/useStyles";
import { SETTINGS } from "../helpers/settings";

function EditPortfolioModal({
  onClick,
  children,
  type,
  isDisabled = false,
  w,
}) {
  const { isOpen, onOpen, onClose } = useDisclosure();
  const { bgModal, modalBorderStyle, colorModalTitle } = useStyles();

  const onOpenModal = () => {
    onOpen();
    // onClick();
  };

  return (
    <>
      <Flex
        w={w}
        variant="ghost"
        onClick={!isDisabled ? onOpenModal : undefined}
        align="center"
        rounded="0"
        m="0"
        p="0"
        // isDisabled={isDisabled}
        _hover={{ bg: "none" }}
        _pressed={{ bg: "none" }}
        _focusVisible={{ bg: "none" }}
      >
        {children}
      </Flex>
      <Modal onClose={onClose} isOpen={isOpen} isCentered size="xl">
        <ModalOverlay backdropFilter="blur(2px)!important" />
        <ModalContent bg={bgModal} border={modalBorderStyle} rounded="0">
          <ModalHeader color={colorModalTitle} px="4">Edit Portfolios</ModalHeader>
          <ModalCloseButton color={SETTINGS.VIOLET_DARK} mt="2" />
          <ModalBody p="0">
            <PortfoliosEditor onClose={onClose} />
          </ModalBody>
        </ModalContent>
      </Modal>
    </>
  );
}

export default EditPortfolioModal;
