import { getSubgraphqlURI } from "../helpers/enviroment";
import { useManagerState } from "../context/managerContext";
import { useQuery } from "react-query";
import { request, gql } from "graphql-request";

const useAgreement = (addressSelected = undefined) => {
  const { portfolioSelected } = useManagerState();
  const address = addressSelected
    ? addressSelected.toLowerCase()
    : portfolioSelected?.address?.toLowerCase();

  return useQuery(["agreement", address], () => fetchAgreement(address));
};

const fetchAgreement = async (agreementAddress) => {
  const { agreement } = await request(
    getSubgraphqlURI(),
    gql`
        {
          agreement(id: "${agreementAddress}") {
            id
            name
            withdrawers
            maxSlippage
            managers {
              id
            }
            allowedStrategies
            customStrategies
            allowedTokens
            customTokens
            portfolio {
              id
              depositFee
              performanceFee
              feeCollector
                account {
                  id
                  vault {
                    id
                  }
                  balances {
                    id
                  }
                  strategies {
                    id
                  }
                }
              }
    
          }
        }
      `
  );
  return agreement;
};

export default useAgreement;
