import { Flex, Grid, Spinner } from "@chakra-ui/react";
import { useParams } from "react-router";
import StrategyCard from "../components/StrategyCard";
import { TabTitle } from "./typography/TabTitle";
import { useTranslation } from "react-i18next";
import { useWhitelistedStrategies } from "../hooks/useWhitelistedStrategies";
import { useEthers } from "@usedapp/core";
import { BoxTitle } from "./typography/BoxTitle";
import { useStyles } from "../hooks/useStyles";

export const Strategies = () => {
  const { t } = useTranslation();
  const { account, chainId } = useEthers();
  const { buttonBorderStyle } = useStyles();

  const { data: whitelistedStrategies, isLoading } = useWhitelistedStrategies();
  // console.log("whitelistedStrategies", whitelistedStrategies);

  // strategy from the url to redirect
  let { strategy } = useParams();

  return (
    <Flex align="flex-start" w="full" py={4} direction="column">
      <TabTitle>{t("strategiesDescription")}</TabTitle>
      <Grid
        templateColumns={{ md: "repeat(4, 1fr)", base: "repeat(1, 1fr)" }}
        w="full"
        gridRowGap="0"
        gridAutoRows="1fr"
        borderTop={buttonBorderStyle}
        mt="6"
      >
        {isLoading ? (
          <LoadingStrategyMessage />
        ) : whitelistedStrategies?.length > 0 ? (
          whitelistedStrategies.map((st) => {
            return (
              <StrategyCard
                key={st?.id}
                strategy={st}
                param={strategy}
                mx={4}
                w="full"
                chainId={chainId}
                account={account}
                manager={true}
              />
            );
          })
        ) : (
          <NoStrategiesMessage />
        )}
      </Grid>
    </Flex>
  );
};

const NoStrategiesMessage = () => {
  const { t } = useTranslation();

  return <BoxTitle px="6">{t("noStrategy")} 🤷‍♂️</BoxTitle>;
};

const LoadingStrategyMessage = () => {
  const { t } = useTranslation();

  return (
    <Flex p="6" justify="flex-start" align="center">
      <BoxTitle px="6">{t("loadingStrategy")}</BoxTitle>
      <Spinner />
    </Flex>
  );
};
