import { isAddress } from ".";
import { TRANSACTION_BATCH } from "../constants";

export async function encodeSwapCall(
  contract,
  account,
  tokenIn,
  tokenOut,
  amountIn,
  slippage,
  data = "0x"
) {
  try {
    const tx = await contract.populateTransaction.swap(
      isAddress(account),
      isAddress(tokenIn),
      isAddress(tokenOut),
      amountIn,
      slippage,
      data
    );
    return tx || "0x";
  } catch (error) {
    console.log("encodeSwapCall", error, 
    account,
    tokenIn,
    tokenOut,
    amountIn,
    slippage,
    );
    return null;
  }
}

export async function encodeDepositCall(contract, account, token, amount) {
  try {
    const tx = await contract.populateTransaction.deposit(
      isAddress(account),
      isAddress(token),
      amount
    );
    return tx || "0x";
  } catch (error) {
    console.log( "error encodeDepositCall", contract,
    account, token, amount)
    return null;
  }
}

