export async function composeTransactionHelper(composition, contract, tx) {
  let compTransaction = [];
  const promises = Promise.all(
    composition.map((comp) => {
      console.log("composeTransaction ", comp, composition, tx);

      if (comp === TRANSACTION_BATCH.DEPOSIT) {
        return encodeDepositCall(
          contract,
          tx.account,
          tx.tokenIn,
          tx.amount
        ).then((call) => compTransaction.push(call));
      } else if (comp === TRANSACTION_BATCH.JOIN) {
        return encodeJoinCall(
          contract,
          tx.account,
          tx.strategy,
          tx.amount,
          "0x99"
        ).then((call) => compTransaction.push(call));
      } else if (comp === TRANSACTION_BATCH.PARTIAL_DEPOSIT) {
        return encodeDepositCall(
          contract,
          tx.account,
          tx.tokenIn,
          tx.onlyAmountToDepositWithoutFee
        ).then((call) => compTransaction.push(call));
      } else if (comp === TRANSACTION_BATCH.SWAP) {
        console.log('useCompositionOptions.SWAP')
        return encodeSwapCall(
          contract,
          tx.account,
          tx.tokenIn,
          tx.tokenOut,
          tx.amount,
          tx.slippage,
          "0x"
        ).then((call) => compTransaction.push(call));
      } else if (comp === TRANSACTION_BATCH.EXIT) {
        return encodeExitCall(
          contract,
          tx.account,
          tx.strategy,
          tx.ratio,
          false,
          "0x"
        ).then((call) => compTransaction.push(call));
      }
    })
  ).then(() => {
    return compTransaction;
  });

  return promises;
}
