import { useState } from "react";
import {
  NumberInputField,
  NumberInput,
  NumberInputStepper,
  NumberIncrementStepper,
  NumberDecrementStepper,
} from "@chakra-ui/react";
import { useStyles } from "../hooks/useStyles";

export const FeeInput = (props) => {
  const { buttonBorderStyle } = useStyles();
  const [setValue] = useState("0.00");

  const format = (val) => val + `%`;
  const parse = (val) => val.replace(/^\$/, "");

  return (
    <NumberInput
      onChange={(valueString) => setValue(parse(valueString))}
      value={format}
      max={100}
      precision={2}
      step={0.25}
      w="8rem"
      {...props.field}
    >
      <NumberInputField border={buttonBorderStyle} rounded="sm" />
      <NumberInputStepper>
        <NumberIncrementStepper />
        <NumberDecrementStepper />
      </NumberInputStepper>
    </NumberInput>
  );
};
