import { useEffect } from "react";
import {
  Text,
  Flex,
  Table,
  Thead,
  Tbody,
  Tr,
  Th,
  Td,
  TableCaption,
  Image,
  Spinner,
  Stat,
  StatHelpText,
  StatArrow,
  useToast,
  useClipboard,
  Button,
} from "@chakra-ui/react";
import { useAllTokens } from "../hooks/useAllTokens";
import useUSDCPrice from "../hooks/useUSDCPrice";
import { useTokensBalance } from "../hooks/useTokensBalance";
import {
  formatBNPrice,
  formatPrice,
  getAssetImg,
  getBalanceByToken,
  greaterThanMax,
  isAddress,
  subBN,
  sumBN,
} from "../helpers";
import { useManagerDispatch } from "../context/managerContext";
import {
  useTransferToAgreementModalToggle,
  useWithdrawFromVaultModalToggle,
} from "../hooks";
import { useTranslation } from "react-i18next";
import { ChainId, useEthers } from "@usedapp/core";
import useStrategiesBalance from "../hooks/useStrategiesBalance";
import { useStyles } from "../hooks/useStyles";
import useVaultBalance from "../hooks/useVaultBalance";
import { SETTINGS } from "../helpers/settings";
import { Link } from "react-router-dom";
import { URL_FEEDBACK } from "../constants";
import { AiOutlineDownload, AiOutlineExport } from "react-icons/ai";
import { isDevelopment, isProduction } from "../helpers/enviroment";

export function BalanceTable() {
  const { t } = useTranslation();

  const { chainId } = useEthers();
  const allTokens = useAllTokens();
  const tokensBalance = useTokensBalance();
  const strategiesBalance = useStrategiesBalance();
  const { data: vaultBalance, isLoading: vaultBalanceLoading } =
    useVaultBalance();
  // console.log("BalanceTable vaultBalance", vaultBalance);
  // console.log("BalanceTable strategiesBalance", strategiesBalance &&
  // strategiesBalance.invested);

  const isLoading = strategiesBalance.isLoading && vaultBalanceLoading;

  return (
    <Table variant="simple">
      <TableCaption>
        <TableCap />
      </TableCaption>
      <Thead>
        <Tr>
          <Thh></Thh>
          <TableHead>{t("portfolioAllocation")}</TableHead>
          <TableHead>{t("strategyAllocation")}</TableHead>
          <TableHead>{t("totalAssets")}</TableHead>
          <TableHead>{t("totalUsd")}</TableHead>
          <TableHead>{t("gainsUsd")}</TableHead>
          <Thh></Thh>
        </Tr>
      </Thead>
      <Tbody>
        {allTokens &&
          allTokens.map((token) => {
            const tk = isAddress(token[chainId]);
            return (
              <TableRow
                key={token[chainId]}
                token={token}
                agreementBalance={tokensBalance && tokensBalance[tk]}
                vaultBalance={
                  vaultBalance && getBalanceByToken(vaultBalance, tk)
                }
                strategyBalance={
                  strategiesBalance && strategiesBalance.holdings[tk]
                }
                strategyInvested={
                  strategiesBalance && strategiesBalance.invested[tk]
                }
                strategySumApy={
                  strategiesBalance && strategiesBalance.sumApy[tk]
                }
                isLoading={isLoading}
                chainId={chainId}
              />
            );
          })}
      </Tbody>
    </Table>
  );
}

function TableRow({
  token,
  agreementBalance,
  vaultBalance,
  strategyBalance,
  strategyInvested,
  strategySumApy,
  isLoading,
  chainId,
}) {
  const { t } = useTranslation();

  const { buttonBorderStyle, bgHover, colorTitle } = useStyles();
  const { name, decimals } = token;

  const agreementTotal = formatBNPrice(agreementBalance, decimals);
  const vaultBalanceAccount = vaultBalance
    ? formatBNPrice(vaultBalance, decimals)
    : "0"; // balance from the vault for this pair token/account
  const portfolioBalance = sumBN(agreementTotal, vaultBalanceAccount);
  const totalAssetsPortfolio = portfolioBalance; //+vaultBalance
  const totalAssetsStrategy = formatBNPrice(strategyBalance, decimals);
  const gainsStrategies = subBN(strategyBalance, strategyInvested);
  const totalGainsStrategy = formatBNPrice(gainsStrategies, decimals);

  const totalSum = sumBN(totalAssetsPortfolio, totalAssetsStrategy);
  const totalUSD = useUSDCPrice(token[1], totalSum); // hardcode mainnet to get assetPrice
  const totalGainsUSD = useUSDCPrice(token[1], totalGainsStrategy); // hardcode mainnet to get assetPrice
  // (APYGS1 * USDI1 + APYGS2 * USDI2+ APYGS3 * USDI3) / (USDI1 + USDI2 + USDI3)
  // const apyPerTotal = formatBNPrice(strategySumApy) // this value is intended to calc the global APY
  const { updatePortfolioBalance } = useManagerDispatch();
  const withdrawFromVaultModalToggle = useWithdrawFromVaultModalToggle(
    token[chainId]
  );

  const transferToAgreementModalToggle = useTransferToAgreementModalToggle(
    token[chainId]
  );

  useEffect(() => {
    updatePortfolioBalance({
      address: token[chainId],
      total: totalUSD,
      gains: totalGainsUSD,
      sumApy: strategySumApy,
    });
  }, [
    totalUSD,
    totalGainsUSD,
    token[chainId],
    updatePortfolioBalance,
    strategySumApy,
  ]);

  return (
    <Tr
      key={token[chainId]}
      _hover={{
        bg: bgHover,
        cursor: "pointer",
      }}
    >
      <Td borderBottom={buttonBorderStyle}>
        <AssetCell name={name} mainnet={token[ChainId.Mainnet]} />
      </Td>

      {/* PORTFOLIO ALLOCATION */}
      <Td isNumeric borderBottom={buttonBorderStyle}>
        <ShowValue value={totalAssetsPortfolio} /> {/* TODO: REMOVE DEBUG */}
        {/* DEBUG BALANCE */}
        {/* {!isProduction() && (
          <Text color="orange">
            ({agreementTotal} + {vaultBalanceAccount})
          </Text>
        )} */}
      </Td>
      {/*STRATEGY ALLOCATION =  total assets joined at strategies  */}
      <Td isNumeric borderBottom={buttonBorderStyle}>
        <ShowValue value={totalAssetsStrategy} />
      </Td>

      {/*TOTAL =  all assets portfolio + strategies */}
      <Td isNumeric borderBottom={buttonBorderStyle}>
        <ShowValue value={totalSum} />
      </Td>

      {/* TOTAL USD */}
      <Td isNumeric borderBottom={buttonBorderStyle}>
        <ShowValue value={totalUSD} price={true} />
      </Td>
      <Td isNumeric borderBottom={buttonBorderStyle}>
        <GainsValue
          totalGainsStrategy={totalGainsStrategy}
          totalGainsUSD={totalGainsUSD}
        />
      </Td>
      <Td borderBottom={buttonBorderStyle}>
        {isLoading ? (
          <Spinner size="sm" />
        ) : (
          <>
            <Flex color={colorTitle} justify="space-evenly">
              <Button
                variant="ghost"
                px="2"
                onClick={() => transferToAgreementModalToggle()}
              >
                <Text fontWeight="600" pl={2} fontSize="sm" mr="1">
                  {t("deposit")}
                </Text>
                <AiOutlineDownload />
              </Button>

              <Button
                variant="ghost"
                px="2"
                onClick={() => withdrawFromVaultModalToggle()}
              >
                <Text fontWeight="600" pl={2} fontSize="sm" mr="1">
                  {t("withdraw")}
                </Text>
                <AiOutlineExport />
              </Button>
            </Flex>
          </>
        )}
      </Td>
    </Tr>
  );
}

const TableHead = ({ children }) => {
  if (!children) return null;

  return (
    <Thh>
      {children
        .toString()
        .split(" ")
        .map((w, i) => {
          return <Text key={i}>{w}</Text>;
        })}
    </Thh>
  );
};

const AssetCell = ({ mainnet, name }) => {
  const { onCopy } = useClipboard(mainnet);
  const toast = useToast();

  const copyAddress = () => {
    onCopy();
    toast({
      title: `Token address copied`,
      ...SETTINGS.toast,
    });
  };
  return (
    <Flex align="center" h="12" onClick={copyAddress}>
      <Image
        src={getAssetImg(mainnet)}
        alt={name}
        boxSize="30px"
        fallbackSrc={require("../assets/images/ph_tk.png").default}
      />
      <Text pl={2} fontSize="lg">
        {name}
      </Text>
    </Flex>
  );
};

const ShowValue = ({ value, price }) => {
  if (value === "0" || value === 0 || value === "0.00")
    return <TextValue>-</TextValue>;
  if (!value)
    return (
      <TextValue>
        <Spinner size="sm" />
      </TextValue>
    );
  return <TextValue>{price ? `$ ${formatPrice(value)}` : value}</TextValue>;
};

const TextValue = ({ children }) => {
  return (
    <Text fontWeight="500" pl={2} fontSize="lg" minW="120px">
      {children}
    </Text>
  );
};

const GainsValue = ({ totalGainsStrategy, totalGainsUSD }) => {
  if (!totalGainsStrategy || totalGainsStrategy === "0.00") return "-";
  const positive = greaterThanMax(totalGainsUSD, "0");
  return (
    <Stat>
      <StatHelpText m="0">
        <Flex justify="flex-end" align="center">
          <StatArrow type={positive ? "increase" : "decrease"} />$
          {totalGainsUSD}
        </Flex>
      </StatHelpText>
    </Stat>
  );
};

const Thh = ({ children }) => {
  const { buttonBorderStyle, colorSubtitle } = useStyles();

  return (
    <Th isNumeric borderBottom={buttonBorderStyle} color={colorSubtitle}>
      {children}
    </Th>
  );
};

const TableCap = () => {
  const toast = useToast();
  const { t } = useTranslation();

  const openFeedback = () => {
    window.open(URL_FEEDBACK, "_blank");
    toast({
      title: `Opening feedback form`,
      ...SETTINGS.toast,
    });
  };

  return (
    <Link onClick={openFeedback} to="#">
      {t("dontSeeAssets")}
    </Link>
  );
};
