import { useMemo } from "react";
import { useEthers } from "@usedapp/core";
import { isTheSameAddress, mulBN, parseBN } from "../helpers";
import { TRANSACTION_TYPE } from "../constants";
import useTransactionComposition from "./useTransactionComposition";
import { useApplicationState } from "../context/appContext";
import useToken from "./useToken";

export function useSwapCallback(
  portfolioAddress,
  tokenIn = "",
  tokenOut = "",
  amountIn = "0"
) {
  const { chainId } = useEthers();
  const same = isTheSameAddress(tokenIn, tokenOut);
  const { slippage } = useApplicationState();
  const tokenInfo = useToken(tokenIn); // token info to get decimals
  const txAmount = parseBN(amountIn, tokenInfo?.decimals);

  const tx = {
    account: portfolioAddress,
    tokenIn: tokenIn?.toString(),
    tokenOut: tokenOut?.toString(),
    amount: txAmount,
    slippage: mulBN(slippage, "1e18"),
  };

  const { state, send, query, queryState } =
    useTransactionComposition(
      tokenIn,
      tokenInfo?.decimals, 
      amountIn,
      same,
      tx,
      TRANSACTION_TYPE.SWAP
    );

  console.log(
    "useSwapCallback 11",
    tokenIn,
    tokenInfo?.decimals, 
  );

  return useMemo(() => {
    // if (!strategy || !library || !account || !chainId || !amount) {
    //   return { state: null, callback: null, error: "Missing dependencies" };
    // }

    return {
      state: state,
      queryCallback: async function onQuery() {
        return query();
      },
      queryState,
      callback: async function onExit() {
        return send();
      },
    };
  }, [tokenIn, tokenOut, amountIn, chainId, state, tx, send]);
}
