import { Collapse, Text, Flex } from "@chakra-ui/react";
import { SETTINGS } from "../helpers/settings";

export const ShowFormError = ({ errorMessage }) => {
  return (
    <Collapse in={errorMessage} animateOpacity w="100%">
      <Flex w="full" bg={SETTINGS.ERROR} py="1" justify="center" px="4">
        <Text fontSize="1.1rem" color={SETTINGS.VIOLET_LIGHTER}>
          {errorMessage}
        </Text>
      </Flex>
    </Collapse>
  );
};
