import { Flex, Text } from "@chakra-ui/react";
import { useStyles } from "../../hooks/useStyles";
import { Hr } from "../strategy/Hr";

export const SimpleTitle = ({ children, px, color }) => {
  const { colorModalTitle } = useStyles();

  return (
    <Text
      fontSize="1.15rem"
      fontWeight="300"
      px={px}
      pt={6}
      pb="4"
      color={color ? color : colorModalTitle}
    >
      {children}
    </Text>
  );
};

export const BoxContainer = ({ title, children, px }) => {
  return (
    <>
      {title && <SimpleTitle px={px}>{title}</SimpleTitle>}
      <Flex rounded="md" w="full" pb="2" align="center">
        {children}
      </Flex>
    </>
  );
};

export const LineBox = ({ title, children, px, last }) => {
  const { colorModalTitle } = useStyles();

  return (
    <>
      <Flex rounded="md" w="full" align="center" px="4" pt="4">
        <Text
          fontSize="1.15rem"
          fontWeight="300"
          px={px}
          pr="2"
          color={colorModalTitle}
        >
          {title}
        </Text>
        {children}
      </Flex>
      {!last && <Hr />}
    </>
  );
};
