import {
  Flex,
  Text,
  Menu,
  MenuItem,
  Button,
  useDisclosure,
  MenuButton,
  MenuList,
  useToast,
  useClipboard,
  IconButton,
} from "@chakra-ui/react";
import { FaChevronDown } from "react-icons/fa";
import EditPortfolioModal from "./EditPortfolio";
import { useManagerDispatch, useManagerState } from "../context/managerContext";
import { useStyles } from "../hooks/useStyles";
import { RiExternalLinkLine, RiFileCopyLine } from "react-icons/ri";
import { SETTINGS } from "../helpers/settings";
import { PortfolioLine } from "./common/PortfolioLine";
import { useTranslation } from "react-i18next";
import { isTheSameAddress } from "../helpers";
import { getExplorerAddressLink, useEthers } from "@usedapp/core";
import { Hr } from "./strategy/Hr";
import { RoundedIconButton } from "./common/RoundedIconButton";

export default function PortfoliosDropDown() {
  const { t } = useTranslation();
  const { buttonBorderStyle, bgModal, colorBody } = useStyles();
  const portfolioList = useDisclosure();
  const toast = useToast();
  const { chainId } = useEthers();
  const { portfolioSelected, portfolios } = useManagerState();
  const { selectPortfolio } = useManagerDispatch();
  const { onCopy } = useClipboard(portfolioSelected?.address);

  const changePortfolioSelected = (address) => {
    console.log("Selecting new address", address);
    selectPortfolio(address);
  };

  const onPortfolioChange = (address) => {
    portfolioList.onToggle();
    changePortfolioSelected(address);
  };

  const copyAddress = () => {
    onCopy();
    toast({
      title: `Active portfolio copied`,
      ...SETTINGS.toast,
    });
  };

  const openAddress = () => {
    window.open(
      getExplorerAddressLink(portfolioSelected?.address, chainId),
      "_blank"
    );
    toast({
      title: `Opening address on Etherscan`,
      ...SETTINGS.toast,
    });
  };

  const withPortfolios = portfolios && portfolios.length;

  return (
    <Flex align="center" pl="2">
      {withPortfolios ? (
        <Text
          fontSize="1.25rem"
          mr="4"
          display={{ md: "inline-flex", base: "none" }}
        >
          {t("portfolio")}
        </Text>
      ) : null}
      <Menu>
        {({ isOpen }) => (
          <>
            <MenuButton
              isActive={isOpen}
              as={Button}
              variant="ghost"
              border={buttonBorderStyle}
              rounded="sm"
              px="2"
              rightIcon={<FaChevronDown />}
            >
              <Flex align="center"  >
                {withPortfolios ? (
                  <PortfolioLine
                    color={colorBody}
                    address={portfolioSelected?.address}
                    size="sm"
                  />
                ) : (
                  <EmptyPortfolioLine />
                )}
              </Flex>
            </MenuButton>
            <MenuList rounded="md" bg={bgModal}>
              {withPortfolios ? (
                portfolios.map((portfolio) => {
                  return (
                    <PortfolioItem
                      key={portfolio.address}
                      {...portfolio}
                      isOpen={isOpen}
                      portfolioSelected={portfolioSelected.address}
                      onPortfolioChange={onPortfolioChange}
                    />
                  );
                })
              ) : (
                <NoPortfolioLine />
              )}
              <Hr mt="2" />
              <EditPortfolio onClick={() => portfolioList.onToggle()} />
            </MenuList>
          </>
        )}
      </Menu>
      {withPortfolios ? (
        <>
          <RoundedIconButton
            ariaLabel="Open portfolio on etherscan"
            target="_blank"
            rel="noopener noreferrer"
            onClick={openAddress}
            icon={<RiExternalLinkLine />}
          />
          <RoundedIconButton
            ariaLabel="Copy"
            onClick={copyAddress}
            icon={<RiFileCopyLine />}
          />
        </>
      ) : null}
    </Flex>
  );
}

const NoPortfolioLine = () => {
  const { t } = useTranslation();
  const { colorHighlighted } = useStyles();

  return (
    <Flex color={colorHighlighted}  w="full" h="4.5rem" justify="center" align="center">
      <Text>{t("noPortfolios")}</Text>
    </Flex>
  );
};

const PortfolioItem = ({
  address,
  onPortfolioChange,
  portfolioSelected,
  isOpen,
}) => {
  const toast = useToast();
  const { onCopy } = useClipboard(address);
  const match = isTheSameAddress(portfolioSelected, address);

  const copyAddress = () => {
    onCopy();
    toast({
      title: `Address copied`,
      ...SETTINGS.toast,
    });
  };
  return (
    <MenuItem
      maxW="300px"
      key={address}
      onClick={match ? () => copyAddress() : () => onPortfolioChange(address)}
    >
      <PortfolioLine
        address={address}
        isOpen={isOpen}
        portfolioSelected={portfolioSelected}
        size="sm"
      />
    </MenuItem>
  );
};

const EmptyPortfolioLine = () => {
  const { t } = useTranslation();
  const { colorBody } = useStyles();

  return (
    <Flex w="full" justify="center" align="center">
      <Text fontWeight="400" color={colorBody}>{t("emptyPortfolioLine")}</Text>
    </Flex>
  );
};

const EditPortfolio = ({ onClick }) => {
  const { t } = useTranslation();
  const { colorModalTitle } = useStyles();

  return (
    <MenuItem onClick={onClick} p="0">
      <EditPortfolioModal w="full">
        <Text
          px="4"
          pt="2"
          color={colorModalTitle}
          fontSize="1.15rem"
          fontWeight="600"
        >
          {t("editPortfolio")}
        </Text>
      </EditPortfolioModal>
    </MenuItem>
  );
};
