import { useEffect, useState } from "react";
import {
  COMPOSITION_TYPE,
  TRANSACTION_TYPE,
  TRANSACTION_BATCH,
  TRANSACTION_NAME,
} from "../constants";
import { composeTransactionHelper } from "../helpers/vault";
const useCompositionOptions = (
  contract,
  tx,
  amount = "0",
  same = "true",
  comp_type = "default",
  strategy_type = ""
) => {
  const [simpleComposition, setSimpleComposition] = useState(true);
  const [readsOutput, setReadsOutput] = useState([]);
  const [arrayCall, setArrayCall] = useState([]);
  const [composition, setComposition] = useState([]);
  const [transactionName, setTransactionName] = useState("");
  const [functionName, setFunctionName] = useState("");
  const [queryPos, setQueryPos] = useState(0); // Query result position
  const [error, setError] = useState(null);

  // console.log(
  //   "useCompositionOptions0",
  //   simpleComposition,
  //   readsOutput,
  //   arrayCall,
  //   composition,
  //   transactionName,
  //   functionName
  // );

  // JOIN, EXIT, WITHDRAW needs a diff composition to send() the Tx
  async function getOptions() {
    // console.log("useCompositionOptions GOGO", tx?.amount);
    setSimpleComposition(same);
    setComposition([]);
    setReadsOutput([]);
    setArrayCall([]);
    setError(null);
    try {
      // 1) vault has enough balance enoughVaultBalance
      if (
        strategy_type === TRANSACTION_TYPE.JOIN &&
        comp_type === COMPOSITION_TYPE.ENOUGH_VAULT_BALANCE
      ) {
        const co = same
          ? [TRANSACTION_BATCH.JOIN]
          : [TRANSACTION_BATCH.SWAP, TRANSACTION_BATCH.JOIN];
        const ro = same ? [] : [false, true];
        const ac = await composeTransactionHelper(co, contract, tx);
        const tn = same ? TRANSACTION_NAME.JOIN : TRANSACTION_NAME.SWAP_JOIN;

        setSimpleComposition(same);
        setComposition([tx.account, tx.strategy, tx.amount, "0x99"]); //Simple op
        setReadsOutput(ro);
        setArrayCall(ac);
        setTransactionName(tn);
        setFunctionName("join");
        setQueryPos(co?.length);

        // 2) If vault has balance but not enough and agreement has balance vaultAndAgreementBalance
      } else if (
        strategy_type === TRANSACTION_TYPE.JOIN &&
        comp_type === COMPOSITION_TYPE.VAULT_AND_AGREEMENT_BALANCE
      ) {
        const co = same
          ? [TRANSACTION_BATCH.PARTIAL_DEPOSIT, TRANSACTION_BATCH.JOIN]
          : [
              TRANSACTION_BATCH.PARTIAL_DEPOSIT,
              TRANSACTION_BATCH.SWAP,
              TRANSACTION_BATCH.JOIN,
            ];
        const ro = same ? [false, true] : [false, false, true];
        const ac = await composeTransactionHelper(co, contract, tx);
        const tn = same
          ? TRANSACTION_NAME.DEPOSIT_JOIN
          : TRANSACTION_NAME.DEPOSIT_SWAP_JOIN;

        setSimpleComposition(false); // both cases is a batch operation
        setComposition(co);
        setReadsOutput(ro);
        setArrayCall(ac);
        setTransactionName(tn);
        setQueryPos(co?.length);

        // 3) If vault its empty and Agreement has enough balance enougAgreementBalance
      } else if (
        strategy_type === TRANSACTION_TYPE.JOIN &&
        comp_type === COMPOSITION_TYPE.ENOUGH_AGREEMENT_BALANCE
      ) {
        const co = same
          ? [TRANSACTION_BATCH.DEPOSIT, TRANSACTION_BATCH.JOIN]
          : [
              TRANSACTION_BATCH.DEPOSIT,
              TRANSACTION_BATCH.SWAP,
              TRANSACTION_BATCH.JOIN,
            ];
        const ro = same ? [false, true] : [false, true, true];
        const ac = await composeTransactionHelper(co, contract, tx);
        const tn = same
          ? TRANSACTION_NAME.DEPOSIT_JOIN
          : TRANSACTION_NAME.DEPOSIT_SWAP_JOIN;

        setSimpleComposition(false); // both cases is a batch operation
        setComposition(co);
        setReadsOutput(ro);
        setArrayCall(ac);
        setTransactionName(tn);
        setQueryPos(co?.length);
      } else if (strategy_type === TRANSACTION_TYPE.EXIT) {
        const co = same
          ? [TRANSACTION_BATCH.EXIT]
          : [TRANSACTION_BATCH.EXIT, TRANSACTION_BATCH.SWAP];
        const ro = same ? [] : [false, true];
        const ac = await composeTransactionHelper(co, contract, tx);
        const tn = same ? TRANSACTION_NAME.EXIT : TRANSACTION_NAME.EXIT_SWAP;

        setSimpleComposition(same);
        setComposition([tx.account, tx.strategy, tx.ratio, false, "0x"]);
        setReadsOutput(ro);
        setArrayCall(ac);
        setTransactionName(tn);
        setFunctionName("exit");
        setQueryPos(co?.length);
      } else if (strategy_type === TRANSACTION_TYPE.SWAP) {
        const co = [TRANSACTION_BATCH.SWAP];
        const ro = [false];
        const ac = await composeTransactionHelper(co, contract, tx);
        const tn = TRANSACTION_NAME.SWAP;

        setSimpleComposition(true);
        setComposition([
          tx.account,
          tx.tokenIn,
          tx.tokenOut,
          tx.amount,
          tx.slippage,
          "0x",
        ]);
        setReadsOutput(ro);
        setArrayCall(ac);
        setTransactionName(tn);
        setFunctionName("swap");
        setQueryPos(co?.length);
      } else if (strategy_type === TRANSACTION_TYPE.DESPOSIT) {
        const co = [TRANSACTION_BATCH.DEPOSIT];
        const ro = [false];
        const ac = await composeTransactionHelper(co, contract, tx);
        const tn = TRANSACTION_NAME.DEPOSIT_JOIN;

        setSimpleComposition(true);
        setComposition([tx.account, tx.tokenIn, tx.amount]);
        setReadsOutput(ro);
        setArrayCall(ac);
        setTransactionName(tn);
        setFunctionName("deposit");
        setQueryPos(co?.length);
      } else if (strategy_type === TRANSACTION_TYPE.WITHDRAW) {
        const co = same ? [TRANSACTION_BATCH.WITHDRAW] : [];
        const ro = same ? [] : [];
        // const ac = await composeTransactionHelper(co, contract, tx);
        const tn = same ? TRANSACTION_NAME.WITHDRAW : null;

        setSimpleComposition(same);
        setComposition([tx.account, tx.tokenOut, tx.amount, tx.recipient]);
        setReadsOutput(ro);
        // setArrayCall(ac);
        setTransactionName(tn);
        setFunctionName("withdraw");
        setQueryPos(co?.length);

        // 4) If Agreement has not balance noBalanceAtAll
      } else {
        console.log(
          `useCompositionOptions  strategy_type: ${strategy_type}, comp_type: ${comp_type}`
        );
      }
    } catch (e) {
      setError(e);
    }
  }

  useEffect(() => {
    getOptions();
  }, [amount, same, comp_type, strategy_type]);

  console.log(
    "useCompositionOptions ->",
    tx,
    amount,
    same,
    comp_type,
    strategy_type
  );

  return {
    simpleComposition,
    readsOutput,
    arrayCall,
    composition,
    transactionName,
    functionName,
    error,
    queryPos,
  };
};

export default useCompositionOptions;
