import React from "react";
import {
  Flex,
  Text,
  useClipboard,
  useToast,
  Button, Link
} from "@chakra-ui/react";
import { shortenAddress } from "../../helpers";
import { RiFileCopyLine, RiExternalLinkLine } from "react-icons/ri";
import { SETTINGS } from "../../helpers/settings";
import Identicon from "../Identicon";
import {
  getExplorerAddressLink,
  useEthers,
  useLookupAddress,
  useTransactions,
} from "@usedapp/core";
import { TransactionHashLink } from "../common/TransactionHashLink";
import { useStyles } from "../../hooks/useStyles";
import { BoxTitle } from "../typography/BoxTitle";
import { Hr } from "../strategy/Hr";
import { RoundedIconButton } from "../common/RoundedIconButton";


export default function AccountDetails({ connector }) {
  const { deactivate, account, chainId } = useEthers()
  const { inputBorderStyle, colorModalTitle } = useStyles();
  const toast = useToast();
  const { onCopy } = useClipboard(account);
  const ens = useLookupAddress();
  const copyAddress = () => {
    onCopy();
    toast({
      title: `Manager address copied`,
      ...SETTINGS.toast,
    });
  };

  const openAddress = () => {
    window.open(getExplorerAddressLink(account, chainId), "_blank");
    toast({
      title: `Opening address on Etherscan`,
      ...SETTINGS.toast,
    });
  };

  function StatusIcon() {
    if (connector) {
      return <Identicon />;
    }
    return null;
  }
  function FormatConnectorName() {
    return <BoxTitle>Connected with Metamask</BoxTitle>;
  }

  return (
    <Flex direction="column" align="center" w="full">
      <FormatConnectorName />
      <Flex justify="center" align="center" pb={3} w="full">
        <Button
          py="0.7rem"
          px="0.7rem"
          fontSize="lg"
          variant="ghost"
          color="current"
          rounded="0"
          mx={1}
          border={inputBorderStyle}
        >
          <Flex align="center">
            <Flex>
              <Text mr={2} fontSize="1rem" fontWeight="400">
                {ens ? ens : shortenAddress(account)}
              </Text>
              {connector && <StatusIcon connector={connector} />}
            </Flex>
          </Flex>
        </Button>

        <RoundedIconButton
          aria-label="Copy"
          onClick={copyAddress}
          icon={<RiFileCopyLine  color={colorModalTitle} />}
          border={inputBorderStyle}
        />
        <RoundedIconButton
          target="_blank"
          rel="noopener noreferrer"
          aria-label="Open"
          onClick={openAddress}
          icon={<RiExternalLinkLine color={colorModalTitle} />}
          border={inputBorderStyle}

        />
      </Flex>
      <Flex>
        <Link onClick={deactivate} fontSize="0.75rem" >Disconnect Wallet</Link>
      </Flex>
      <Hr mt="8" />
      <Flex
        w="full"
        direction="column"
        justify="flex-start"
        m="4"
        px="4"
        pb="4"
        rounded="sm"
        minH="6rem"
        maxH="20rem"
        overflowY="scroll"
      >
        <BoxTitle>Transactions:</BoxTitle>
        <Flex direction="column" w="full">
          <TransactionsList />
        </Flex>
      </Flex>
    </Flex>
  );
}

const TransactionsList = () => {
  const { transactions } = useTransactions();
  return (
    <>
      {transactions && transactions.length ? (
        transactions.map((transaction) => (
          <ListTx
            transaction={transaction.transaction}
            title={transaction.transactionName}
            // icon={transaction.transactionName === 'Unwrap' ? <UnwrapIcon /> : <WrapIcon />}
            key={transaction.transaction.hash}
          />
        ))
      ) : (
        <Text opacity="0.2">No transactions here 👀</Text>
      )}
    </>
  );
};

const ListTx = ({ transaction, title }) => {

  return (
    <Flex align="center" w="full" justify="space-between" py="2">
      {title}
      <TransactionHashLink
        hash={transaction.hash}
        chainId={transaction.chainId}
      />
    </Flex>
  );
};
