import {
  Flex,
  IconButton,
  useClipboard,
  useToast,
  Divider,
} from "@chakra-ui/react";
import { isTheSameAddress } from "../helpers";
import {
  RiFileCopyLine,
  RiExternalLinkLine,
  RiDeleteBin7Line,
} from "react-icons/ri";
import { SETTINGS } from "../helpers/settings";
import { getExplorerAddressLink, useEthers } from "@usedapp/core";
import { useStyles } from "../hooks/useStyles";
import { PortfolioLine } from "./common/PortfolioLine";
import { RoundedIconButton } from "./common/RoundedIconButton";

export default function PortfoliosSelectorItem({
  address,
  portfolioSelected,
  removePortfolioFromList,
  index,
  totalPortfolios,
  disableDelete = false
}) {
  const toast = useToast();
  const { chainId } = useEthers();
  const { onCopy } = useClipboard(address);

  const { inputBorderStyle, colorModalTitle } = useStyles();

  const copyAddress = () => {
    onCopy();
    toast({
      title: `Portfolio address copied`,
      ...SETTINGS.toast,
    });
  };

  const openAddress = () => {
    const url = getExplorerAddressLink(address, chainId);
    window.open(url, "_blank");
    toast({
      title: `Opening address on Etherscan`,
      ...SETTINGS.toast,
    });
  };

  const match = isTheSameAddress(portfolioSelected, address);
  const last = totalPortfolios === index

  return (
    <>
      <Flex
        align="center"
        px="4"
        pb="1"
        rounded="sm"
        my="1"
        justify="space-between"
      >
        <Flex align="center">
          <PortfolioLine
            address={address}
            isOpen={true}
            portfolioSelected={portfolioSelected}
          />
        </Flex>

        <Flex>
          {!match && !disableDelete && (
            <RoundedIconButton
              onClick={() => removePortfolioFromList(address)}
              ariaLabel="Open"
              border={inputBorderStyle}
              icon={<RiDeleteBin7Line color={colorModalTitle} />}
            />
          )}
           <RoundedIconButton
            ariaLabel="Open"
            onClick={() => openAddress()}
            border={inputBorderStyle}
            icon={<RiExternalLinkLine color={colorModalTitle} />}
          />
          <RoundedIconButton
            ariaLabel="Copy"
            onClick={copyAddress}
            border={inputBorderStyle}
            icon={<RiFileCopyLine color={colorModalTitle} />}
          />
         
        </Flex>
      </Flex>
    </>
  );
}
