import { Box, Flex, Heading, Text, Button } from "@chakra-ui/react";
import { useState } from "react";
import { FiArrowDown } from "react-icons/fi";
import Container from "../components/Container";
import TokenInput from "../components/TokenInput";
import { QUERY_STATUS } from "../constants";
import { useManagerState } from "../context/managerContext";
import { SETTINGS } from "../helpers/settings";
import useAvailableBalance from "../hooks/useAvailableBalance";
import { useDepositCallback } from "../hooks/useDepositCallback";
import { useStyles } from "../hooks/useStyles";
import { useSwapCallback } from "../hooks/useSwapCallback";
import useToken from "../hooks/useToken";

function Swap() {
  const { inputBorderStyle } = useStyles();
  const { portfolioSelected } = useManagerState();

  const [tokenIn, setTokenIn] = useState({
    token: "0x4F96Fe3b7A6Cf9725f59d353F723c1bDb64CA6Aa",
    amount: "0",
  });
  const [tokenOut, setTokenOut] = useState({
    token: "0xb7a4F3E9097C08dA09517b5aB877F7a917224ede",
    amount: "0",
  });
  const [tokenDeposit, setTokenDeposit] = useState({
    token: "0x4F96Fe3b7A6Cf9725f59d353F723c1bDb64CA6Aa",
    amount: "0",
  });

  const tokenInfo = useToken(tokenIn?.token); // token info to get decimals
  
  const { vaultBalance } = useAvailableBalance(
    tokenIn?.token,
    tokenInfo.decimals
  );

  const {
    state: depositStateCallback,
    callback: depositCallback,
    queryCallback: depositQueryCallback,
    queryState: depositQueryState,
  } = useDepositCallback(
    portfolioSelected.address,
    tokenDeposit?.token,
    tokenDeposit?.amount
  );

  const {
    state: stateCallback,
    callback: swapCallback,
    queryCallback,
    queryState,
  } = useSwapCallback(
    portfolioSelected.address,
    tokenIn?.token,
    tokenOut?.token,
    tokenIn?.amount
  );

  const handleInputTokenInChange = (token, amount) => {
    if (token !== null) {
      setTokenIn({ token: token, amount: amount });
      queryCallback();
    }
  };

  const handleInputTokenOutChange = (token, amount) => {
    if (token !== null) {
      setTokenOut({ token: token, amount: amount });
      queryCallback();
    }
  };

  const handleInputTokenDepositChange = (token, amount) => {
    if (token !== null) {
      setTokenDeposit({ token: token, amount: amount });
      depositQueryCallback();
    }
  };

  const handleDeposit = () => {
    depositCallback()
  };

  const handleSwap = () => {
    swapCallback();
  };

  return (
    <Box w="full" h="90vh">
      <Container>
        <Flex direction="column">
          <Heading size="md">DESPOSIT TO VAULT</Heading>
          <Flex
            direction="column"
            align="center"
            border={inputBorderStyle}
            rounded="sm"
            mb="8"
          >
            <Flex direction="column" p="8">
              <TokenInput
                startToken={tokenDeposit?.token}
                onChange={handleInputTokenDepositChange}
                isChangeable={true}
                hintText="deposit"
              />

              <Flex
                color="orange"
                direction="column"
                fontSize="0.75rem"
                mt="6"
                w="full"
              >
                <Text>========================</Text>
                <QueryResult queryState={depositQueryState} />
                <StateResult stateCallback={depositStateCallback} />
              </Flex>
            </Flex>

            <Button
              w="full"
              h="4rem"
              bg={SETTINGS.VIOLET_LIGHT}
              onClick={handleDeposit}
              rounded="0"
              fontSize="1.25rem"
              fontWeight="600"
            >
              Deposit
            </Button>
          </Flex>
          <Heading size="md">SWAP VAULT BALANCE</Heading>
          <Flex
            direction="column"
            align="center"
            border={inputBorderStyle}
            rounded="sm"
          >
            <Flex direction="column" p="8">
              <Text fontSize="1.25rem" pb="4">
                Max amount to swap from vault: {vaultBalance} {tokenInfo.name}
              </Text>

              <TokenInput
                startToken={tokenIn?.token}
                onChange={handleInputTokenInChange}
                isChangeable={true}
                hintText="In"
              />
              <FiArrowDown fontSize="1.25rem" />
              <TokenInput
                onChange={handleInputTokenOutChange}
                startToken={tokenOut?.token}
                isChangeable={true}
                hintText="Out"
              />
              <Flex
                color="orange"
                direction="column"
                fontSize="0.75rem"
                mt="6"
                w="full"
              >
                <Text>
                  TokenIn: {tokenIn?.token} - AmountIn: {tokenIn?.amount}
                </Text>
                <Text>
                  TokenOut: {tokenOut?.token} - AmountOut: {tokenOut?.amount}
                </Text>
                <Text>========================</Text>
                <QueryResult queryState={queryState} />
                <StateResult stateCallback={stateCallback} />
              </Flex>
            </Flex>
            <Button
              w="full"
              h="4rem"
              bg={SETTINGS.VIOLET_LIGHT}
              onClick={handleSwap}
              rounded="0"
              fontSize="1.25rem"
              fontWeight="600"
            >
              Swap
            </Button>
          </Flex>
        </Flex>
      </Container>
    </Box>
  );
}

export default Swap;

const QueryResult = ({ queryState }) => {
  return (
    <>
      <Text noOfLines="1">Query Status: {queryState?.status}</Text>
      {!queryState?.status === QUERY_STATUS.DONE && (
        <Text noOfLines="1">
          ✅ Query result: {queryState?.result[queryState?.queryPos - 1]}
        </Text>
      )}
      {queryState?.status === QUERY_STATUS.ERROR && (
        <Text noOfLines="1">
          ❌ Query error: {queryState?.error?.code} -{" "}
          {queryState?.error?.message}{" "}
        </Text>
      )}
    </>
  );
};

const StateResult = ({ stateCallback }) => {
  return (
    <>
      <Text noOfLines="1">Hash: {stateCallback?.transaction?.hash}</Text>
      <Text noOfLines="1">{stateCallback?.status}</Text>
    </>
  );
};
