import { Flex, Text } from "@chakra-ui/react";
import { useModalParams } from "../hooks";
import { useManagerState } from "../context/managerContext";
import PortfoliosSelectorItem from "../components/PortfolioSelectorItem";
import QRCode from "react-qr-code";
import useToken from "../hooks/useToken";
import { useStyles } from "../hooks/useStyles";
import { Hr } from "../components/strategy/Hr";
import { useTranslation } from "react-i18next";

export default function TransferToAgreement() {
  const { t } = useTranslation();
  const { bgModal, colorModalTitle } = useStyles();

  // here we receive the asset selected
  const modalParams = useModalParams();
  const token = useToken(modalParams);
  const managerState = useManagerState();
  const { portfolioSelected } = managerState;

  return (
    <Flex w="full" direction="column" justify="center">
      <Text color={colorModalTitle} px="4" fontWeight="500">
        {`${t("transfer")} ${token && token.name} ${t("followingAddress")}: `}
      </Text>
      <PortfoliosSelectorItem
        name={"name"}
        address={portfolioSelected.address}
        disableDelete={true}
      />
      <Hr />
      <Flex mb="4" mt="8" w="full" justify="center">
        <QRCode
          value={portfolioSelected.address}
          fgColor={colorModalTitle}
          bgColor={bgModal}
        />
      </Flex>
      <Text color={colorModalTitle} py="4" fontWeight="300" align="center">
        {t("scanQr")}
      </Text>
    </Flex>
  );
}
