export const ActionTypes = {
  SET_OPEN_MODAL: 'SET_OPEN_MODAL',
  SET_BLOCK_NUMBER: 'SET_BLOCK_NUMBER',
  SET_SLIPPAGE: 'SET_SLIPPAGE',
};

export const reducer = (state, action) => {
  console.log('state', state);
  console.log('action', action);
  switch (action.type) {
    case ActionTypes.SET_OPEN_MODAL:
      return {
        ...state,
        openModal: action.modal,
        modalParams: action.params,
      };
    case ActionTypes.SET_BLOCK_NUMBER:
      return {
        ...state,
        blockNumber: action.blockNumber,
      };
      case ActionTypes.SET_SLIPPAGE:
        return {
          ...state,
          slippage: action.slippage,
        };
    default:
      return state;
  }
};
