import { Button } from "@chakra-ui/react";
import { SETTINGS } from "../helpers/settings";
import { useStyles } from "../hooks/useStyles";

const StButton = ({ children, w, onClick, isDisabled }) => {
  const { bgButton } = useStyles();

  
  return (
    <Button
      w={w ? w : "full"}
      h={SETTINGS.BUTTON_H}
      bg={bgButton}
      onClick={onClick}
      isDisabled={isDisabled}
      rounded="md"
      fontSize="1.5rem"
      fontWeight="600"
      roundedTop="0"
      color={SETTINGS.VIOLET_LIGHTER}
      _hover={{ bg: SETTINGS.VIOLET_DARK}}
    >
      {children}
    </Button>
  );
};

export default StButton;
