import { useMemo } from "react";
import { useEthers } from "@usedapp/core";
import useToken from "./useToken";
import {
  parseBN,
} from "../helpers";
import { TRANSACTION_TYPE } from "../constants";
import useTransactionComposition from "./useTransactionComposition";

// returns a function that will execute a join in strategy,
export function useDepositCallback(
  portfolioAddress,
  tokenIn = "", //strategy address
  amount = "0"
) {
  const { account, chainId, library } = useEthers();

  const tokenInfo = useToken(tokenIn); // token info to get decimals
  const txAmount = parseBN(amount, tokenInfo.decimals);

  const tx = {
    account: portfolioAddress,
    tokenIn: tokenIn.toString(),
    amount: txAmount,
  };

  const { state, send, query, queryState } = useTransactionComposition(
    tokenIn,
    tokenInfo.decimals,
    amount,
    true,
    tx,
    TRANSACTION_TYPE.DESPOSIT
  );

  return useMemo(() => {
    // if (!strategy || !library || !account || !chainId || !amount) {
    //   return { state: null, callback: null, error: "Missing dependencies" };
    // }

    return {
      state: state,
      queryCallback: async function onQuery() {
        return query();
      },
      queryState,
      callback: async function onJoin() {
        return send();
      },
    };
  }, [library, account, chainId, amount, state, tx, send]);
}
