import { getSubgraphqlURI } from "../helpers/enviroment";
import { useManagerState } from "../context/managerContext";
import { useQuery } from "react-query";
import { request, gql } from "graphql-request";

const useVaultBalance = () => {
  const { portfolioSelected } = useManagerState();
  const address = portfolioSelected?.address?.toLowerCase();

  return useQuery(["vaultBalance", address], () => fetchStrategies(address));
};

const fetchStrategies = async (agreementAddress) => {
  const {account} = await request(
    getSubgraphqlURI(),
    gql`
        {
          account (id: "${agreementAddress}"){
            id
            balances {
              id
              token {
                id
              } 
              amount
            }
          }
        }
      `
  );
  // console.log("useVaultBalance", account);
  return account?.balances;
};

export default useVaultBalance;
