import {
  Button,
  Flex,
  Image,
  Text,
  Input,
  Spinner,
  useColorModeValue,
  Menu,
  MenuButton,
  MenuList,
  MenuItem,
  Divider,
  Box,
  Link,
} from "@chakra-ui/react";
import { useEffect, useLayoutEffect, useRef, useState } from "react";
import { SETTINGS } from "../helpers/settings";
import useUSDCPrice from "../hooks/useUSDCPrice";
import { formatPrice, getAssetImg, greaterThanMax } from "../helpers";
import { FaChevronDown } from "react-icons/fa";
import useToken from "../hooks/useToken";
import useWhitelistedTokens from "../hooks/useWhitelistedToken";
import { ChainId, useEthers } from "@usedapp/core";
import { DEFAULT_TOKEN } from "../constants";
import { useStyles } from "../hooks/useStyles";
import useAvailableBalance from "../hooks/useAvailableBalance";

export default function TokenInput({
  balance,
  onChange,
  isChangeable,
  hideBalance,
  hintText,
  defaultToken = null,
  defaultAmount,
  startToken,
  setOverBalance = undefined,
  px,
}) {
  const { inputBorderStyle, colorSubtitle, colorModalTitle } = useStyles();
  const { chainId } = useEthers();
  // get the width size for the menu element
  const [width, setWidth] = useState(0);
  const targetRef = useRef();

  const [inputValue, setInputValue] = useState("");
  const [selectedToken, setSelectedToken] = useState(DEFAULT_TOKEN[chainId]);

  const tokenToUse = defaultToken ? defaultToken : selectedToken;
  const token = useToken(tokenToUse);
  const price = useUSDCPrice(token[ChainId.Mainnet], inputValue);

  // reset form on every load
  useEffect(() => {
    setInputValue(defaultAmount);
    onChange(defaultToken, null, isChangeable);
  }, []);

  // setting
  useEffect(() => {
    setInputValue(defaultAmount);
    setSelectedToken(startToken);
  }, [defaultAmount, startToken]);

  // get the width size for the menu element
  useLayoutEffect(() => {
    if (targetRef.current) {
      setWidth(targetRef.current.offsetWidth);
    }
  }, []);

  const onUserInput = (event) => {
    if (defaultAmount) {
      // dont do anything, amountDefault is set
      setInputValue(defaultAmount);
    } else {
      setInputValue(event?.target?.value);
      onChange(selectedToken, event?.target?.value, isChangeable);
    }
  };

  const onChangeToken = (tk) => {
    setSelectedToken(tk);
    onChange(tk, inputValue, isChangeable);
  };

  const onMaxBtn = (max) => {
    setInputValue(max);
    onChange(selectedToken, max, isChangeable);
  };

  return (
    <Flex w="full" align="flex-start" direction="column" mb="4" px={px}>
      <Flex w="full" align="flex-end" ref={targetRef}>
        {isChangeable ? (
          <TokenSelection
            token={token}
            widthElement={width}
            onUserChangeToken={onChangeToken}
            chainId={chainId}
            generalBalance={balance}
          />
        ) : (
          <Box w="4rem">
            <DisplayToken name={token.name} mainnet={token[ChainId.Mainnet]} />
          </Box>
        )}
        <Flex w="full" align="flex-end" direction="column">
          <Input
            type="number"
            value={inputValue}
            placeholder="0.0"
            size="lg"
            textAlign="right"
            fontWeight="600"
            fontSize="1.5rem"
            minW="150px"
            w="full"
            ml="2"
            rounded="0"
            _placeholder={{ color: SETTINGS.VIOLET_LIGHT }}
            onChange={onUserInput}
            border={inputBorderStyle}
          />
        </Flex>
      </Flex>
      <Flex w="full" justify="space-between" align="flex-end">
        <Text
          color={colorSubtitle}
          textTransform="uppercase"
          fontSize="0.75rem"
          pb="2"
        >
          {hintText}
        </Text>

        <Text py="2" color={colorModalTitle} fontSize="1.1rem" fontWeight="500">
          ~ $ {formatPrice(price)}
        </Text>
      </Flex>
      <ShowBalance
        inputValue={inputValue}
        token={token}
        chainId={chainId}
        hideBalance={hideBalance}
        setOverBalance={setOverBalance}
        onMaxBtn={onMaxBtn}
      />
    </Flex>
  );
}

const TokenSelection = ({
  token,
  onUserChangeToken,
  chainId,
  generalBalance,
}) => {
  const { inputBorderStyle } = useStyles();
  const bgMenu = useColorModeValue("#dcdcdc", SETTINGS.BG_DARK);
  const allTokens = useWhitelistedTokens();

  return (
    <Flex w="full">
      <Menu closeOnSelect={true} w="full">
        <Flex w="full">
          <Box w="4rem">
            <DisplayToken name={token.name} mainnet={token[ChainId.Mainnet]} />
          </Box>
          <MenuButton
            as={Button}
            rightIcon={<FaChevronDown />}
            border={inputBorderStyle}
            minW="120px"
            h="3rem"
            rounded="0"
            bg="transparent"
          >
            <Text fontSize="1.5rem" fontWeight="600">
              {token.name}
            </Text>
          </MenuButton>
        </Flex>
        <MenuList mt="3" bg={bgMenu} border="none" w="300px" ml={"-2px"}>
          <Flex w="full" h="250px" direction="column" overflowX="scroll">
            {allTokens &&
              allTokens.map((tk) => {
                return (
                  <TokenItem
                    balance={generalBalance}
                    key={tk.name}
                    token={tk}
                    onUserChangeToken={onUserChangeToken}
                    chainId={chainId}
                  />
                );
              })}
          </Flex>
        </MenuList>
      </Menu>
    </Flex>
  );
};

const DisplayToken = ({ name, mainnet }) => {
  // mainnet is intended for img display

  if (!name)
    return (
      <Flex direction="column" w="3.5rem" align="flex-start" mr="2">
        <Spinner size="sm" />
      </Flex>
    );
  return (
    <Flex direction="column" w="3.5rem" align="flex-start" mr="2">
      <Image
        src={getAssetImg(mainnet)}
        boxSize="3rem"
        fallbackSrc={require("../assets/images/ph_tk.png").default}
      />
    </Flex>
  );
};

const TokenItem = ({ token, chainId, onUserChangeToken, balance }) => {
  const tokenAddress = token[chainId];
  const { totalBalance } = useAvailableBalance(token[chainId], token.decimals);

  return (
    <>
      <MenuItem
        onClick={() => onUserChangeToken(tokenAddress)}
        closeOnSelect="true"
      >
        <Flex w="full" py="2" px="2" align="center">
          <Flex direction="column" w="full">
            <DisplayToken name={token.name} mainnet={token[ChainId.Mainnet]} />
          </Flex>
          <Text mr="2">{formatPrice(totalBalance)}</Text>
          <Text fontSize="1.5rem" fontWeight="600" textAlign="right">
            {token.name}
          </Text>
        </Flex>
      </MenuItem>
      <Divider />
    </>
  );
};

const ShowBalance = ({
  token,
  hideBalance,
  onMaxBtn,
  inputValue,
  chainId,
  setOverBalance,
}) => {
  const { colorModalTitle } = useStyles();
  const { totalBalance } = useAvailableBalance(token[chainId], token.decimals);
  const gt = !hideBalance && greaterThanMax(inputValue, totalBalance);

  useEffect(() => {
    if (setOverBalance) {
      if (gt) {
        setOverBalance(true);
      } else {
        setOverBalance(false);
      }
    }
  }, [gt]);

  if (hideBalance) return null;
  return (
    <>
      <Link onClick={() => onMaxBtn(totalBalance)}>
        <Text color={colorModalTitle} fontWeight="600">
          BALANCE: {totalBalance} {token.name}
        </Text>
      </Link>
      {gt && !setOverBalance && <Text color={SETTINGS.RED}>Not enough balance</Text>}
    </>
  );
};
