import { Box, Flex } from "@chakra-ui/react";

import WelcomeOnBoard from "./WelcomeOnBoard";
import { useManagerDispatch, useManagerState } from "../context/managerContext";
import OverviewHeader from "./OverviewHeader";
import { BalanceTable } from "./BalanceTable";
import { useParams } from "react-router-dom";
import { useEffect } from "react";

function Overview() {
  const { loading, portfolioSelected } = useManagerState();
  // Add portfolio directly from URL
  const { address } = useParams();
  const { addPortfolio } = useManagerDispatch();

  // detect url param to add new agreement
  useEffect(() => {
    if (address) {
      console.log("-> Adding new portfolio: ", address);
      addPortfolio({ address: address });
    }
  }, [address, addPortfolio]);

  return (
    <>
      {!portfolioSelected?.address ? (
        <WelcomeOnBoard />
      ) : (
        <Box w="full">
          <OverviewHeader isLoading={loading} />
          <Flex mt="12">
            <BalanceTable portfolioSelected={portfolioSelected.address} />
          </Flex>
        </Box>
      )}
    </>
  );
}

export default Overview;
