import useStrategies from "./useStrategies";

export function useWhitelistedStrategies() {
  const { data, isLoading, isError } = useStrategies();

  const FILTERED_STRATEGIES = [
    "0xd1d69a0626af81da08277efade3318de4b5d4d98",
    "0xe2f6fde8939a77c316a8c006fb7a4480089c7e42",
  ];

  const result = data?.filter((t) => FILTERED_STRATEGIES.includes(t.id));

  return {
    data: result,
    isLoading: isLoading,
  };
}
