import { useEffect, useMemo, useState } from "react";
import { QUERY_STATUS } from "../constants";
import {
  bn,
  formatBNPrice,
  getHoldings,
  isTheSameAddress,
  mulBN,
} from "../helpers";
import useToken from "./useToken";

const useExitEstimation = (
  tokenIn,
  strategyAccountData,
  ratio,
  tokenOut = undefined,
  queryState,
  queryCallback,
  stStats
) => {
  const [amountOut, setAmountOut] = useState("0");
  const tokenInInfo = useToken(tokenIn);
  const tokenOutInfo = useToken(tokenOut);

  const totalHoldings = getHoldings(
    strategyAccountData?.shares,
    strategyAccountData?.strategy?.shares,
    stStats?.deposited
  );

  const amountIn = formatBNPrice(
    mulBN(totalHoldings, ratio),
    tokenInInfo?.decimals
  );
  const same =
    tokenOut === undefined ? true : isTheSameAddress(tokenIn, tokenOut);
  useEffect(() => {
    if (queryState.status === QUERY_STATUS.DONE) {
      const qPosition = queryState.queryPos - 1;
      const qResult = queryState.result;

      setAmountOut(qResult[qPosition]);

      // force this rare case
      if (qResult[qPosition] === undefined) {
        queryCallback();
      }
    }
  }, [queryState]);

  return useMemo(() => {
    return {
      same,
      amountIn,
      tokenIn,
      tokenOut,
      amountOut: formatBNPrice(amountOut, tokenOutInfo?.decimals),
      isLoading: queryState.status === QUERY_STATUS.LOADING,
    };
  }, [same, amountIn, tokenIn, tokenOut, queryState, amountOut]);
};

export default useExitEstimation;
