import {
  Text,
  Box,
  Flex,
  Heading,
  SkeletonCircle,
  SkeletonText,
  Skeleton,
  Image,
} from "@chakra-ui/react";
import { InstrumentsLogos } from "../../assets/Instruments";
import { MimicBadge } from "./MimicBadge";
import { useTranslation } from "react-i18next";

export const StrategyTitle = ({
  stMetadata,
  isLoading,
  hideDescription,
  px,
  pb,
}) => {
  return (
    <>
      {!isLoading ? (
        <Flex
          minH={!hideDescription ? "210px" : "0"}
          direction="column"
          px={px}
          pb={pb}
          h="full"
        >
          <Flex direction="column" align="flex-start" h="full">
            <Flex align="flex-start" justify="space-between" w="full">
              <Flex wrap="wrap" h="full">
                {stMetadata?.instruments &&
                  stMetadata?.instruments.map((inst) => {
                    return (
                      <Image
                        key={inst}
                        mr="1"
                        mb="2"
                        h="35px"
                        src={InstrumentsLogos[inst]}
                      />
                    );
                  })}
              </Flex>
              <RenderBadge type={stMetadata?.type} oneColor={hideDescription} />
            </Flex>
            <Text
              textTransform="uppercase"
              fontSize="1.25rem"
              fontWeight="600"
              pt="4"
              noOfLines={1}
            >
              {stMetadata?.title}
            </Text>
            <Text fontSize="1rem">
              {stMetadata ? `v${stMetadata?.version}` : "No data"}
            </Text>
            {!hideDescription && (
              <>
                <Flex w="full" align="center">
                  <Text fontSize="1rem" noOfLines={4}>
                    {stMetadata?.description}
                  </Text>
                </Flex>
              </>
            )}
          </Flex>
        </Flex>
      ) : (
        <LoadingSkeleton hideDescription={hideDescription} />
      )}
    </>
  );
};

const RenderBadge = ({ type, oneColor }) => {
  const { t } = useTranslation();
  const isStable = type?.toUpperCase() === "STABLE";
  return (
    <MimicBadge schema={oneColor ? "subtle" : isStable ? "light" : null} mt="1">
      {isStable ? t("stableStrategy") : t("tokenStrategy")}
    </MimicBadge>
  );
};

const LoadingSkeleton = ({ hideDescription }) => {
  return (
    <Box minH={!hideDescription ? "210px" : "0"} py="3">
      <Flex justify="space-between" w="full" align="center">
        <Flex>
          <SkeletonCircle size="12" />
          <SkeletonCircle size="12" ml="-3" />
        </Flex>
        <Skeleton h="25px" w="130px" />
      </Flex>
      <Skeleton mt="6">
        <Heading textTransform="uppercase" fontSize="1.5rem" noOfLines={1}>
          the hidden title skeleton
        </Heading>
      </Skeleton>
      <SkeletonText mt="4" noOfLines={3} spacing="4" />
    </Box>
  );
};
