import { Text, Flex, Spinner, Box } from "@chakra-ui/react";
import useToken from "../../hooks/useToken";
import useUSDCPrice from "../../hooks/useUSDCPrice";
import {
  formatBNPrice,
  formatPercentage,
  getPercentPortfolio,
  getHoldings,
} from "../../helpers";
import { ChainId } from "@usedapp/core";
import { SETTINGS } from "../../helpers/settings";
import { useTranslation } from "react-i18next";
import useStrategyStats from "../../hooks/useStrategyStats";
import { getAnnualYield } from "../../helpers/annualYieldCalc";
import { FIXED_APY, FIXED_CHANGE_TITLE } from "../../constants";
export const StrategyStats = ({
  strategy,
  isLoading,
  holdings,
  stMetadata,
}) => {
  const { t } = useTranslation();
  const tokenStrategy = strategy?.token?.id;

  const { data: stStats } = useStrategyStats(
    stMetadata?.subgraph,
    strategy?.id
  );

  const actualHoldings = getHoldings(
    holdings?.shares, // portfolio holdings
    holdings?.strategy?.shares,
    stStats?.deposited
  );

  // const apy = getAnnualYield(stStats?.rates)
  // console.log("ST id", strategy?.id);
  // console.log("ST holdings", holdings);
  // console.log("ST metadata", stMetadata);
  // console.log("ST stats", stStats);
  // console.log("ST APY",  apy);
  // console.log("holdings.strategy finalRate", finalRate);

  return (
    <>
      <ApyRow
        title={FIXED_CHANGE_TITLE[strategy?.id]}
        value={FIXED_APY[strategy?.id]}
        isLoading={isLoading}
      />

      <NumberRow
        title={t("strategyHoldings")}
        token={tokenStrategy}
        amount={stStats?.deposited}
        isLoading={isLoading}
      />

      <NumberRow
        title={t("portfolioHoldings")}
        token={tokenStrategy}
        amount={actualHoldings}
        isLoading={isLoading}
      />

      <ApyRow
        title={"shares"}
        value={getPercentPortfolio(
          holdings?.shares,
          holdings?.strategy?.shares
        )}
        isLoading={isLoading}
      />
      {/* TODO: REMOVE DEBUG */}
      {/* <Box> */}
        {/* <Text color="orange" noOfLines={1}>rates count: {stStats?.rates.length}</Text>  */}
        {/* <Text color="orange" noOfLines={1}>{strategy?.metadata}</Text> */}
        {/* <Text color="orange" noOfLines={1}>token: {strategy?.token?.id}</Text> */}
        {/* <Text color="orange" noOfLines={1}> {strategy?.id}</Text>  */}
      {/* </Box> */}
    </>
  );
};

const ApyRow = ({ title, value, isLoading }) => {
  return (
    <Flex direction="column" mb={3} px={6}>
      <TitleRow>{title}</TitleRow>
      <Flex justify="space-between">
        {isLoading ? (
          <Spinner size="sm" color="gray.600" />
        ) : (
          <Flex align="center" fontWeight="600">
            <Text mr={2}>{value}</Text>
            <Text>%</Text>
          </Flex>
        )}
      </Flex>
    </Flex>
  );
};

const NumberRow = ({ title, token, amount, isLoading }) => {
  const tk = useToken(token);
  const address = tk ? tk[ChainId.Mainnet] : token;
  const amountNormalized = formatBNPrice(amount, tk.decimals);
  const price = useUSDCPrice(address, amountNormalized);
  return (
    <Flex direction="column" mb={3} px="6" w="full">
      <TitleRow>{title}</TitleRow>
      <Flex key={token} justify="space-between">
        <ShowAmountWithToken price={amountNormalized} tk={tk} />
        <Flex fontWeight="600">
          {isLoading ? (
            <Spinner size="sm" color="gray.600" />
          ) : (
            <ShowAmountWithToken price={price} simbol={"$"} />
          )}
        </Flex>
      </Flex>
    </Flex>
  );
};

const TitleRow = ({ children }) => {
  return (
    <Text color={SETTINGS.GRAY} py="0" pt="4" fontWeight="500">
      {children}
    </Text>
  );
};

const ShowAmountWithToken = ({ price, tk, simbol }) => {
  if (price === "0.00") {
    return "-";
  } else {
    return (
      <Flex align="center" fontWeight="500">
        <Text>
          {simbol} {price}
        </Text>
        {tk ? <Text ml="2">{tk?.name}</Text> : null}
      </Flex>
    );
  }
};
