import { useCallback, useEffect, useState } from "react";
import { Flex, Text, Alert, Image } from "@chakra-ui/react";
import { APPLICATION_MODAL } from "../context/appContext";
import {
  useModalOpen,
  useModalParams,
} from "../hooks";
import { useManagerState } from "../context/managerContext";
import { useParams } from "react-router";
import {
  formatBNPercentage,
  getAssetImg,
  shortenAddress,
  updateLocalTransactionState,
} from "../helpers";
import { RiAlertFill } from "react-icons/ri";
import { ChainId, useEthers } from "@usedapp/core";
import { constants } from "ethers";
import useToken from "../hooks/useToken";
import { ManagerAddress } from "../components/Web3/ManagerAddress";
import { TransactionStatus } from "../components/common/TransactionStatus";
import useAgreement from "../hooks/useAgreement";
import useStrategyAgreementHoldings from "../hooks/useStrategyAgreementHoldings";
import ExitOptions from "../components/ExitOptions";
import { BoxContainer, SimpleTitle } from "../components/common/BoxContainer";
import { STRATEGY_VIEWS } from "../constants";
import { useExitCallback } from "../hooks/useExitCallback";
import StButton from "../components/StButton";
import { ShowFormError } from "../components/ShowFormError";

export default function ExitStrategy() {
  // here we receive the strategy selected
  const modalParams = useModalParams();
  // selected strategy
  let { strategy } = useParams();
  // swe need the agreements fees
  const { data: agreement } = useAgreement();

  // watch account
  const { account, chainId } = useEthers();

  const [view, setView] = useState(STRATEGY_VIEWS.START);
  // address of the selected strategy
  const [selectedStrategy, setSelectedStrategy] = useState(
    constants.AddressZero
  );
  const [{ errorMessage, txHash }, setState] = useState({
    errorMessage: undefined,
    txHash: undefined,
  });
  const [ratio, setRatio] = useState(1);
  const [tokenAmounts, setTokenAmounts] = useState({ token: "", amount: null });

  const { data: strategyAccountData } = useStrategyAgreementHoldings(selectedStrategy);

  // Modal management
  const exitStrategyModalOpen = useModalOpen(
    APPLICATION_MODAL.EXIT_FROM_STRATEGY
  );

  const { portfolioSelected } = useManagerState();

  const {
    state: stateCallback,
    callback: exitCallback,
    queryCallback,
    queryState,
  } = useExitCallback(
    portfolioSelected.address,
    selectedStrategy,
    strategyAccountData?.strategy?.token?.id,
    tokenAmounts?.token,
    ratio
  );

  useEffect(() => {
    console.log("Setting a default token");
    const withdrawToken = strategyAccountData?.strategy?.token?.id;
    setTokenAmounts({ token: withdrawToken });
  }, [strategyAccountData]);

  // always resetting view
  useEffect(() => {
    if (exitStrategyModalOpen) {
      setView(STRATEGY_VIEWS.START);
      setState({
        errorMessage: undefined,
        txHash: undefined,
      });
    }
  }, [exitStrategyModalOpen]);

  // Enable the feature to use the modal from an URL
  useEffect(() => {
    setSelectedStrategy(
      modalParams ? modalParams : strategy ? strategy : constants.AddressZero
    );
    setView(STRATEGY_VIEWS.SELECTED);
    setState({
      errorMessage: undefined,
      txHash: undefined,
    });
  }, [modalParams, strategy]);

  // Control action for the InputElement component.
  const handleInputTokenChange = (token, amount, reset = false) => {
    if (token !== null) {
      setTokenAmounts({ token: token, amount: amount });
    }
  };

  // check for tx status None Mining Success Failed Exception
  // the callback with the state changed of the transaction
  useEffect(() => {
    updateLocalTransactionState(stateCallback, setState, setView);
  }, [stateCallback]);

  const resetJoinView = () => {
    setView(STRATEGY_VIEWS.START);
  };

  const handleJoin = useCallback(() => {
    setView(STRATEGY_VIEWS.LOADING);
    setState({ errorMessage: undefined, txHash: undefined });
    exitCallback();
  }, [account, portfolioSelected, exitCallback, tokenAmounts]);

  function getModalBody() {
    // START State
    if (view === STRATEGY_VIEWS.SELECTED || view === STRATEGY_VIEWS.START) {
      return (
        <ExitOptions
          strategyAccountData={strategyAccountData}
          handleInputTokenChange={handleInputTokenChange}
          tokenAmounts={tokenAmounts}
          ratio={ratio}
          setRatio={setRatio}
          queryCallback={queryCallback}
          queryState={queryState}
        />
      );
    }

    // Confirm window
    if (view === STRATEGY_VIEWS.CONFIRM) {
      return (
        <Flex direction="column" w="full">
          <BoxContainer title="Portfolio Selected:">
            <Text>
              {shortenAddress(portfolioSelected?.address)} | {agreement?.name}
            </Text>
          </BoxContainer>

          <SimpleTitle>Manager:</SimpleTitle>
          <ManagerAddress py="6" mb="4" />

          <BoxContainer title="Deposit Fee:">
            <Text>{formatBNPercentage(agreement?.portfolio?.depositFee)}</Text>
          </BoxContainer>

          <BoxContainer title="Join Strategy:">
            <Text>title</Text>
          </BoxContainer>

          <BoxContainer title="Amounts:">
            <Flex w="full" direction="column">
              {tokenAmounts &&
                Object.keys(tokenAmounts).map((token) => {
                  return (
                    <ShowTokenAmount
                      key={token}
                      address={token}
                      amount={tokenAmounts[token]}
                    />
                  );
                })}
            </Flex>
          </BoxContainer>
        </Flex>
      );
    }
  }

  return (
    <>
      <Flex w="full" justify="center" >
        <TransactionStatus
          status={view}
          txHash={txHash}
          chainId={chainId}
          errorMessage={errorMessage}
          errorChildren={
            <Flex align="center" textDecor="underline" onClick={resetJoinView}>
              <Text px="2">Try again</Text>
            </Flex>
          }
          doneMessage={`Transaction success!`}
          doneChildren={null}
        />
        {getModalBody()}
      </Flex>
      <ShowFormError errorMessage={errorMessage} />
      <Flex w="full">
        {(view === STRATEGY_VIEWS.SELECTED || view === STRATEGY_VIEWS.START) && (
          <StButton
            onClick={handleJoin}
            isDisabled={view === STRATEGY_VIEWS.START}
          >
            Exit Strategy
          </StButton>
        )}

        {view === STRATEGY_VIEWS.CONFIRM && (
          <StButton onClick={handleJoin}>Confirm Exit</StButton>
        )}
      </Flex>
    </>
  );
}

const HeaderInfo = ({ errorMessage }) => {
  return (
    <>
      {errorMessage && (
        <Alert status="error">
          <RiAlertFill />
          <Text ml={2}> Error: {errorMessage} </Text>
        </Alert>
      )}
    </>
  );
};

const ShowTokenAmount = ({ address, amount }) => {
  const token = useToken(address);
  return (
    <Flex align="center">
      <Image src={getAssetImg(token[ChainId.Mainnet])} boxSize="1rem" />
      <Text px={2}>{amount}</Text>
      <Text>{token?.name}</Text>
    </Flex>
  );
};
