import { useMemo } from "react";
import { useEthers } from "@usedapp/core";
import { isTheSameAddress, mulBN } from "../helpers";
import { TRANSACTION_TYPE } from "../constants";
import useTransactionComposition from "./useTransactionComposition";
import { useApplicationState } from "../context/appContext";

export function useExitCallback(
  portfolioAddress,
  strategy = "", //strategy address
  tokenStrategy = "", //strategy address
  token = "",
  ratio = "0",
) {
  const { chainId } = useEthers();
  const same = isTheSameAddress(tokenStrategy, token);
  const { slippage } = useApplicationState();

  const tx = {
    account: portfolioAddress,
    strategy: strategy.toString(),
    tokenOut: token?.toString(),
    amount: mulBN('0','1e18'),
    tokenIn: tokenStrategy?.toString(),
    ratio: mulBN(ratio.toString(),'1e18'),
    slippage: mulBN(slippage, "1e18"),
  };

  const { state, send, query, queryState } =
    useTransactionComposition(
      token,
      '18', // no need for decimals
      0, // no amount on exit
      same,
      tx,
      TRANSACTION_TYPE.EXIT
    );

  // console.log(
  //   "useExitCallback 11",
  //   portfolioAddress,
  //   strategy ,
  //   tokenStrategy,
  //   token ,
  //   ratio ,
  // );

  return useMemo(() => {
    // if (!strategy || !library || !account || !chainId || !amount) {
    //   return { state: null, callback: null, error: "Missing dependencies" };
    // }
      console.log(
    "useExitCallback",
    same, queryState
  );

    return {
      state: state,
      queryCallback: async function onQuery() {
        return query();
      },
      queryState,
      callback: async function onExit() {
        return send();
      },
    };
  }, [strategy, chainId, state, tx, send, same]);
}
