import * as _ from "lodash";
import { Contract } from "@ethersproject/contracts";
import { getAddress } from "@ethersproject/address";
import { AddressZero } from "@ethersproject/constants";
import { formatUnits, parseEther } from "@ethersproject/units";
import numeral from "numeral";
import { Decimal } from "decimal.js";
import { DUST_THRESHOLD, EMPTY_TOKEN, TRANSACTION_STATUS } from "../constants";
import { utils } from "ethers";
import { WHITELISTED_TOKENS } from "../constants/tokens";

export function isAddress(value) {
  try {
    return getAddress(value);
  } catch {
    return false;
  }
}

export const CHAIN_ID_NAME = {
  1: "mainnet",
  3: "ropsten",
  4: "rinkeby",
  5: "goerli",
  42: "kovan",
};

const ETHERSCAN_PREFIXES = {
  1: "",
  3: "ropsten.",
  4: "rinkeby.",
  5: "goerli.",
  42: "kovan.",
};

export function getEtherscanLink(networkId, data, type) {
  const prefix = `https://${
    ETHERSCAN_PREFIXES[networkId] || ETHERSCAN_PREFIXES[1]
  }etherscan.io`;

  switch (type) {
    case "transaction": {
      return `${prefix}/tx/${data}`;
    }
    case "address":
    default: {
      return `${prefix}/address/${data}`;
    }
  }
}

const generalFormatPrice = "0.00";

export function shortenAddress(address, digits = 4) {
  // if (!isAddress(address)) {
  //   throw Error(`Invalid 'address' parameter '${address}'.`);
  // }
  return `${address?.substring(0, digits + 2)}...${address?.substring(
    42 - digits
  )}`;
}

export function getKeyByValue(obj, index) {
  // if (!index) return null;
  return _.invert(obj)[index];
}

export function getListTokens(inTokens) {
  return _.keys(inTokens).toString();
}

export function getTokenDecimals(addressToken, chainId) {
  return (
    WHITELISTED_TOKENS.find((t) =>
      isTheSameAddress(t[chainId], addressToken)
    ) || EMPTY_TOKEN
  );
}

export function getTokenSymbol(token) {
  return Object.keys(token);
}

// account is not optional
export function getSigner(library, account) {
  return library.getSigner(account).connectUnchecked();
}

// account is optional
export function getProviderOrSigner(library, account = null) {
  return account ? getSigner(library, account) : library;
}

// account is optional
export function getContract(address, ABI, library, account = null) {
  if (!isAddress(address) || address === AddressZero) {
    throw Error(`Invalid 'address' parameter '${address}'.`);
  }
  return new Contract(address, ABI, getProviderOrSigner(library, account));
}

export function formatName(name) {
  if (!name) return "";
  return name.replace(/\s+/g, "").toUpperCase();
}

export function formatUSDC(value, decimals = 6) {
  if (!value) return "0";
  const price = formatUnits(value.toString(), decimals);
  return price.toString();
}

export function formatAmountToWei(value) {
  if (!value) return "0";
  const price = parseEther(value.toString());
  return price.toString();
}

export function formatPrice(value) {
  if (!value) return generalFormatPrice;
  const price = numeral(value).format(generalFormatPrice);
  return price;
}

// magic to get prity numbers for humans
export function formatBNPrice(value = "", decimals = 18) {
  const Dec = Decimal.clone({ precision: 50, defaults: true });
  if (value.toString() === "NaN") return "0";
  if (!value.toString()) return "0";
  const a = new Dec(value.toString());
  if (!a.isFinite()) return "0";
  const priceDecimal = formatUnits(a.toFixed(0), decimals);
  const b = new Dec(priceDecimal);
  // console.log("vaultBalanceAccount ->", value, b.toString(), decimals);
  const price = numeral(b.toFixed(4)).format(generalFormatPrice);
  return price;
}

export function formatPercentage(value, decimals = 18) {
  const a = new Decimal(value).isFinite();
  if (!a) return "0";
  const priceDecimal = formatUnits(value, decimals);
  const price = numeral(priceDecimal).multiply(100).format(generalFormatPrice);
  return price;
}

export function formatBNUnits(value, decimals = 18) {
  // const Dec = Decimal.clone({ precision: 50, defaults: true });
  if (!value) return "0";
  // const a = new Dec(value)
  // if (!a.isFinite()) return "0";
  const priceDecimal = formatUnits(value, decimals || 18);
  return priceDecimal;
}

export function formatBNPercentage(value, decimals = 18) {
  if (!value) return "0";
  const priceDecimal = formatUnits(value, decimals);
  const price = numeral(priceDecimal).format("0.00%");
  return price;
}

export function amountPerPrice(amount, price = 0) {
  if (!amount) return "0";
  // const totalPrice = numeral(amount).multiply(price).format(generalFormatPrice);
  const priceDecimal = mulBN(amount, price);
  const totalPrice = numeral(priceDecimal).format(generalFormatPrice);
  return totalPrice;
}

export function isTheSameUrl(url1, url2) {
  if (!url1 || !url2) return false;
  return url1.localeCompare(url2, undefined, { sensitivity: "base" }) === 0;
}

export function isTheSameAddress(url1, url2) {
  if (!url1 || !url2) return false;
  return (
    url1
      .toString()
      .localeCompare(url2.toString(), undefined, { sensitivity: "base" }) === 0
  );
}

export function getAssetImg(address) {
  if (!address) return "";
  return `https://storage.googleapis.com/zapper-fi-assets/tokens/ethereum/${address.toLowerCase()}.png`;
}

export function isEmpty(value) {
  return _.isEmpty(value);
}

export function greaterThanMax(value, max) {
  if (!value || !max) return false;
  const a = new Decimal(value);
  const b = new Decimal(max);
  const gt = a.gt(b);
  if (!a.isFinite() || !b.isFinite()) return false;
  return gt;
}

export function greaterOrEqualThanMax(value, max) {
  if (!value || !max) return false;
  const a = new Decimal(value);
  const b = new Decimal(max);
  const gt = a.gte(b);
  if (!a.isFinite() || !b.isFinite()) return false;
  return gt;
}

export function withBalance(value) {
  if (!value) return false;
  const a = new Decimal(value);
  const gt = a.gt(DUST_THRESHOLD);
  if (!a.isFinite()) return false;
  return gt;
}

export function sumBN(a, b) {
  const aa = new Decimal(a);
  const bb = new Decimal(b);
  if (!aa.isFinite() || !bb.isFinite()) return 0;
  const ab = aa.add(bb);
  return ab.toString();
}

export function mulBN(a, b) {
  if (!a || !b) return 0;
  const ab = new Decimal(a).mul(new Decimal(b));
  return ab.toString();
}

export function divBN(a, b) {
  if (!a) return "0";
  const ab = new Decimal(a).div(new Decimal(b));
  return ab.toFixed(0);
}

export function subBN(a, b) {
  if (!a || !b) return 0;
  const ab = new Decimal(a).sub(new Decimal(b));
  return ab.toString();
}

export function subBNfee(a, depositFee, decimals = 18) {
  if (!a || !depositFee) return 0;
  const fee = new Decimal(formatUnits(depositFee, decimals).toString());
  const totalFee = new Decimal(a).mul(fee);
  const ab = new Decimal(a).sub(totalFee);
  return ab.toFixed(0);
}

export function feePercentage(a) {
  if (!a) return "0";
  const ab = new Decimal(a).div(new Decimal("100"));
  return ab.toString();
}

export function getAgreementAddressFromEvents(events) {
  if (!events) return null;
  // const event = events.find(e => e.event === 'AgreementCreated'); //OLD
  // if (!event) return null;
  return events[0]["address"];
}

export function getHoldings(shares, totalShares, deposit) {
  if (!shares || !totalShares || !deposit) return "0";
  const perShares = new Decimal(shares).div(new Decimal(totalShares));
  const holdings = new Decimal(perShares).mul(new Decimal(deposit));
  return holdings.toFixed(0);
}

export function getPercentPortfolio(shares, totalShares) {
  if (!shares || !totalShares) return "0";
  const perShares = new Decimal(shares).div(new Decimal(totalShares));
  const holdings = new Decimal(perShares).mul(new Decimal("100"));
  return holdings.toFixed(2) || 0;
}

export function getBalanceByToken(data, token) {
  // console.log("getBalanceByToken(", data, token);
  if (!data || !token) return "0";
  const [tokenBalance] = data?.filter((t) =>
    isTheSameAddress(t?.token?.id, token)
  );
  return tokenBalance?.amount || "0";
}

// (totalJoinAmount - vaultBalance) * (1 - fee)
export function getAmountToDeposit(totalJoinAmount, vaultBalance, depositFee) {
  if (!totalJoinAmount || !vaultBalance) return "0";
  const subTotal = new Decimal(totalJoinAmount).sub(new Decimal(vaultBalance));
  const fee = new Decimal("1e18").sub(new Decimal(depositFee));
  const total = subTotal.mul(fee);
  return total.div("1e18").toFixed(0);
}

// (finalRate - initialRate)/initialRate (365 * 24 * 60 * 60) / (timeFinalRate - timeInitialRate)
export function getApy(initialRate, finalRate) {
  if (!initialRate || !finalRate) return "0";
  const instantApy = new Decimal(finalRate.value)
    .sub(new Decimal(initialRate.value))
    .mul(new Decimal("1e18"))
    .div(new Decimal(initialRate.value));
  const secondsPerRate = new Decimal("31536000").mul(instantApy); // 365 * 24 * 60 * 60
  const difTime = new Decimal(finalRate.timestamp).sub(
    new Decimal(initialRate.timestamp)
  );
  const apy = new Decimal(secondsPerRate).div(difTime);
  return apy.toFixed(0);
}

export function getSimpleApyInSeconds(a, b, c) {
  if (!a || !b) return "0";
  console.log("getSimpleApyInSeconds", b, a);
  const baAccDiff = new Decimal(b.accumulators[0]).sub(
    new Decimal(a.accumulators[0])
  );
  const baTimeDiff = new Decimal(b.timestamp).sub(new Decimal(a.timestamp));
  const rateBA = baAccDiff.div(baTimeDiff);

  const cbAccDiff = new Decimal(c.accumulators[0]).sub(
    new Decimal(b.accumulators[0])
  );
  const cbTimeDiff = new Decimal(c.timestamp).sub(new Decimal(b.timestamp));
  const rateCB = cbAccDiff.div(cbTimeDiff);

  // console.log('getSimpleApyInSeconds accB: ',b.accumulators[0], ' accA: ',a.accumulators[0], ' tB: ', b.timestamp, ' tA: ', a.timestamp, ' accDiff: ', accDiff.toString(), ' timeDiff: ', timeDiff.toString(), ' accDivTime: ',  accDivTime.toString())
  const diff = rateCB.sub(rateBA);
  return diff.toString();
}

export function annualizedValueInSeconds(value) {
  if (!value) return "0";
  const annValue = new Decimal("31536000").mul(value); // 365 * 24 * 60 * 60
  return annValue.toFixed(0);
}

export function bn(bn) {
  if (!bn) return "0";
  const newBn = new Decimal(bn);
  return newBn.toString();
}

export function parseBN(value = "0", decimals = 18) {
  if (!value || !decimals) return "0";
  if (value === "0.0") return "0";
  const newBn = utils.parseUnits(value, decimals);
  return newBn.toString();
}

export function filterEmptyElements(array) {
  if (!array) return [];
  return array.filter(Boolean);
}

export function updateLocalTransactionState(stateCallback, setState, setView) {
  console.log(
    `updateLocalTransactionState: useEffects stateCallback: ${JSON.stringify(
      stateCallback
    )}`
  );

  const hash = stateCallback?.transaction?.hash;

  if (stateCallback?.status === "Mining") {
    setView(TRANSACTION_STATUS.MINING);

    setState({
      createErrorMessage: undefined,
      txHash: hash,
    });
  }
  if (
    stateCallback?.status === "Fail" ||
    stateCallback?.status === "Exception"
  ) {
    console.log("updateLocalTransactionState error ", stateCallback);
    setState({
      errorMessage: stateCallback.errorMessage,
      txHash: hash,
    });
    setView(TRANSACTION_STATUS.ERROR);
  }
  if (stateCallback?.status === "Success") {
    setView(TRANSACTION_STATUS.DONE);
    setState({
      errorMessage: undefined,
      txHash: hash,
    });
  }
}
