import { useTokensBalance } from "./useTokensBalance";
import { useMemo } from "react";
import {
  formatBNUnits,
  getBalanceByToken,
  isAddress,
  sumBN,
  withBalance,
} from "../helpers";
import useVaultBalance from "./useVaultBalance";

const useAvailableBalance = (tk, decimals = 18, amount = "0") => {
  const token = isAddress(tk?.toString());
  const tokensBalance = useTokensBalance();
  const { data: vaultBal } = useVaultBalance();
  const vaultBalance = formatBNUnits(
    getBalanceByToken(vaultBal, tk?.toString()) || "0",
    decimals
  );

  const agreementBalance = formatBNUnits(
    tokensBalance[token && token] || "0",
    decimals
  );

  const aBalance = withBalance(agreementBalance) ? agreementBalance : 0;
  const vBalance = withBalance(vaultBalance) ? vaultBalance : 0;
  const total = sumBN(vBalance || 0, aBalance || 0);
  return useMemo(() => {
    // console.log('useAvailableBalance', token, decimals , total, aBalance,vBalance)

    return {
      totalBalance: total,
      agreementBalance: aBalance,
      vaultBalance: vBalance,
    };
  }, [tokensBalance, total, amount, token, tk, aBalance, vBalance, vaultBal]);
};

export default useAvailableBalance;
