import { useMemo } from 'react';
import useSWR from 'swr';
import axios from 'axios';

export const IPFSfetcher = url => url ? axios.get(url).then(res => res.data) :  null;
export const getIPFSurl = metadata => {
  const filterHash = (hash) => {
    if(!hash) return null
    const arrayHash = hash?.split('ipfs:')
    const validHash = arrayHash?.length > 1 ? arrayHash[1] : null
    return validHash
  }

  const IPFShash = filterHash(metadata);
  return `https://cloudflare-ipfs.com/ipfs/${IPFShash}`;
}

// IPFS with hooks
export default function useIPFSMetadata(metadata) {
  const url = getIPFSurl(metadata)
  const { data, error } = useSWR(url, IPFSfetcher);

  return useMemo(() => {
    return {
      data: data, 
      isLoading: !error && !data,
      isError: error
    };
  }, [metadata, data]);
}
