import { Flex, Text } from '@chakra-ui/react';
import { getExplorerTransactionLink } from '@usedapp/core';
import { Link } from 'react-router-dom';
import { FiExternalLink } from 'react-icons/fi';

export const TransactionHashLink = ({ hash, chainId }) => (
  <>
    {hash && (
      <Link
        to={{ pathname: getExplorerTransactionLink(hash, chainId) }}
        target="_blank"
        rel="noopener noreferrer"
      >
        <Flex align="center" textDecor="underline">
          <Text px="2">View on Etherscan</Text>
          <FiExternalLink />
        </Flex>
      </Link>
    )}
  </>
);
