import { Badge } from "@chakra-ui/layout";
import { SETTINGS } from "../../helpers/settings";

export const MimicBadge = ({ schema, children, mt }) => {
  if (schema === "light") {
    return (
      <SimpleBadge mt={mt} bg={SETTINGS.VIOLET_LIGHTER} color={SETTINGS.VIOLET_DARK}>
        {children}
      </SimpleBadge>
    );
  }

  if (schema === "subtle") {
    return (
      <SimpleBadge mt={mt} bg={SETTINGS.PRIMARY} color={SETTINGS.VIOLET_LIGHTER}>
        {children}
      </SimpleBadge>
    );
  }


  return (
    <SimpleBadge mt={mt} bg={SETTINGS.PRIMARY} color={SETTINGS.VIOLET_DARK}>
      {children}
    </SimpleBadge>
  );
};

const SimpleBadge = ({ bg, children, color, mt }) => {
  return (
    <Badge variant="subtle" bg={bg} color={color} py="1" px="2" mt={mt}>
      {children}
    </Badge>
  );
};
