import { useEthers } from "@usedapp/core";
import { EMPTY_TOKEN } from "../constants";
import { WHITELISTED_TOKENS } from "../constants/tokens";
import { isTheSameAddress } from "../helpers";

export default function useToken(addressToken) {
  const { chainId } = useEthers();

  return WHITELISTED_TOKENS.find((t) =>
    isTheSameAddress(t[chainId], addressToken)
  ) || EMPTY_TOKEN
}
