import { Flex, Spinner } from "@chakra-ui/react";
import { AgreementSettingsViewer } from "../components/viewer/AgreementSettingsViewer";
import { RulesViewer } from "../components/viewer/RulesViewer";
import { TabTitle } from "../components/typography/TabTitle";
import { useTranslation } from "react-i18next";
import useAgreement from "../hooks/useAgreement";
import { useStyles } from "../hooks/useStyles";
import { BoxTitle } from "../components/typography/BoxTitle";

function Agreement() {
  const { t } = useTranslation();
  const { buttonBorderStyle } = useStyles();
  const { data: agreement, isLoading } = useAgreement();
  // console.log("useAgreement ", agreement, isLoading);

  return (
    <Flex align="flex-start" w="full" py={4} direction="column">
      <TabTitle>{t("agreementDescription")}</TabTitle>
      <Flex
        borderTop={buttonBorderStyle}
        wrap="wrap"
        justify="center"
        w="full"
        py="6"
        mt="6"
        direction="column"
      >
        {isLoading ? (
          <LoadingAgreement />
        ) : agreement ? (
          <>
            <AgreementSettingsViewer
              isLoading={isLoading}
              agreement={agreement}
            />
            <RulesViewer />
          </>
        ) : (
          <NoStrategiesMessage />
        )}
      </Flex>
    </Flex>
  );
}

export default Agreement;

const LoadingAgreement = () => {
  const { t } = useTranslation();

  return (
    <Flex p="6" justify="flex-start" align="center">
      <BoxTitle px="6">{t("loadingAgreement")}</BoxTitle>
      <Spinner />
    </Flex>
  );
};

const NoStrategiesMessage = () => {
  const { t } = useTranslation();

  return <BoxTitle>{t("cantFindAgreement")} 🤷‍♂️</BoxTitle>;
};
