import { useToast } from '@chakra-ui/toast';
import { useNotifications } from '@usedapp/core';
import { useEffect } from 'react';
import { BiError } from 'react-icons/bi';
import { SETTINGS } from '../helpers/settings';

export default function NotificationsUpdater() {
  const toast = useToast();
  const notificationContent = {
    transactionFailed: { title: 'Transaction failed', icon: <BiError /> },
    transactionStarted: { title: 'Transaction started', icon: <BiError /> },
    transactionSucceed: { title: 'Transaction succeed', icon: <BiError /> },
    walletConnected: { title: 'Wallet connected', icon: <BiError /> },
  };

  const showToast = (id, title) => {
    if (!toast.isActive(id)) {
      toast({
        id: id,
        title: `${title}`,
        ...SETTINGS.toast,
      });
    }
  };

  const NotificationManager = () => {
    const { notifications } = useNotifications();

    useEffect(() => {
      notifications.map(notification => {
        return showToast(
          notification.id,
          notificationContent[notification.type].title
        );
      });
    }, [notifications]);

    return null;
  };

  return <NotificationManager />;
}
