import { useQuery } from "react-query";
import { request, gql } from "graphql-request";
import { getSubgraphqlBaseURI } from "../helpers/enviroment";

const useStrategyStats = (stPath, stAddress) => {
  return useQuery(
    ["strategyStats", stAddress, stPath],
    () => fetchStrategiesStats(stPath, stAddress),
    {
      refetchInterval: 10000,
    }
  );
};

export const fetchStrategiesStats = async (stPath, stAddress) => {
  if (stPath === undefined) return [];
  const strategyGraphqlUrl = `${getSubgraphqlBaseURI()}${stPath}`;
  const { strategy } = await request(
    strategyGraphqlUrl,
    gql`
    {
      strategy(id: "${stAddress}") {
        id
        token
        metadata
        deposited
        shares
        rates {
          id
          accumulators
          shares
          block
          timestamp
          strategy {
            id
          }
        }
      }
    }
    `
  );
  return strategy;
};

export default useStrategyStats;

