import React, { useEffect } from 'react';
import { DEFAULT_INITIAL_PORTFOLIO } from '../constants';
import { useLocalStorage } from '../hooks/useLocalStorage';
import { reducer, ActionTypes } from './managerReducer';

const StateContext = React.createContext(DEFAULT_INITIAL_PORTFOLIO);
const DispatchContext = React.createContext(undefined);

export const ManagerProvider = ({ children }) => {
  const [storage, setStorage] = useLocalStorage('portfolioManager');

  const [state, dispatch] = React.useReducer(
    reducer,
    storage ? storage : DEFAULT_INITIAL_PORTFOLIO
  );

  useEffect(() => {
    console.log('New portfolio selected', state);
    setStorage(state);
  }, [state]);

  return (
    <StateContext.Provider value={state}>
      <DispatchContext.Provider value={dispatch}>
        {children}
      </DispatchContext.Provider>
    </StateContext.Provider>
  );
};

export const useManagerState = () => {
  return React.useContext(StateContext);
};

export const useManagerDispatch = () => {
  const dispatch = React.useContext(DispatchContext);

  if (dispatch === undefined) {
    throw new Error('useManagerDispatch must be used within a BookingProvider');
  }

  const addPortfolio = React.useCallback(
    portfolio => {
      dispatch({ type: ActionTypes.ADD_PORTFOLIO, portfolio });
    },
    [dispatch]
  );

  const removePortfolio = React.useCallback(
    portfolio => {
      dispatch({ type: ActionTypes.SUBSTRACT_PORTFOLIO, portfolio });
    },
    [dispatch]
  );

  const selectPortfolio = React.useCallback(
    portfolio => {
      dispatch({ type: ActionTypes.SELECT_PORTFOLIO, portfolio });
    },
    [dispatch]
  );

  const updatePortfolioBalance = React.useCallback(
    balance => {
      dispatch({ type: ActionTypes.UPDATE_PORTFOLIO_BALANCE, balance  });
    },
    [dispatch]
  );

  const updatePortfolioBalanceOnVault = React.useCallback(
    balance => {
      dispatch({ type: ActionTypes.UPDATE_PORTFOLIO_BALANCE_VAULT, balance });
    },
    [dispatch]
  );

  const updatePortfolioBalanceOnStrategies = React.useCallback(
    balance => {
      dispatch({ type: ActionTypes.UPDATE_PORTFOLIO_BALANCE_SRATEGY, balance });
    },
    [dispatch]
  );

  return React.useMemo(
    () => ({
      addPortfolio,
      removePortfolio,
      selectPortfolio,
      updatePortfolioBalance,
      updatePortfolioBalanceOnVault,
      updatePortfolioBalanceOnStrategies,
    }),
    [dispatch]
  );
};
