import { Flex, Tooltip, Text, Box, Spinner, Collapse } from "@chakra-ui/react";
import { SETTINGS } from "../../helpers/settings";
import { bn, formatBNPrice, getHoldings } from "../../helpers";
import useToken from "../../hooks/useToken";
import { QUERY_STATUS } from "../../constants";
import { FaInfoCircle } from "react-icons/fa";
import { useTranslation } from "react-i18next";
import { ShowFormError } from "../ShowFormError";
import useStrategyStats from "../../hooks/useStrategyStats";

const ShowEstimation = ({
  address,
  amount,
  strategyInfo,
  queryPos,
  show,
  stMetadata,
}) => {
  const { t } = useTranslation();
  // const { inputBorderStyleDisabled } = useStyles();
  const token = useToken(address);
  const { data: stStats } = useStrategyStats(
    stMetadata?.subgraph,
    strategyInfo?.id
  );
  const futureHoldings = getHoldings(
    bn(amount[queryPos - 1]),
    strategyInfo?.shares,
    stStats?.deposited
  );

  console.log(
    "EstimateBox ShowEstimation",
    amount,
    queryPos,
    strategyInfo?.shares,
    stMetadata?.subgraph,
    strategyInfo?.id,
    amount[queryPos - 1],
    futureHoldings
  );

  return (
    <Collapse in={show} animateOpacity w="full">
      <Flex
        align="center"
        w="full"
        justify="center"
        bg={SETTINGS.SUCCESS}
        py="2"
        px="4"
        color={SETTINGS.VIOLET_LIGHTER}
      >
        <Text px={2}>{t("portfolioWillGet")}</Text>
        <Flex w="50%" align="center" justify="flex-end">
          <Text px={2} fontWeight="bold" textAlign="right">
            {" "}
            ~ {formatBNPrice(futureHoldings, token?.decimals)} {token?.name}
          </Text>

          <Tooltip
            hasArrow
            label="Final position. Swaps and Fees applied"
            bg={SETTINGS.PRIMARY}
            placement="auto"
          >
            <Box>
              <FaInfoCircle />
            </Box>
          </Tooltip>
        </Flex>
      </Flex>
    </Collapse>
  );
};

export const EstimateBox = ({
  address,
  queryState,
  strategyInfo,
  show,
  stMetadata,
}) => {
  const { t } = useTranslation();
  // console.log("EstimateBox", queryState, strategyInfo);

  return (
    <Collapse in={show} w="full">
      <Flex w="full" justify="flex-end" minH="61px" direction="column">
        {queryState.status === QUERY_STATUS.LOADING && (
          <Flex w="full" justify="center">
            <Spinner my="2" mx="4" />
          </Flex>
        )}

        <ShowEstimation
          show={queryState.status === QUERY_STATUS.DONE}
          amount={queryState?.result}
          queryPos={queryState?.queryPos}
          address={address}
          strategyInfo={strategyInfo}
          stMetadata={stMetadata}
        />

        <ShowFormError
          errorMessage={
            queryState.status === QUERY_STATUS.ERROR
              ? t("noEstimation")
              : undefined
          }
        />
      </Flex>
    </Collapse>
  );
};
