import { useEthers } from "@usedapp/core";
import { useCallback, useState } from "react";
import { QUERY_STATUS } from "../constants";
import { connectContractToSigner } from "./useContractBatchFunction";

export function useQueryVault(contract) {
  const DEFAULT_STATUS = { status: QUERY_STATUS.NONE, error: {}, result: [] };
  const { library, account } = useEthers();
  const [state, setState] = useState(DEFAULT_STATUS);

  async function queryContract(
    contract,
    functionDataArray,
    readsOutput,
    account
  ) {
    setState({ ...DEFAULT_STATUS, status: QUERY_STATUS.LOADING });

    const result = await contract
      .query(
        functionDataArray.map((t) => t?.data),
        readsOutput,
        {
          gasLimit: 2500000, //TODO: KOVAN feature
          from: account,
        }
      )
      .then((res) => {
        const response = res.map((t) => t);
        console.log("useQueryVault THEN->", res);
        setState({ status: QUERY_STATUS.DONE, error: {}, result: res });

        return response;
      })
      .catch((res) => {
        console.log("useQueryVault2 ERROR->", res);
        setState({ status: QUERY_STATUS.ERROR, error: res, result: [] });
        return res;
      });
    return state;
  }

  const query = useCallback(
    async (functionDataArray, readsOutput) => {
      console.log("useQueryVault data", functionDataArray, readsOutput);
      if (!functionDataArray || !readsOutput) return [];
      const contractWithSigner = connectContractToSigner(contract, {}, library);
      const result = queryContract(
        contractWithSigner,
        functionDataArray,
        readsOutput,
        account
      );
      return result;
    },
    [contract]
  );

  return { state, query };
}
