import React, { useEffect, useState } from "react";
import { isMobile } from "react-device-detect";
import { injected } from "../../connectors";
import { SUPPORTED_WALLETS, WALLET_VIEWS } from "../../constants";
import usePrevious from "../../hooks/usePrevious";
import {
  Modal,
  ModalOverlay,
  ModalContent,
  ModalHeader,
  ModalBody,
  ModalCloseButton,
  Button,
  Flex,
  Image,
  Link,
} from "@chakra-ui/react";
import { APPLICATION_MODAL } from "../../context/appContext";
import { useModalOpen, useWalletModalToggle } from "../../hooks";
import AccountDetails from "./AccountDetails";
import { useEthers } from "@usedapp/core";
import { TabTitle } from "../typography/TabTitle";
import { useStyles } from "../../hooks/useStyles";
import { useTranslation } from "react-i18next";
import { SETTINGS } from "../../helpers/settings";
import { Hr } from "../strategy/Hr";

export default function WalletModal() {
  const { t } = useTranslation();
  const { bgModal, modalBorderStyle, colorModalTitle } = useStyles();

  const [walletView, setWalletView] = useState(WALLET_VIEWS.ACCOUNT);
  const walletModalOpen = useModalOpen(APPLICATION_MODAL.WALLET);
  const toggleWalletModal = useWalletModalToggle();
  const { active, account, connector, activate, error } = useEthers();
  const previousAccount = usePrevious(account);

  // close on connection, when logged out before
  useEffect(() => {
    if (account && !previousAccount && walletModalOpen) {
      toggleWalletModal();
    }
  }, [account, previousAccount, toggleWalletModal, walletModalOpen]);

  // resetting view
  useEffect(() => {
    if (walletModalOpen) {
      setWalletView(WALLET_VIEWS.ACCOUNT);
    }
  }, [walletModalOpen]);

  // close modal when a connection is successful
  const activePrevious = usePrevious(active);
  const connectorPrevious = usePrevious(connector);
  useEffect(() => {
    if (
      walletModalOpen &&
      ((active && !activePrevious) ||
        (connector && connector !== connectorPrevious && !error))
    ) {
      setWalletView(WALLET_VIEWS.ACCOUNT);
    }
  }, [
    setWalletView,
    active,
    error,
    connector,
    walletModalOpen,
    activePrevious,
    connectorPrevious,
  ]);

  const tryActivation = async (connector) => {
    let name = "";
    Object.keys(SUPPORTED_WALLETS).map((key) => {
      if (connector === SUPPORTED_WALLETS[key].connector) {
        return (name = SUPPORTED_WALLETS[key].name);
      }
      return true;
    });

    setWalletView(WALLET_VIEWS.PENDING);

    connector &&
      activate(connector, undefined, true).catch((error) => {
        if (error) {
          activate(connector); // a little janky...can't use setError because the connector isn't set
        }
      });
  };

  const RenderOptions = () => {
    const isMetamask = window.ethereum && window.ethereum.isMetaMask;
    return Object.keys(SUPPORTED_WALLETS).map((key) => {
      const option = SUPPORTED_WALLETS[key];

      // overwrite injected when needed
      if (option.connector === injected) {
        // don't show injected if there's no injected provider
        if (!(window.web3 || window.ethereum)) {
          if (option.name === "MetaMask") {
            return <InstallMm />;
          } else {
            return null; //dont want to return install twice
          }
        }
        // don't return metamask if injected provider isn't metamask
        else if (option.name === "MetaMask" && !isMetamask) {
          return null;
        }
        // likewise for generic
        else if (option.name === "Injected" && isMetamask) {
          return null;
        }
      }

      // return rest of options
      return (
        !isMobile &&
        !option.mobileOnly && (
          <>
            <Flex
              key={key}
              align="center"
              w="full"
              justify="flex-start"
              pt="6"
              pb="2"
              px={6}
            >
              <Image
                w="2rem"
                src={require("../../assets/images/" + option.iconName).default}
              />

              <Button
                // px={4}
                // w="full"
                variant="ghost"
                fontSize="1.5rem"
                color={colorModalTitle}
                onClick={() => {
                  option.connector === connector
                    ? setWalletView(WALLET_VIEWS.ACCOUNT)
                    : !option.href && tryActivation(option.connector);
                }}
                key={key}
              >
                Connect {option.name}
              </Button>
            </Flex>
          </>
        )
      );
    });
  };

  function getModalContent() {
    if (error) {
      return <Flex>{t("wrongNetwork")}</Flex>;
    }
    if (account && walletView === WALLET_VIEWS.ACCOUNT) {
      return <AccountDetails connector={connector} />;
    }
    return (
      <Flex direction="column" align="flex-start" w="full">
        <Hr />
        <Flex direction="column" align="flex-start" w="full">
          {walletView === WALLET_VIEWS.PENDING ? (
            <Flex>Please accept the message </Flex>
          ) : (
            <>
              <RenderOptions />
              <Hr />
              <Flex p="4">
                By connecting a wallet, you agree to OTF Finance app Terms of Service
                and acknowledge that you have read and understand the protocol
                disclaimer.
              </Flex>
            </>
          )}
        </Flex>

        {walletView !== WALLET_VIEWS.ACCOUNT && (
          <Flex mt={6}>
            <Button
              variant="link"
              onClick={() => setWalletView(WALLET_VIEWS.ACCOUNT)}
            >
              Back
            </Button>
          </Flex>
        )}
      </Flex>
    );
  }

  return (
    <>
      <Modal isOpen={walletModalOpen} onClose={toggleWalletModal}>
        <ModalOverlay backdropFilter="blur(2px)!important" />
        <ModalContent bg={bgModal} border={modalBorderStyle} rounded="md">
          <ModalHeader color={colorModalTitle} px="4">
            {account ? "Wallet Status" : "Connect Wallet"}
          </ModalHeader>
          <ModalCloseButton color={SETTINGS.VIOLET_DARK} mt="2" />
          <ModalBody p="0" color={colorModalTitle}>
            {getModalContent()}
          </ModalBody>
        </ModalContent>
      </Modal>
    </>
  );
}

const InstallMm = () => {
  const { t } = useTranslation();

  return (
    <Flex w="full" h="12rem" align="center" justify="center">
      <TabTitle>
        {t("needAccount")} <Link fontWeight="600">try with Metamask</Link>
      </TabTitle>
    </Flex>
  );
};
