import { useEffect, useState } from "react";
import {
  Flex,
  FormControl,
  FormLabel,
  Input,
  FormHelperText,
  Button,
  Text,
  Checkbox,
  Collapse,
  Divider,
  Select,
} from "@chakra-ui/react";
import { useForm, Controller } from "react-hook-form";
import { NewPortfolioContainer } from "../components/NewPortfolioContainer";
import { FeeInput } from "../components/FeeInput";
import { formatName, mulBN, updateLocalTransactionState } from "../helpers";
import { SETTINGS } from "../helpers/settings";
import { useEthers } from "@usedapp/core";
import { useTranslation } from "react-i18next";
import { useCreateCallback } from "../hooks/useCreateCallback";
import { TabTitle } from "../components/typography/TabTitle";
import { Link } from "react-router-dom";
import { TransactionStatus } from "../components/common/TransactionStatus";
import { useManagerDispatch } from "../context/managerContext";
import { useStyles } from "../hooks/useStyles";
import { SLIPPAGE_UI_OPTIONS } from "../constants";
import { RoundedIconButton } from "../components/common/RoundedIconButton";
import { ShowFormError } from "../components/ShowFormError";

const CREATE_VIEWS = {
  START: "start",
  MINING: "mining",
  LOADING: "loading",
  ERROR: "error",
  DONE: "done",
};

const FORM_STEP = {
  ONE: "one",
  TWO: "two",
};

function CreatePortfolio() {
  const { t } = useTranslation();
  const { buttonBorderStyle, colorSubtitle, colorTitle } = useStyles();

  const { addPortfolio } = useManagerDispatch();
  const { account, chainId } = useEthers();
  // here we have the transaction state
  const [{ errorMessage, txHash, newPortfolioAddress }, setCreateState] =
    useState({
      errorMessage: undefined,
      txHash: undefined,
      addressPortfolio: undefined,
    });

  const [createdPortfolio, setCreatedPortfolio] = useState({
    name: undefined,
    agreement: undefined,
  });
  // here we have the UI state
  const [createView, setCreateView] = useState(CREATE_VIEWS.START);
  const [diffCollectedFees, setDiffCollectedFees] = useState(false);
  const [strategyType, setStrategyType] = useState("2");
  const [tokenSwapType, setTokenSwapType] = useState("2");
  const [maxSlippage, setMaxSlippage] = useState("0.5");
  const [step, setStep] = useState(FORM_STEP.ONE);

  const isLoading = createView === CREATE_VIEWS.LOADING;

  // manipulate forms
  const {
    register,
    control,
    handleSubmit,
    watch,
    formState: { errors },
  } = useForm();
  const watchFields = watch();

  // the callback to execute the join
  const {
    state: stateCallback,
    callback: createCallback,
    events,
  } = useCreateCallback(
    chainId,
    watchFields.uniqueName,
    watchFields.wallet1,
    watchFields.wallet2 ? watchFields.wallet2 : watchFields.wallet1,
    watchFields.depositFee,
    watchFields.performanceFee,
    diffCollectedFees ? watchFields.collectedFeesAddress : account,
    maxSlippage,
    account,
    watchFields.managerBackupAddress
      ? watchFields.managerBackupAddress
      : account,
    strategyType,
    tokenSwapType
  );
  // check for tx status None Mining Success Failed Exception
  // the callback with the state changed of the transaction
  useEffect(() => {
    console.log("stateCallback", stateCallback);
    updateLocalTransactionState(stateCallback, setCreateState, setCreateView);
  }, [stateCallback]);

  useEffect(() => {
    // new portfolio created!
    if (events && events.length > 0) {
      const portfolioName = events[0]?.args?.name;
      const portfolioAddress = events[0]?.args?.agreement;
      if (portfolioName && portfolioAddress) {
        setCreatedPortfolio({
          name: portfolioName,
          agreement: portfolioAddress,
        });
        addPortfolio({ address: portfolioAddress });
      }
    }
    // console.log("events", events);
  }, [events]);

  const handleCreateOnSubmit = () => {
    if (step === FORM_STEP.ONE) {
      setStep(FORM_STEP.TWO);
    } else {
      setCreateView(CREATE_VIEWS.LOADING);
      setCreateState({
        errorMessage: undefined,
        txHash: undefined,
        newPortfolioAddress: undefined,
      });
      createCallback();
    }
  };

  const resetCreateView = () => {
    setCreateView(CREATE_VIEWS.START);
  };

  const FootButton = () => {
    return (
      <>
        <ShowErrors errors={errors} />
        <Flex
          w="full"
          align="center"
          px="6"
          py="3"
          borderTop={buttonBorderStyle}
        >
          <Flex w="full" justify="flex-start" align="center">
            <NumButton
              stateStep={step}
              setStep={setStep}
              step={FORM_STEP.ONE}
            />
            <NumButton
              stateStep={step}
              setStep={setStep}
              step={FORM_STEP.TWO}
            />
          </Flex>
          <Button
            rounded="0"
            minW="25%"
            h="4rem"
            isDisabled={isLoading}
            variant="ghost"
            type="submit"
          >
            {watchFields.uniqueName && `${watchFields.uniqueName}: `}
            {t("createPortfolio")}
          </Button>
        </Flex>
      </>
    );
  };

  return (
    <Flex textAlign="center" w="full" pt={4} direction="column">
      <Flex w="full" direction="column" pb="6" borderBottom={buttonBorderStyle}>
        <TabTitle>{t("createPortfolioTitle")}</TabTitle>
      </Flex>
      <Flex w="full" justify="center">
        {/* Show transaction status based on state */}
        <TransactionStatus
          status={createView}
          hash={txHash}
          chainId={chainId}
          errorChildren={
            <Link to={"/app/new"} onClick={resetCreateView}>
              <Flex align="center" textDecor="underline">
                <Text px="2">Try again</Text>
              </Flex>
            </Link>
          }
          doneMessage={`Portfolio ${createdPortfolio.name} created!`}
          doneChildren={
            <Link to={`/app/${createdPortfolio.agreement}`}>
              <Flex justify="center" textDecor="underline">
                <Text px="2" textAlign="center">
                  Manage portfolio
                </Text>
              </Flex>
            </Link>
          }
        />
      </Flex>

      {createView === CREATE_VIEWS.LOADING ||
        (createView === CREATE_VIEWS.START && (
          <form onSubmit={handleSubmit(handleCreateOnSubmit)}>
            {/* STEP 1 */}
            <Collapse in={step === FORM_STEP.ONE} animateOpacity w="100%">
              <Flex
                w="full"
                align="flex-start"
                justify="center"
                mt="4"
                px="6"
                minH="520px"
              >
                <Flex direction="column" w="33.333%">
                  <NewPortfolioContainer
                    title={t("portfolioName")}
                    description={t("nicknameDescription")}
                  >
                    <Flex w="full" h="full">
                      <FormControl>
                        <Flex>
                          <Input
                            w="25rem"
                            type="text"
                            rounded="sm"
                            isDisabled={isLoading}
                            border={buttonBorderStyle}
                            value={formatName(watchFields.uniqueName)}
                            placeholder={t("uniqueName")}
                            {...register("uniqueName", { required: true })}
                          />
                        </Flex>
                        <FormHelperText textAlign="left" color={colorSubtitle}>
                          {t("nameHint")}
                        </FormHelperText>
                      </FormControl>
                    </Flex>
                  </NewPortfolioContainer>

                  {/* ==== OWNER ==== */}
                  <NewPortfolioContainer
                    title={t("ownerAddress")}
                    description={t("ownerDescription")}
                  >
                    <Flex w="full" direction="column">
                      <FormControl id="hotwallet">
                        <Input
                          type="text"
                          isDisabled={isLoading}
                          border={buttonBorderStyle}
                          rounded="sm"
                          placeholder={t("ownerMainAddress")}
                          {...register("wallet1", {
                            required: true,
                            pattern: /^0x[a-fA-F0-9]{40}$/i,
                          })}
                        />
                      </FormControl>

                      <FormControl mt={6} id="Coldwallet">
                        <Input
                          type="text"
                          border={buttonBorderStyle}
                          isDisabled={isLoading}
                          rounded="sm"
                          placeholder={`${t("ownerEthereumWallet")} (optional)`}
                          {...register("wallet2", {
                            pattern: /^0x[a-fA-F0-9]{40}$/i,
                          })}
                        />

                        <FormHelperText textAlign="left">
                          {t("backupWalletHint")}
                        </FormHelperText>
                      </FormControl>
                    </Flex>
                  </NewPortfolioContainer>
                </Flex>

                <Flex direction="column" w="33.333%">
                  {/* ==== MANAGER ADDRESS ==== */}
                  <NewPortfolioContainer
                    title={t("managerTitle")}
                    description={t("managerDescription")}
                  >
                    <Flex w="full" direction="column">
                      <FormControl id="managerMainAddress">
                        <Input
                          type="text"
                          placeholder={account}
                          value={account}
                          rounded="sm"
                          border={buttonBorderStyle}
                          {...register("managerMainAddress", {
                            pattern: /^0x[a-fA-F0-9]{40}$/i,
                          })}
                        />
                        <FormHelperText textAlign="left" color={colorSubtitle}>
                          {t("managerHint")}
                        </FormHelperText>
                      </FormControl>

                      <FormControl id="managerBackupAddress" mt={6}>
                        <Input
                          type="text"
                          placeholder="Other manager Address (optional)"
                          isDisabled={isLoading}
                          rounded="sm"
                          border={buttonBorderStyle}
                          {...register("managerBackupAddress", {
                            pattern: /^0x[a-fA-F0-9]{40}$/i,
                          })}
                        />
                        <FormHelperText textAlign="left" color={colorSubtitle}>
                          {t("otherManagerHint")}
                        </FormHelperText>
                      </FormControl>
                    </Flex>
                  </NewPortfolioContainer>

                  {/* ==== MANAGER FEES ==== */}
                  <NewPortfolioContainer
                    title={t("feeTitle")}
                    description={t("feeDescription")}
                  >
                    <Flex w="full" align="flex-start">
                      <FormControl>
                        <Title title={t("feeDeposit")} />

                        <Controller
                          name="depositFee"
                          control={control}
                          defaultValue={"0.00"}
                          rules={{ required: true, min: 0, max: 100 }}
                          render={(props) => <FeeInput {...props} />}
                        />
                      </FormControl>

                      <FormControl>
                        <Title title={t("performanceFee")} />

                        <Controller
                          name="performanceFee"
                          control={control}
                          defaultValue={"0.00"}
                          rules={{ required: true, min: 0, max: 100 }}
                          render={(props) => <FeeInput {...props} />}
                        />

                        <FormHelperText textAlign="left" color={colorSubtitle}>
                          {t("feeHint")}
                        </FormHelperText>
                      </FormControl>
                    </Flex>
                  </NewPortfolioContainer>
                </Flex>
                <Flex direction="column" w="33.333%"
                      style={{visibility: 'hidden'}}>
                  {/* ==== COLLECTED FEES ==== */}
                  <NewPortfolioContainer
                    title={t("collectedFees")}
                    description={t("collectedFeesDescription")}
                  >
                    <FormControl>
                      <Flex w="full" justify="flex-start">
                        {/* if unchecked we send th manager logged address */}
                        <Checkbox
                          isDisabled={isLoading}
                          size="lg"
                          rounded="sm"
                          onChange={(e) =>
                            setDiffCollectedFees(e.target.checked)
                          }
                        >
                          <Text align="left" pl="2">
                            I want to collect the fees with a different account
                          </Text>
                        </Checkbox>
                      </Flex>
                      <Collapse in={diffCollectedFees} animateOpacity>
                        <FormControl id="collectedFees" mt={6}>
                          <Input
                            type="text"
                            placeholder={t("addressForCollectedFees")}
                            border={buttonBorderStyle}
                            rounded="sm"
                            {...register("collectedFeesAddress", {
                              pattern: /^0x[a-fA-F0-9]{40}$/i,
                            })}
                          />
                          <FormHelperText
                            textAlign="left"
                            color={colorSubtitle}
                          >
                            {t("collectedFeesHint")}
                          </FormHelperText>
                        </FormControl>
                      </Collapse>
                    </FormControl>
                  </NewPortfolioContainer>
                </Flex>
              </Flex>
            </Collapse>

            {/* STEP 2 */}
            <Collapse in={step === FORM_STEP.TWO} animateOpacity w="100%">
              <Flex
                w="full"
                align="flex-start"
                justify="center"
                mt="4"
                px="6"
                minH="520px"
              >
                <Flex direction="column" w="33.333%">
                  {/* ==== SLIPPEAGE ==== */}
                  <NewPortfolioContainer
                    title={t("slippageType")}
                    description={t("slippageTypeDescription")}
                  >
                    <FormControl>
                      <Title title={t("slippageTypeLabel")} />
                      <Flex w="full" align="flex-start">
                        <Select
                          border={buttonBorderStyle}
                          variant="outline"
                          isDisabled={isLoading}
                          rounded="sm"
                          value={maxSlippage}
                          onChange={(e) => setMaxSlippage(e.target.value)}
                        >
                          {SLIPPAGE_UI_OPTIONS.map((option) => {
                            return (
                              <option value={option} key={option}>
                                {mulBN(option, "100")}%
                              </option>
                            );
                          })}
                        </Select>
                      </Flex>
                    </FormControl>
                  </NewPortfolioContainer>
                </Flex>

                <Flex direction="column" w="33.333%">
                  {/* ==== WHITESTRATEGIES ==== */}
                  <NewPortfolioContainer
                    title={t("strategyType")}
                    description={t("strategyTypeDescription")}
                  >
                    <FormControl>
                      <Title title={t("strategyTypeLabel")} />

                      <Flex w="full" align="flex-start">
                        <Select
                          border={buttonBorderStyle}
                          variant="outline"
                          isDisabled={isLoading}
                          rounded="sm"
                          value={strategyType}
                          onChange={(e) => setStrategyType(e.target.value)}
                        >
                          <option value="2">
                            Only Whitelisted Strategies (recommended)
                          </option>
                          <option value="0">Any Strategies</option>
                        </Select>
                      </Flex>
                    </FormControl>
                  </NewPortfolioContainer>

                  {/* ==== WHITE TOKEN ==== */}
                  <NewPortfolioContainer
                    title={t("tokenSwapType")}
                    description={t("tokenSwapTypeDescription")}
                  >
                    <FormControl>
                      <Title title={t("tokenSwapTypeLabel")} />
                      <Flex w="full" align="flex-start">
                        <Select
                          border={buttonBorderStyle}
                          variant="outline"
                          isDisabled={isLoading}
                          rounded="sm"
                          value={tokenSwapType}
                          onChange={(e) => setTokenSwapType(e.target.value)}
                        >
                          <option value="2">
                            Manager can only swap to whitelisted tokens
                            (recommended)
                          </option>
                          <option value="0">
                            Manager can swap to any token
                          </option>
                        </Select>
                      </Flex>
                    </FormControl>
                  </NewPortfolioContainer>
                </Flex>
                <Flex direction="column" w="33.333%"></Flex>
              </Flex>
            </Collapse>
            {/* 
            </>
          )} */}
            <FootButton />
          </form>
        ))}
    </Flex>
  );
}

export default CreatePortfolio;

const ErrorMsg = ({ children }) => {
  return (
    <Text textAlign="right" color={SETTINGS.RED} fontSize="0.75rem">
      {children}
    </Text>
  );
};

const Title = ({ title, icon }) => {
  const { colorSubtitle } = useStyles();

  return (
    <FormLabel>
      <Text textAlign="left" mt="1" fontSize="1rem" color={colorSubtitle}>
        {title}
      </Text>
      {icon ? icon : null}
    </FormLabel>
  );
};

const NumButton = ({ stateStep, setStep, step }) => {
  const { colorTitle } = useStyles();

  return (
    <RoundedIconButton
      onClick={() => setStep(step)}
      border={stateStep === step ? `1px solid ${colorTitle}` : undefined}
      icon={
        <Text
          fontSize="1.15rem"
          pb="1"
          pr="0.5"
          color={stateStep === step ? colorTitle : undefined}
        >
          {step === FORM_STEP.ONE && "1"}
          {step === FORM_STEP.TWO && "2"}
        </Text>
      }
    />
  );
};

const ShowErrors = ({ errors }) => {
  const { t } = useTranslation();

  let errorMsg = "";
  if (errors.wallet1?.type === "required") {
    errorMsg = errorMsg + " | Owner main address: " + t("fieldRequired");
  } else if (errors.uniqueName?.type === "required") {
    errorMsg = errorMsg + " | Portfolio Name: " + t("fieldRequired");
  } else if (errors.wallet1?.type === "pattern") {
    errorMsg = errorMsg + " | Owner main address: " + t("completeValidAddress");
  } else if (errors.wallet2) {
    errorMsg = errorMsg + " | Owner Address: " + t("completeValidAddress");
  } else if (errors.depositFee) {
    errorMsg = errorMsg + " | Deposit fee: " + t("fieldRequired");
  } else if (errors.performanceFee) {
    errorMsg = errorMsg + " | Performance fee: " + t("fieldRequired");
  }
  console.log("errors", errors);

  return (
    <ShowFormError
      errorMessage={
        errorMsg === ""
          ? null
          : `Please solve the following problems with the form ${errorMsg}`
      }
    />
  );
};
