import { utils } from "ethers";
import { useMemo } from "react";
import { useEthers } from "@usedapp/core";
import useToken from "./useToken";
import {
  feePercentage,
  formatBNPrice,
  getAmountToDeposit,
  greaterOrEqualThanMax,
  isTheSameAddress,
  mulBN,
  parseBN,
  subBN,
  sumBN,
} from "../helpers";
import { TRANSACTION_TYPE } from "../constants";
import useTransactionComposition from "./useTransactionComposition";
import useAvailableBalance from "./useAvailableBalance";
import { useApplicationState } from "../context/appContext";

// returns a function that will execute a join in strategy,
export function useJoinCallback(
  portfolioAddress,
  strategy = "", //strategy address
  tokenStrategy = "", //strategy address
  token = "",
  amount = "0",
  depositFee = "0"
) {
  const { account, chainId, library } = useEthers();

  const tokenInfo = useToken(token); // token info to get decimals
  const same = isTheSameAddress(tokenStrategy, token);
  const { vaultBalance, agreementBalance } = useAvailableBalance(
    token,
    tokenInfo.decimals
  );
  const txAmount = parseBN(amount, tokenInfo.decimals);
  const totalVaultBalance = parseBN(vaultBalance, tokenInfo.decimals);
  // const totalAgreementBalance = parseBN(agreementBalance, tokenInfo.decimals);
  // depositedAmountWithFee is the amount deposited with the fee applied
  const depositedAmountWithFee = getAmountToDeposit(
    txAmount,
    totalVaultBalance,
    depositFee.toString()
  )
  // dont use more than available
  // const maxAmountAvailableToDeposit = greaterOrEqualThanMax(depositedAmountWithFee, totalAgreementBalance) ? totalAgreementBalance : depositedAmountWithFee
  const vaultAndDepositedAmountWithFee = sumBN(totalVaultBalance,depositedAmountWithFee)
  const maxAmountAvailableToJoin = greaterOrEqualThanMax(vaultAndDepositedAmountWithFee, txAmount) ? txAmount : vaultAndDepositedAmountWithFee 
     
  const { slippage } = useApplicationState();

  const tx = {
    account: portfolioAddress,
    strategy: strategy.toString(),
    tokenIn: token.toString(),
    tokenOut: tokenStrategy.toString(),
    slippage: mulBN(slippage, "1e18"),
    // onlyAmountToDepositWithoutFee is the rest needed to complete a join
    onlyAmountToDepositWithoutFee: subBN(txAmount, totalVaultBalance),
    amount: maxAmountAvailableToJoin,
  };

  console.log('useJoinCallback 1 txAmount', formatBNPrice(txAmount, tokenInfo.decimals))
  console.log('useJoinCallback 2 totalVaultBalance', formatBNPrice(totalVaultBalance, tokenInfo.decimals))
  console.log('useJoinCallback 3 depositFee', formatBNPrice(depositFee))
  console.log('useJoinCallback 4 depositedAmountWithFee', formatBNPrice(depositedAmountWithFee, tokenInfo.decimals))
  console.log('useJoinCallback 5', formatBNPrice(vaultAndDepositedAmountWithFee, tokenInfo.decimals), formatBNPrice(txAmount, tokenInfo.decimals), formatBNPrice(maxAmountAvailableToJoin, tokenInfo.decimals))

  const { state, send, query, queryState } = useTransactionComposition(
    token,
    tokenInfo.decimals,
    amount,
    same,
    tx,
    TRANSACTION_TYPE.JOIN
  );

  return useMemo(() => {
    // if (!strategy || !library || !account || !chainId || !amount) {
    //   return { state: null, callback: null, error: "Missing dependencies" };
    // }

    return {
      state: state,
      queryCallback: async function onQuery() {
        return query();
      },
      queryState,
      callback: async function onJoin() {
        return send();
      },
    };
  }, [strategy, library, account, chainId, amount, state, tx, send]);
}
