import { useEffect, useState } from "react";
import { Box, Button, Flex, Text } from "@chakra-ui/react";
import OverviewEmptyHeader from "./OverviewEmptyHeader";
import { SETTINGS } from "../helpers/settings";
import { useTranslation } from "react-i18next";
import { MdLooksOne, MdLooksTwo } from "react-icons/md";
import { useEthers } from "@usedapp/core";
import Web3Status from "./Web3/Web3Status";
import { Redirect } from "react-router-dom";
import { ManagerAddress } from "./Web3/ManagerAddress";
import EditPortfolioModal from "./EditPortfolio";
import { useStyles } from "../hooks/useStyles";

const STEPS = {
  ONE: "one",
  TWO: "two",
};

export default function WelcomeOnBoard(props) {
  const { t } = useTranslation();
  const { colorBody } = useStyles();

  const [createNew, setCreateNew] = useState(false);
  const [step, setStep] = useState(STEPS.ONE);
  const { account } = useEthers();

  // check if web3 account is logged
  useEffect(() => {
    if (account) {
      setStep(STEPS.TWO);
    }
  }, [account]);

  if (createNew) {
    return (
      <Redirect
        to={{
          pathname: "/app/new",
        }}
      />
    );
  }

  return (
    <>
      <OverviewEmptyHeader />
      {/* <Container w={{ base: '100%', md: '40rem', lg: '40rem' }}> */}
      <Flex
        p={8}
        w="full"
        direction="column"
        rounded="md"
        mt="6"
        color={colorBody}
      >
        <Text textTransform="uppercase" fontWeight="500" fontSize="1.5rem">
          Welcome to {t("projectName")} 🎉
        </Text>
        <Text fontSize="1.5rem">
          We are few steps away to start managing many portfolios at the same
          time:
        </Text>

        <Flex my="10">
          {/* STEP 1 */}
          <Step
            number={<MdLooksOne fontSize="1.5rem" />}
            done={account}
            active={step === STEPS.ONE}
          >
            <StepOneContent account={account} />
          </Step>
          {/* STEP 2 */}
          <Step
            number={<MdLooksTwo fontSize="1.5rem" />}
            active={step === STEPS.TWO}
            end={true}
          >
            <StepTwoContent setCreateNew={setCreateNew} step={step} />
          </Step>
        </Flex>
      </Flex>
      {/* </Container> */}
    </>
  );
}

const Step = ({ number, children, end, done, active }) => {
  const { inputBorderStyleDisabled, colorBody } = useStyles();

  return (
    <Flex direction="column" align="center" w="50%" color={colorBody}>
      <Flex
        w="full"
        align="center"
        fontSize="2rem"
        color={
          done ? SETTINGS.VIOLET_LIGHT : active ? SETTINGS.WHITE : SETTINGS.GRAY
        }
      >
        <Flex
          color={
            done
              ? SETTINGS.VIOLET_LIGHT
              : active
              ? SETTINGS.WHITE
              : SETTINGS.GRAY
          }
          border={
            !active
              ? done
                ? `1px solid ${SETTINGS.VIOLET_LIGHT}`
                : inputBorderStyleDisabled
              : `1px solid ${SETTINGS.WHITE}`
          }
          p="4"
          rounded="full"
        >
          {number}
        </Flex>
        {!end && (
          <Box
            h="2px"
            bg={done ? SETTINGS.VIOLET_LIGHT : SETTINGS.GRAY}
            w="full"
          />
        )}
      </Flex>
      <Flex
        pt="10"
        w="full"
        direction="column"
        align="flex-start"
        pr="20"
        color={active ? "white" : SETTINGS.GRAY}
      >
        {children}
      </Flex>
    </Flex>
  );
};

const StepOneContent = ({ account }) => {
  return (
    <>
      <Text fontSize="1.5rem" mb="5">
        First you need to connect you manager account. By now Metamask its the
        best option.
      </Text>
      {account ? (
        <Flex direction="column">
          <Text fontSize="1.25rem" color={SETTINGS.GREY_LIGHT} pb="2">
            Manager Account:
          </Text>
          <ManagerAddress />
        </Flex>
      ) : (
        <Web3Status />
      )}
    </>
  );
};

const StepTwoContent = ({ setCreateNew, step }) => {
  return (
    <>
      <Text fontSize="1.5rem">
        Now you can choose between create a new agreement or add an existing
        one.
      </Text>
      <Button
        onClick={() => setCreateNew(true)}
        variant="outline"
        rounded="full"
        size="lg"
        my="5"
        fontWeight="400"
        isDisabled={step !== STEPS.TWO}
      >
        <Text>Create Portfolio</Text>
      </Button>
      <EditPortfolioModal isDisabled={step !== STEPS.TWO}>
        <Button
          variant="outline"
          rounded="full"
          size="lg"
          fontWeight="400"
          isDisabled={step !== STEPS.TWO}
        >
          <Text>Add existing Portfolio </Text>
        </Button>
      </EditPortfolioModal>
    </>
  );
};
