import { useState } from "react";
import {
  Flex,
  Text,
  FormErrorMessage,
  Divider,
  Button,
  Input,
} from "@chakra-ui/react";
import { useForm } from "react-hook-form";
import { RiAddBoxLine } from "react-icons/ri";
import { useManagerDispatch, useManagerState } from "../context/managerContext";
import PortfoliosSelectorItem from "./PortfolioSelectorItem";
import { Redirect } from "react-router-dom";
import { SETTINGS } from "../helpers/settings";
import { useStyles } from "../hooks/useStyles";
import { useTranslation } from "react-i18next";
export default function PortfoliosEditor(props) {
  const { t } = useTranslation();
  const { colorModalTitle, inputBorderStyle } = useStyles();
  const [createNew, setCreateNew] = useState(false);
  const { addPortfolio, removePortfolio } = useManagerDispatch();
  const managerState = useManagerState();

  const {
    register,
    handleSubmit,
    formState: { errors },
  } = useForm();

  const onSubmit = (data) => {
    console.log("adding new portfolio");
    addPortfolio({ address: data.porfolioAddress });
  };

  const removePortfolioFromList = (address) => {
    console.log("removing portfolio", address);
    removePortfolio({ address: address });
  };

  const onCreatePortfolio = () => {
    setCreateNew(true);
    props.onClose();
  };

  if (createNew) {
    return (
      <Redirect
        to={{
          pathname: "/app/new",
        }}
      />
    );
  }

  return (
    <Flex direction="column" w="full" >
      {managerState.portfolios ? (
        managerState.portfolios.map((portfolio, i) => {
          return (
            <PortfoliosSelectorItem
              key={portfolio.address}
              {...portfolio}
              index={i + 1}
              removePortfolioFromList={removePortfolioFromList}
              portfolioSelected={managerState.portfolioSelected?.address}
              totalPortfolios={managerState?.portfolios.length}
            />
          );
        })
      ) : (
        <Text py={6} textAlign="center" color="gray.600">
          {t("emptyPortfolios")}
        </Text>
      )}
       <Button
        h="4rem"
        onClick={() => onCreatePortfolio()}
        border={inputBorderStyle}
        variant="outline"
        my="4"
        mx="4"
        size="xl"
        rounded="sm"
        color={colorModalTitle}

      >
        <Text fontSize="1.15rem">{t("createNewPortfolio")}</Text>
      </Button>

      <form onSubmit={handleSubmit(onSubmit)}>
        <Flex mb="4" mt="2" mx="4">
          <Input
            placeholder={t("orAddPortfolio")}
            size="lg"
            borderBottom={inputBorderStyle}
            mr={1}
            variant="flushed"
            rounded="sm"
            borderColor={inputBorderStyle}
            color={colorModalTitle}
            _placeholder={{color: colorModalTitle}}
            {...register("porfolioAddress", { required: true })}
          />
          <Button
            h="3rem"
            w="10rem"
            ml="2"
            type="submit"
            rounded="sm"
            border={inputBorderStyle}
            variant="ghost"
            color={colorModalTitle}
          >
            <Text fontSize="1.15rem">{t("addPortfolio")}</Text>
          </Button>
        </Flex>
        {errors.porfolioAddress && (
          <FormErrorMessage>{t("fieldRequired")}</FormErrorMessage>
        )}
      </form>


     
    </Flex>
  );
}
