import { getSubgraphqlURI } from "../helpers/enviroment";
import { useManagerState } from "../context/managerContext";
import { useQuery } from "react-query";
import { request, gql } from "graphql-request";
import { REFETCH_INTERVAL_ACCOUNT } from "../constants";

const useStrategiesFromAccount = () => {
  const { portfolioSelected } = useManagerState();
  const address = portfolioSelected?.address?.toLowerCase();

  return useQuery(
    ["strategyData", address], 
  () => fetchStrategiesFromAccount(address),
  {
    // Refetch the data every second
    refetchInterval: REFETCH_INTERVAL_ACCOUNT,
  }
  );
};

const fetchStrategiesFromAccount = async (agreementAddress) => {
  const { account } = await request(
    getSubgraphqlURI(),
    gql`
        {
          account (id: "${agreementAddress}"){
           id
           balances {
             id
             token {
               id
             } 
             amount
           }
           strategies {
             id
             shares
             invested
             account {
               id
             }
             strategy {
              id
              token {
                id
              }
              metadata
              whitelisted
              shares
             
            }
           }
          }
        }
      `
  );
  return account;
};

export default useStrategiesFromAccount;
