import { useCallback, useEffect, useState } from "react";
import {
  Flex,
  Text,
  Image,
} from "@chakra-ui/react";
import { APPLICATION_MODAL } from "../context/appContext";
import { useModalOpen, useModalParams } from "../hooks";
import { useManagerState } from "../context/managerContext";
import { useParams } from "react-router";
import TokenInput from "../components/TokenInput";
import {
  formatBNPercentage,
  getAssetImg,
  getTokenSymbol,
  isEmpty,
  updateLocalTransactionState,
} from "../helpers";
import { ChainId, shortenAddress, useEthers } from "@usedapp/core";
import { constants } from "ethers";
import useToken from "../hooks/useToken";
import { TransactionStatus } from "../components/common/TransactionStatus";
import { useJoinCallback } from "../hooks/useJoinCallback";
import useAgreement from "../hooks/useAgreement";
import { StrategyTitle } from "../components/common/StrategyTitle";
import useAvailableBalance from "../hooks/useAvailableBalance";
import { PortfolioLine } from "../components/common/PortfolioLine";
import { STRATEGY_VIEWS } from "../constants";
import useStrategyWithoutData from "../hooks/useStrategyWithoutData";
import { EstimateBox } from "../components/strategy/EstimateBox";
import StButton from "../components/StButton";
import { ShowFormError } from "../components/ShowFormError";
import { Hr } from "../components/strategy/Hr";
import { BoxContainer, LineBox } from "../components/common/BoxContainer";
import useIPFSMetadata from "../hooks/useIPFSMetadata";

export default function JoinStrategy() {
  // here we receive the strategy selected
  const modalParams = useModalParams();
  // selected strategy
  let { strategy } = useParams();
  // swe need the agreements fees
  const { data: agreement } = useAgreement();
  // watch account
  const { account, chainId } = useEthers();
  const [view, setView] = useState(STRATEGY_VIEWS.START);
  // address of the selected strategy
  const [selectedStrategy, setSelectedStrategy] = useState(
    constants.AddressZero
  );
  const [{ errorMessage, txHash }, setState] = useState({
    errorMessage: undefined,
    txHash: undefined,
  });
  const [tokenAmounts, setTokenAmounts] = useState({});
  // Modal management
  const joinStrategyModalOpen = useModalOpen(
    APPLICATION_MODAL.JOIN_TO_STRATEGY
  );
  const { data: strategyInfo } = useStrategyWithoutData(selectedStrategy);
  const { data: stMetadata } = useIPFSMetadata(
    strategyInfo?.metadata
  );
  const { portfolioSelected } = useManagerState();
  const availableBalance = useAvailableBalance();
  const tokenSelected = getTokenSymbol(tokenAmounts);

  const {
    state: stateCallback,
    callback: joinCallback,
    queryCallback,
    queryState,
  } = useJoinCallback(
    portfolioSelected.address,
    selectedStrategy,
    strategyInfo?.token?.id,
    tokenSelected.length ? tokenSelected : strategyInfo?.token?.id,
    tokenAmounts[tokenSelected],
    agreement?.portfolio?.depositFee
  );

  // always resetting view
  useEffect(() => {
    if (joinStrategyModalOpen) {
      setView(STRATEGY_VIEWS.START);
      setState({
        errorMessage: undefined,
        txHash: undefined,
      });
    }
  }, [joinStrategyModalOpen]);

  // Enable the feature to use the modal from an URL
  useEffect(() => {
    setSelectedStrategy(
      modalParams ? modalParams : strategy ? strategy : constants.AddressZero
    );
    setView(STRATEGY_VIEWS.SELECTED);
    setState({
      errorMessage: undefined,
      txHash: undefined,
    });
  }, [modalParams, strategy]);

  // Control action for the InputElement component.
  const handleInputTokenChange = (token, amount, reset = false) => {
    console.log("handleInputTokenChange", token, amount, reset, tokenAmounts);
    if (token === null && amount === null) {
      setTokenAmounts({});
    } else {
      if (reset) {
        setTokenAmounts({ [token]: amount });
      } else {
        setTokenAmounts({ ...tokenAmounts, [token]: amount }).then(() => {});
      }
    }
    if (STRATEGY_VIEWS.ESTIMATE === view) {
      resetJoinView();
    }
  };

  const handleConfirmMessage = () => {
    if (isEmpty(tokenAmounts)) {
      setState({
        errorMessage: "Please complete token amount",
        txHash: undefined,
      });
    } else {
      setState({
        errorMessage: undefined,
        txHash: undefined,
      });
      queryCallback();
      setView(STRATEGY_VIEWS.ESTIMATE);
    }
  };

  const handleEstimateMessage = () => {
    if (isEmpty(tokenAmounts)) {
      setState({
        errorMessage: "Please complete token amount",
        txHash: undefined,
      });
    } else {
      setState({
        errorMessage: undefined,
        txHash: undefined,
      });
      setView(STRATEGY_VIEWS.CONFIRM);
    }
  };

  // check for tx status None Mining Success Failed Exception
  // the callback with the state changed of the transaction
  useEffect(() => {
    updateLocalTransactionState(stateCallback, setState, setView);
  }, [stateCallback]);

  const resetJoinView = () => {
    setView(STRATEGY_VIEWS.SELECTED);
    setState({
      errorMessage: undefined,
      txHash: undefined,
    });
  };

  const setOverBalance = (bool) => {
    setState({
      errorMessage: bool ? "Not enough balance" : undefined,
      txHash: undefined,
    });
  };

  const handleJoin = useCallback(() => {
    setView(STRATEGY_VIEWS.LOADING);
    setState({ errorMessage: undefined, txHash: undefined });
    try {
      joinCallback();
    } catch (error) {
      console.error("joinCallback ", error);
    }
  }, [account, portfolioSelected, joinCallback, tokenAmounts]);

  function getModalBody() {
    // START State
    if (
      view === STRATEGY_VIEWS.SELECTED ||
      view === STRATEGY_VIEWS.START ||
      view === STRATEGY_VIEWS.ESTIMATE
    ) {
      return (
        <Flex direction="column" w="full" pt="4">
          <StrategyTitle
            stMetadata={stMetadata}
            hideDescription={true}
            px="4"
          />
          <Hr />

          <BoxContainer title="Select Token Amount" px="4">
            <TokenInput
              startToken={strategyInfo?.token?.id}
              onChange={handleInputTokenChange}
              balance={availableBalance}
              setOverBalance={setOverBalance}
              isChangeable={true}
              px="4"
            />
          </BoxContainer>
        </Flex>
      );
    }

    // Confirm window
    if (view === STRATEGY_VIEWS.CONFIRM) {
      return (
        <Flex direction="column" w="full" py="4">
          <StrategyTitle
            strategy={strategyInfo}
            hideDescription={true}
            px="4"
          />
          <Hr />

          <LineBox title="Portfolio Selected:">
            <PortfolioLine address={portfolioSelected.address} />
          </LineBox>

          <LineBox title="Manager:">
            <Text fontWeight="600">{shortenAddress(account)}</Text>
          </LineBox>

          <LineBox title="Deposit Fee:">
            <Text fontWeight="600">
              {formatBNPercentage(agreement?.portfolio?.depositFee)}
            </Text>
          </LineBox>

          <LineBox title="Amounts:" last="true">
            <Flex w="full" direction="column" fontWeight="600">
              {tokenAmounts &&
                Object.keys(tokenAmounts).map((token) => {
                  return (
                    <ShowTokenAmount
                      key={token}
                      address={token}
                      amount={tokenAmounts[token]}
                    />
                  );
                })}
            </Flex>
          </LineBox>
        </Flex>
      );
    }
  }

  return (
    <>
      <Flex w="full" justify="center">
        <TransactionStatus
          status={view}
          txHash={txHash}
          chainId={chainId}
          errorMessage={errorMessage}
          errorChildren={
            <Flex align="center" textDecor="underline" onClick={resetJoinView}>
              <Text px="2">Try again</Text>
            </Flex>
          }
          doneMessage={`Transaction success!`}
          doneChildren={null}
        />
        {getModalBody()}
      </Flex>
      <ShowFormError errorMessage={errorMessage} />
      <EstimateBox
        queryState={queryState}
        address={strategyInfo?.token?.id}
        strategyInfo={strategyInfo}
        stMetadata={stMetadata}
        show={view === STRATEGY_VIEWS.ESTIMATE}
      />
      <Flex w="full">
        {(view === STRATEGY_VIEWS.SELECTED ||
          view === STRATEGY_VIEWS.START) && (
          <StButton
            onClick={handleConfirmMessage}
            isDisabled={view === STRATEGY_VIEWS.START}
          >
            Estimate Join
          </StButton>
        )}
        {view === STRATEGY_VIEWS.ESTIMATE && (
          <StButton
            onClick={handleEstimateMessage}
            isDisabled={view === STRATEGY_VIEWS.START}
          >
            Join
          </StButton>
        )}
        {view === STRATEGY_VIEWS.CONFIRM && (
          <StButton onClick={handleJoin}>Confirm Join</StButton>
        )}
      </Flex>
    </>
  );
}

const ShowTokenAmount = ({ address, amount }) => {
  const token = useToken(address);
  return (
    <Flex align="center">
      <Image src={getAssetImg(token[ChainId.Mainnet])} boxSize="1.5rem" />
      <Text px={2}>{amount}</Text>
      <Text>{token?.name}</Text>
    </Flex>
  );
};
