import { getSubgraphqlURI } from "../helpers/enviroment";

import { useMemo, useState } from "react";
import { greaterOrEqualThanMax, greaterThanMax, withBalance } from "../helpers";
import useAvailableBalance from "./useAvailableBalance";
import { COMPOSITION_TYPE } from "../constants";

const useTransactionCheckBalance = (tk, decimals = 18, amount = "0") => {
  const [txType, setTxType] = useState(null);

  const { totalBalance, agreementBalance, vaultBalance } = useAvailableBalance(
    tk,
    decimals,
    amount
  );

  // console.log(
  //   "useCheckBalance 1-> ",
  //   totalBalance,
  //   agreementBalance,
  //   vaultBalance,
  //   amount,
  //   txType
  // );

  return useMemo(() => {
    // 1) vault has enough balance enoughVaultBalance
    // 2) If vault has balance but not enough and agreement has balance vaultAndAgreementBalance
    // 3) If vault its empty and Agreement has enough balance enougAgreementBalance
    // 4) If Agreement has not balance noBalanceAtAll
    const enoughVaultBalance =
      withBalance(vaultBalance) && greaterOrEqualThanMax(vaultBalance, amount);
    const vaultAndAgreementBalance =
      withBalance(vaultBalance) &&
      greaterThanMax(amount, vaultBalance) &&
      withBalance(agreementBalance) &&
      greaterOrEqualThanMax(totalBalance, amount);
    const enoughAgreementBalance =
      withBalance(agreementBalance) &&
      !withBalance(vaultBalance) &&
      greaterOrEqualThanMax(agreementBalance, amount);
    const noBalanceAtAll =
      withBalance(agreementBalance) && withBalance(vaultBalance);

    console.log(
      "useCheckBalance 2-> ",
      enoughVaultBalance,
      vaultAndAgreementBalance,
      enoughAgreementBalance,
      noBalanceAtAll,
      totalBalance,
      agreementBalance,
      vaultBalance,
      amount,
      txType
    );

    if (enoughVaultBalance) {
      setTxType(COMPOSITION_TYPE.ENOUGH_VAULT_BALANCE);
    } else if (vaultAndAgreementBalance) {
      setTxType(COMPOSITION_TYPE.VAULT_AND_AGREEMENT_BALANCE);
    } else if (enoughAgreementBalance) {
      setTxType(COMPOSITION_TYPE.ENOUGH_AGREEMENT_BALANCE);
    } else if (noBalanceAtAll) {
      setTxType(COMPOSITION_TYPE.NO_BALANCE_AT_ALL);
    }

    return {
      agreementBalance,
      vaultBalance,
      totalBalance,  
      txType,
    };
  }, [vaultBalance, amount, agreementBalance, totalBalance, txType]);
};

export default useTransactionCheckBalance;
