import { Flex, Text, Button, Badge } from "@chakra-ui/react";
import { useEtherBalance, useEthers, useLookupAddress } from "@usedapp/core";
import { useWalletModalToggle } from "../../hooks";
import { useStyles } from "../../hooks/useStyles";
import { shortenAddress, formatBNPrice, CHAIN_ID_NAME } from "../../helpers";
import { getDefaultChainID } from "../../helpers/enviroment";
import Identicon from "../Identicon";
import WalletModal from "./WalletModal";
import { useTranslation } from "react-i18next";
import { MimicBadge } from "../common/MimicBadge";

function StatusIcon({ connector }) {
  if (connector) {
    return <Identicon />;
  }
  return null;
}

function Web3StatusInner() {
  const { t } = useTranslation();
  const { buttonBorderStyle, bgHover } = useStyles();

  const { account, connector, error, chainId } = useEthers();
  const ens = useLookupAddress();

  const userBalance = useEtherBalance(account);
  const toggleWalletModal = useWalletModalToggle();

  const ConnectButton = () => {
    return (
      <Button
        variant="ghost"
        color="current"
        rounded="0"
        onClick={toggleWalletModal}
        border={buttonBorderStyle}
        _hover={{ bg: bgHover }}
      >
        {t("connect")}
      </Button>
    );
  };

  if (account) {
    if (chainId !== getDefaultChainID()) {
      return (
        <Badge colorScheme="red" mx="5">
          {t("wrongNetwork")}
        </Badge>
      );
    }
    return (
      <>
        {/* TODO: REMOVE, this is for testing purpose */}
        <MimicBadge mx="4" schema="light">
          {CHAIN_ID_NAME[chainId]}
        </MimicBadge>
        <Flex align="center">
          <Text fontSize="1.15rem" px="1rem" fontWeight="600">
            {`${formatBNPrice(userBalance)} ETH`}
          </Text>
          <Button
            py="0.5rem"
            px="0.7rem"
            fontSize="lg"
            variant="ghost"
            color="current"
            rounded="0"
            onClick={toggleWalletModal}
            border={buttonBorderStyle}
            _hover={{ bg: bgHover }}
          >
            <Flex align="center">
              <Flex align="center">
                <Text mr={2} fontSize="1.15rem" fontWeight="400">
                  {ens ? ens : shortenAddress(account)}
                </Text>
                {connector && <StatusIcon connector={connector} />}
              </Flex>
            </Flex>
          </Button>
        </Flex>
      </>
    );
  } else if (error) {
    return (
      <Flex onClick={toggleWalletModal}>
        <Text>{t("wrongNetwork")}</Text>
        <ConnectButton />
      </Flex>
    );
  } else {
    return <ConnectButton />;
  }
}

export default function Web3Status() {
  const { active } = useEthers();

  if (!active) {
    return null;
  }

  return (
    <>
      <Web3StatusInner />
      <WalletModal />
    </>
  );
}
