import { IconButton } from "@chakra-ui/react";
import { useStyles } from "../../hooks/useStyles";

export const RoundedIconButton = ({
  onClick,
  target,
  rel,
  icon,
  ariaLabel,
  border,
}) => {
  const { buttonBorderStyle } = useStyles();

  return (
    <IconButton
      aria-label={ariaLabel}
      target={target}
      rel={rel}
      size="md"
      ml="3"
      onClick={onClick}
      border={border ? border : buttonBorderStyle}
      opacity="0.7"
      variant="ghost"
      icon={icon}
      rounded="full"
      display={{ md: "inline-flex", base: "none" }}
    />
  );
};
