import { Button, Text } from "@chakra-ui/react";
import { RiShareBoxLine } from "react-icons/ri";
import { shortenAddress } from "../../helpers";
import { Link } from 'react-router-dom';
import { useEthers, getExplorerAddressLink } from "@usedapp/core";

export const AddressLink = props => {
  const {chainId} = useEthers();
  return (
    <Link
    to={{ pathname: getExplorerAddressLink(props.address, chainId) }}
    target="_blank"
    rel="noopener noreferrer"
  >
    <Button rightIcon={<RiShareBoxLine />} variant="link" {...props}>
      <Text isTruncated>
        {props.full ? props.address : shortenAddress(props.address)}
      </Text>
    </Button>
    </Link>
  );
};
