import {
  createContext,
  useCallback,
  useContext,
  useEffect,
  useMemo,
  useReducer,
} from "react";
import { DEFAULT_SLIPPAGE } from "../constants";
import { reducer, ActionTypes } from "./appReducer";
import { useLocalStorage } from "../hooks/useLocalStorage";

export const APPLICATION_MODAL = {
  WALLET: "WALLET",
  JOIN_TO_STRATEGY: "JOIN_TO_STRATEGY",
  EXIT_FROM_STRATEGY: "EXIT_FROM_STRATEGY",
  WITHDRAW_FROM_VAULT: "WITHDRAW_FROM_VAULT",
  TRANSFER_TO_AGREEMENT: "TRANSFER_TO_AGREEMENT",
};

const initialContext = {
  openModal: null,
  modalParams: null,
  blockNumber: 0,
  slippage: DEFAULT_SLIPPAGE,
};

const StateContext = createContext(initialContext);
const DispatchContext = createContext(undefined);

export const ApplicationProvider = ({ children }) => {
  const [storage, setStorage] = useLocalStorage("appManager");

  const [state, dispatch] = useReducer(
    reducer,
    storage ? storage : initialContext
  );

  useEffect(() => {
    setStorage({ ...initialContext, slippage: state.slippage });
  }, [state]);

  return (
    <StateContext.Provider value={state}>
      <DispatchContext.Provider value={dispatch}>
        {children}
      </DispatchContext.Provider>
    </StateContext.Provider>
  );
};

export const useApplicationState = () => {
  return useContext(StateContext);
};

export const useApplicationDispatch = () => {
  const dispatch = useContext(DispatchContext);

  if (dispatch === undefined) {
    throw new Error("useApplicationDispatch must be used within a Provider");
  }

  const setOpenModal = useCallback(
    (modal, params) => {
      dispatch({ type: ActionTypes.SET_OPEN_MODAL, modal, params });
    },
    [dispatch]
  );

  const updateBlockNumber = useCallback(
    (blockNumber) => {
      dispatch({ type: ActionTypes.SET_BLOCK_NUMBER, ...blockNumber });
    },
    [dispatch]
  );

  const updateGlobalSlippage = useCallback(
    (slippage) => {
      dispatch({ type: ActionTypes.SET_SLIPPAGE, slippage: slippage });
    },
    [dispatch]
  );

  return useMemo(
    () => ({
      setOpenModal,
      updateBlockNumber,
      updateGlobalSlippage,
    }),
    [setOpenModal, updateBlockNumber, updateGlobalSlippage]
  );
};
