import { Flex, Text, Button } from "@chakra-ui/react";
import { useEffect } from "react";
import { APPLICATION_MODAL } from "../context/appContext";
import {
  useExitStrategyModalToggle,
  useJoinStrategyModalToggle,
  useModalOpen,
} from "../hooks";
import { isTheSameUrl } from "../helpers";
import { useTranslation } from "react-i18next";
import useStrategyAgreementHoldings from "../hooks/useStrategyAgreementHoldings";
import { StrategyTitle } from "./common/StrategyTitle";
import { useStyles } from "../hooks/useStyles";
import { useEthers } from "@usedapp/core";
import { SETTINGS } from "../helpers/settings";
import useIPFSMetadata from "../hooks/useIPFSMetadata";
import { StrategyStats } from "./common/StrategyStats";
import { isDevelopment, isProduction } from "../helpers/enviroment";

export default function StrategyCard({
  account,
  chainId,
  strategy,
  param,
  m,
  w,
}) {
  const { buttonBorderStyle, bgHover } = useStyles();
  const joinStrategyModalOpen = useModalOpen(
    APPLICATION_MODAL.JOIN_TO_STRATEGY
  );
  const toggleJoinStrategyModal = useJoinStrategyModalToggle(strategy?.id);
  const toggleExitStrategyModal = useExitStrategyModalToggle(strategy?.id);

  // update strategy data for the account
  const { data: holdings, isLoading } = useStrategyAgreementHoldings(
    strategy?.id
  );
  // metadata
  const { data: stMetadata, isLoadingMetadata } = useIPFSMetadata(
    strategy?.metadata
  );

  useEffect(() => {
    if (isTheSameUrl(param, strategy[chainId]) && !joinStrategyModalOpen) {
      setTimeout(() => {
        toggleJoinStrategyModal();
      }, 1000);
    }
  }, [param]);

  const joined = holdings?.shares; // check if the user is joined at this strategy

  return (
    <Flex
      direction="column"
      minW={"300px"}
      mb={m}
      h="full"
      w={w}
      _hover={{
        bg: bgHover,
      }}
      borderRight={buttonBorderStyle}
      borderBottom={buttonBorderStyle}
      className="grid-item"
    >
      <Flex direction="column" mt="4" px={6} h="full">
        <StrategyTitle stMetadata={stMetadata} isLoading={isLoadingMetadata} />
      </Flex>
      <StrategyStats
        strategy={strategy}
        stMetadata={stMetadata}
        isLoading={isLoading}
        holdings={holdings}
      />

      {/* DEBUG */}
      {/* {!isProduction() && <Text color="orange">{strategy?.id}</Text>} */}

      <ActionButtons
        toggleJoinStrategyModal={toggleJoinStrategyModal}
        toggleExitStrategyModal={toggleExitStrategyModal}
        joined={joined}
        account={account}
      />
    </Flex>
  );
}

const ActionButtons = ({
  toggleJoinStrategyModal,
  toggleExitStrategyModal,
  joined,
}) => {
  const { t } = useTranslation();
  const { buttonBorderStyle } = useStyles();
  const { activateBrowserWallet, account } = useEthers();

  return (
    <Flex w="full">
      {account ? (
        <>
          <StCardButton
            w={joined ? "50%" : "full"}
            borderTop={buttonBorderStyle}
            borderRight={joined ? buttonBorderStyle : undefined}
            onClick={toggleJoinStrategyModal}
          >
            {t("join")}
          </StCardButton>
          {joined && (
            <StCardButton
              onClick={toggleExitStrategyModal}
              isDisabled={!joined}
              borderTop={buttonBorderStyle}
              w="50%"
            >
              {t("exit")}
            </StCardButton>
          )}
        </>
      ) : (
        <StCardButton
          onClick={activateBrowserWallet}
          borderTop={buttonBorderStyle}
          w="full"
        >
          {t("connect")}
        </StCardButton>
      )}
    </Flex>
  );
};

const StCardButton = ({
  children,
  w,
  borderRight,
  onClick,
  isDisabled,
  borderTop,
}) => {
  return (
    <Button
      w={w}
      h={SETTINGS.BUTTON_H}
      onClick={onClick}
      isDisabled={isDisabled}
      variant="outline"
      rounded="0"
      textTransform="capitalize"
      fontSize="1.25rem"
      fontWeight="400"
      borderTop={borderTop}
      borderRight={borderRight}
    >
      <Text fontWeight="500" fontSize="1.5rem">
        {children}
      </Text>
    </Button>
  );
};
