import { utils } from 'ethers';
import { Contract } from '@ethersproject/contracts';
import { useMemo } from 'react';
import { useContractFunction } from '@usedapp/core';
import AgreementFactoryABI from '../constants/abis/AgreementFactory.json';
import { feePercentage, filterEmptyElements } from '../helpers';
import { AGREEMENT_FACTORY_ADDRESS } from '../constants';

// returns a function that will execute a create in strategy,
export function useCreateCallback(
  chainId,
  name = '',
  ownerWallet1,
  ownerWallet2,
  depositFee = 0,
  performanceFee = 0,
  feeCollector,
  maxSwapSlippage = 0, 
  managerMainAddress,
  managerBackupAddress,
  strategyType, 
  tokensType
) {

  const AgreementFactoryInterface = new utils.Interface(AgreementFactoryABI);
  const contract = new Contract(
    AGREEMENT_FACTORY_ADDRESS[chainId],
    AgreementFactoryInterface
  );

  const { state, send, events } = useContractFunction(contract, 'create', {
    transactionName: 'CreatePortfolio',
    gasLimit: 250000,
  });

  return useMemo(() => {
    return {
      state: state,
      events: events,
      callback: async function onCreate() {
        // creating a new portfolio
        const tx = {
          name: name.toString(),
          depositFee0: feePercentage(depositFee.toString()),
          depositFee: utils.parseEther(feePercentage(depositFee.toString())),
          withdrawFee: '0',
          performanceFee0: feePercentage(performanceFee.toString()), // testing
          performanceFee: utils.parseEther(
            feePercentage(performanceFee.toString())
          ),
          feeCollector: feeCollector.toString(),
          maxSwapSlippage: utils.parseEther(
            maxSwapSlippage.toString()
          ),
          managers0: [managerMainAddress, managerBackupAddress], // testing
          managers: filterEmptyElements([
            ownerWallet2,
            managerMainAddress,
            managerBackupAddress,
          ]),
          withdrawers0: [ownerWallet1, ownerWallet2], // testing
          withdrawers: filterEmptyElements([ownerWallet1, ownerWallet2]),
          tokens: [],
          allowedTokens: tokensType,
          strategies: [],
          allowedStrategies: strategyType
        };

        console.log('useCreateCallback tx: ', tx);

        return send(
          tx.name,
          tx.feeCollector,
          tx.depositFee,
          tx.withdrawFee,
          tx.performanceFee,
          tx.maxSwapSlippage,
          tx.managers,
          tx.withdrawers,
          tx.tokens,
          tx.allowedTokens,
          tx.strategies,
          tx.allowedStrategies,
        );
      },
    };
  }, [
    name,
    ownerWallet1,
    depositFee,
    performanceFee,
    state,
    feeCollector,
    managerMainAddress,
    managerBackupAddress,
    ownerWallet2,
    strategyType,
    send,
  ]);
}
