import { Button, Flex, useColorModeValue, Text } from "@chakra-ui/react";
import { shortenAddress, useEthers } from "@usedapp/core";
import { useTranslation } from "react-i18next";
import { SETTINGS } from "../../helpers/settings";
import Identicon from "../Identicon";

export function ManagerAddress({ py, mb }) {
  const { t } = useTranslation();

  const borderStyle = useColorModeValue(
    SETTINGS.BORDER.LIGHTER,
    `1px solid ${SETTINGS.PRIMARY}`
  );

  const { account } = useEthers();

  return (
    <Button
      px="0.7rem"
      size="lg"
      fontSize="lg"
      variant="ghost"
      color="current"
      border={borderStyle}
      rounded="0"
      py={py ? py : "1rem"}
      mb={mb ? mb : undefined}
    >
      <Flex align="center">
        <Flex align="center">
          <Text mr={2} fontSize="1rem" fontWeight="400">
            {account ? shortenAddress(account) : t("noAddress")}
          </Text>
          <Identicon />
        </Flex>
      </Flex>
    </Button>
  );
}
