import { useMemo, useState } from 'react';
import numeral from 'numeral';
import { useEffect } from 'react';
import { useManagerState } from '../context/managerContext';
import { divBN, greaterThanMax, subBN, sumBN } from '../helpers';

export default function useTotalPortfolioValue() {
  const [totalLocked, setTotalLocked] = useState(0);
  const [totalGains, setTotalGains] = useState(0);
  const [totalApy, setTotalApy] = useState(0);
  const managerState = useManagerState();
  const { portfolioSelected } = managerState;

  useEffect(() => {
    if (portfolioSelected.balance && portfolioSelected.gains) {
      
      const total = Object.values(portfolioSelected.balance).reduce(
        (sum, tokenValue) => {
          const n = tokenValue ? tokenValue : 0;

          // return sumBN(sum, n)
          return numeral(sum).add(n).value();
        },
        0
      );

      const gains = Object.values(portfolioSelected.gains).reduce(
        (sum, tokenValue) => {
          const n = tokenValue ? tokenValue : 0;
          // return numeral(sum).add(n).value();
          return sumBN(sum, n)
        },
        0
      );

      const totalApy = Object.values(portfolioSelected.sumApy).reduce(
        (sum, tokenValue) => {
          const n = tokenValue ? tokenValue : 0;
          // console.log('totalApy apu', tokenValue, n, sum, sumBN(sum,n))
          return sumBN(sum,n);
        },
        0
      );

      const generalAPy = divBN(totalApy, total)

      setTotalLocked(total);
      setTotalGains(gains);
          console.log('totalApy apu', total, gains, totalApy)

      setTotalApy(greaterThanMax(generalAPy, '0') ? generalAPy : '0');
    } else {
      console.log('No Balance');
      console.log(portfolioSelected);
    }
  }, [portfolioSelected.balance]);

  return {totalLocked, totalGains, totalApy};
}
