import { Flex, Text, Collapse, Image } from "@chakra-ui/react";
import { TransactionHashLink } from "./TransactionHashLink";
import { TRANSACTION_STATUS } from "../../constants";
import { SETTINGS } from "../../helpers/settings";

export const TransactionStatus = ({
  status,
  txHash,
  chainId,
  doneMessage,
  doneChildren,
  errorChildren,
  errorMessage,
}) => {
  const showStatus =
    status === TRANSACTION_STATUS.LOADING ||
    status === TRANSACTION_STATUS.MINING ||
    status === TRANSACTION_STATUS.DONE ||
    status === TRANSACTION_STATUS.ERROR;
  return (
    <Collapse in={showStatus} animateOpacity w="100%">
      <MimicLoader status={status} />
      <LoadingView show={status === TRANSACTION_STATUS.LOADING} />
      <MiningView
        hash={txHash}
        chainId={chainId}
        show={status === TRANSACTION_STATUS.MINING}
      />
      <DoneView
        doneMessage={doneMessage}
        doneChildren={doneChildren}
        hash={txHash}
        chainId={chainId}
        show={status === TRANSACTION_STATUS.DONE}
      />
      <ErrorView
        errorChildren={errorChildren}
        errorMessage={errorMessage}
        hash={txHash}
        chainId={chainId}
        show={status === TRANSACTION_STATUS.ERROR}
      />
    </Collapse>
  );
};

const LoadingView = ({ show }) => {
  return (
    <Collapse in={show} animateOpacity w="100%">
      <Text mx="2" my="4" fontSize="1.5rem" textAlign="center">
        Please confirm the wallet windows
      </Text>
    </Collapse>
  );
};

const MiningView = ({ hash, chainId, show }) => {
  return (
    <Collapse in={show} animateOpacity w="100%">
      <Flex align="center" justify="center" w="full" direction="column">
        <Text mx="2" my="4" fontSize="1.5rem" textAlign="center">
          Waiting for Tx confirmation
        </Text>
        <TxHash chainId={chainId} hash={hash} />
      </Flex>
    </Collapse>
  );
};

const ErrorView = ({ errorChildren, hash, chainId, show }) => {
  return (
    <Collapse in={show} animateOpacity w="100%">
      <Flex align="center" justify="center" w="full" direction="column">
        <Text mx="2" my="4" fontSize="1.5rem">
          Opps, Houston...
        </Text>
        <TxHash chainId={chainId} hash={hash} />
        {errorChildren && errorChildren}
      </Flex>
    </Collapse>
  );
};

const DoneView = ({ doneMessage, doneChildren, hash, chainId, show }) => {
  return (
    <Collapse in={show} animateOpacity w="100%">
      <Flex align="center" justify="center" w="full" direction="column">
        <Text mx="2" my="4" fontSize="1.5rem">
          {doneMessage}
        </Text>
        <TxHash chainId={chainId} hash={hash} />
      </Flex>
      {doneChildren && doneChildren}
    </Collapse>
  );
};


const TxHash = ({ hash, chainId }) => {
  if (!hash) return null;
  return (
    <Flex align="center" mb="10">
      <TransactionHashLink hash={hash} chainId={chainId} />
    </Flex>
  );
};

const MimicLoader = ({ status }) => {
  const play = status === TRANSACTION_STATUS.MINING;
  const done = status === TRANSACTION_STATUS.DONE
  const animate = SETTINGS.MAIN_ANIMATION;
  const freeze = SETTINGS.FREEZE_ANIMATION;
  const success = SETTINGS.SUCCESS_ANIMATION;
  return (
    <Flex justify="center" h="350px" w="full">
      <Image pla src={play ? animate : done ? success : freeze} />
    </Flex>
  );
};
