import useStrategies from "./useStrategies";
import { useMemo } from "react";
import {
  formatBNPrice,
  formatPercentage,
  getApy,
  getHoldings,
  getTokenDecimals,
  isAddress,
  mulBN,
  sumBN,
} from "../helpers";
import useStrategiesFromAccount from "./useStrategiesFromAccount";
import { useEthers } from "@usedapp/core";
import { fetchStrategiesStats } from "./useStrategyStats";
import { getIPFSurl, IPFSfetcher } from "./useIPFSMetadata";

const useStrategiesBalance = () => {
  const { data: accountData, isLoading } = useStrategiesFromAccount();
  const {  chainId } = useEthers();

  return useMemo(() => {
    let totalInvested = {};
    let totalBalance = {};
    let totalSumApy = {};

    const invested = accountData?.strategies?.forEach(async (st) => {
      const tk = isAddress(st?.strategy?.token?.id);
      const tokenInfo = getTokenDecimals(tk, chainId)

      // some magic to get all balancer per token
      const IPFSurl = getIPFSurl(st?.strategy?.metadata)
      const stMetadata = await IPFSfetcher(IPFSurl)
      const stStats =  await fetchStrategiesStats(stMetadata?.subgraph, st?.strategy?.id)

      const holdings = getHoldings(
        st?.shares,
        st?.strategy?.shares,
        stStats?.deposited
      );

      // calc APY
      const lengthRate = st?.strategy?.rates?.length;
      const initialRate = st?.strategy?.rates ? st?.strategy?.rates[1] : 0;
      const finalRate = st?.strategy?.rates ? st?.strategy?.rates[lengthRate - 1] : 0;
      const apy = formatPercentage(getApy(initialRate, finalRate));

      //  (APYGS1 * USDI1 + APYGS2 * USDI2 + APYGS3 * USDI3) / (USDI1 + USDI2 + USDI3)
      const apyPerAmount = mulBN(apy, formatBNPrice(holdings, tokenInfo.decimals));

      // console.log("apyPerAmount", apyPerAmount, apy, formatBNPrice(holdings), tokenInfo.name);

      if (totalInvested.hasOwnProperty(tk)) {
        totalInvested[tk] = sumBN(totalInvested[tk], st?.invested);
        totalBalance[tk] = sumBN(totalBalance[tk], holdings);
        totalSumApy[tk] = sumBN(totalSumApy[tk], apyPerAmount);
      } else {
        totalInvested[tk] = holdings;
        totalBalance[tk] = holdings;
        totalSumApy[tk] = apyPerAmount;
      }
    });

    console.log("apyPerAmount", totalBalance);

    return {
      invested: totalInvested,
      holdings: totalBalance,
      sumApy: totalSumApy,
      isLoading: isLoading,
    };
  }, [accountData]);
};

export default useStrategiesBalance;
