import {
  Modal,
  ModalOverlay,
  ModalContent,
  ModalHeader,
  ModalBody,
  ModalCloseButton,
  Text,
} from "@chakra-ui/react";
import { APPLICATION_MODAL } from "../context/appContext";
import {
  useModalOpen,
  useJoinStrategyModalToggle,
  useExitStrategyModalToggle,
  useWithdrawFromVaultModalToggle,
  useTransferToAgreementModalToggle,
} from "../hooks";
import { useStyles } from "../hooks/useStyles";
import JoinStrategy from "../pages/JoinStrategy";
import ExitStrategy from "../pages/ExitStrategy";
import WithdrawFromVault from "../pages/WithdrawFromVault";
import { SETTINGS } from "../helpers/settings";
import TransferToAgreement from "../pages/TransferToAgreement";

export default function ManagerModal() {
  const { bgModal, colorModalTitle } = useStyles();
  // // Modal management
  const joinStrategyModalOpen = useModalOpen(
    APPLICATION_MODAL.JOIN_TO_STRATEGY
  );
  const toggleJoinStrategyModal = useJoinStrategyModalToggle();

  const exitStrategyModalOpen = useModalOpen(
    APPLICATION_MODAL.EXIT_FROM_STRATEGY
  );
  const toggleExitStrategyModal = useExitStrategyModalToggle();
  const withdraFromVaultModalOpen = useModalOpen(
    APPLICATION_MODAL.WITHDRAW_FROM_VAULT
  );
  const toggleWithdrawFromVaultModal = useWithdrawFromVaultModalToggle();

  const transferToAgreementModalOpen = useModalOpen(
    APPLICATION_MODAL.TRANSFER_TO_AGREEMENT
  );
  const toggleTransferToAGreementModal = useTransferToAgreementModalToggle();

  function close() {
    if (joinStrategyModalOpen) return toggleJoinStrategyModal();
    if (exitStrategyModalOpen) return toggleExitStrategyModal();
    if (withdraFromVaultModalOpen) return toggleWithdrawFromVaultModal();
    if (transferToAgreementModalOpen) return toggleTransferToAGreementModal();
  }
  return (
    <Modal
      isOpen={
        joinStrategyModalOpen ||
        exitStrategyModalOpen ||
        withdraFromVaultModalOpen ||
        transferToAgreementModalOpen
      }
      onClose={close}
    >
      <ModalOverlay backdropFilter="blur(2px)!important" />
      <ModalContent bg={bgModal} rounded="md">
        <ModalHeader color={colorModalTitle} px="4">
          {joinStrategyModalOpen && "Join Strategy"}
          {exitStrategyModalOpen && "Exit Strategy"}
          {withdraFromVaultModalOpen && "Withdraw"}
          {transferToAgreementModalOpen && "Transfer funds"}
        </ModalHeader>
        <ModalCloseButton color={SETTINGS.VIOLET_DARK} mt="2" />
        <ModalBody p="0" color={colorModalTitle}>
          {joinStrategyModalOpen ? <JoinStrategy /> : null}
          {exitStrategyModalOpen ? <ExitStrategy /> : null}
          {withdraFromVaultModalOpen ? <WithdrawFromVault /> : null}
          {transferToAgreementModalOpen ? <TransferToAgreement /> : null}
        </ModalBody>
      </ModalContent>
    </Modal>
  );
}
