import { useMemo } from 'react';
import { addressEqual, ChainId } from '@usedapp/core';
import { useCoingeckoTokenPrice } from '@usedapp/coingecko';
import useSWR from 'swr';
import axios from 'axios';
import { formatUSDC, formatAmountToWei, amountPerPrice } from '../helpers';
import { USDC_TOKEN } from '../constants/tokens';

const fetcher = url => axios.get(url).then(res => res.data);

export default function useUSDCPrice(TOKEN_ADDRESS=undefined, amount=0) {
  const price = useCoingeckoTokenPrice(TOKEN_ADDRESS, 'usd');
  return amountPerPrice(amount, price);
  // return useMemo(() => {
  //   return amountPerPrice(amount, price);
  // }, [TOKEN_ADDRESS, amount. price]);
}

// 1inch USDC price provider
function use1InchUSDCPrice(currencyAddress, amount) {
  const tokenAmount = formatAmountToWei(amount);
  const url = `https://api.1inch.exchange/v3.0/1/quote?fromTokenAddress=${currencyAddress}&toTokenAddress=0xa0b86991c6218b36c1d19d4a2e9eb0ce3606eb48&amount=${tokenAmount}`;

  const { data, error } = useSWR(url, fetcher);

  const isUSDC = value => {
    return addressEqual(value, USDC_TOKEN[ChainId.Mainnet]);
  };

  return useMemo(() => {
    return {
      price: isUSDC(currencyAddress)
        ? formatUSDC(tokenAmount, 18)
        : data?.toTokenAmount && formatUSDC(data?.toTokenAmount),
      isLoading: !error && !data,
      isError: error,
    };
  }, [tokenAmount, data]);
}
