export default function Layout({ children }) {
  const sidebar = useDisclosure();
  const { bgModal } = useStyles();
  const { portfolioSelected } = useManagerState();
  // buttons disabled if portfolios are empty
  const isDisabled = !portfolioSelected?.address;
  return (
    <>
      <SidebarContent
        display={{ base: "none", lg: "unset" }}
        disabled={isDisabled}
      />
      <Drawer
        isOpen={sidebar.isOpen}
        onClose={sidebar.onClose}
        placement="left"
      >
        <DrawerOverlay />
        <DrawerContent>
          <SidebarContent w="full" borderRight="none" disabled={isDisabled} />
        </DrawerContent>
      </Drawer>
      <Box ml={{ base: 0, lg: 60 }} transition=".3s ease">
        <LogoIcon display={{ base: "inline-flex", md: "none" }} />
        <ControlNetwork />

        <Flex
          as="header"
          align="center"
          justify="space-between"
          w="full"
          px="4"
          h={{ md: SETTINGS.HEADER_H, base: "6rem" }}
        >

          <PortfoliosDropDown />
          <IconButton
            aria-label="Menu"
            display={{ base: "inline-flex", md: "none" }}
            onClick={sidebar.onOpen}
            icon={<FiMenu />}
            bg={bgModal}
            size="sm"
          />
          <Box />
          <Flex align="center" display={{ md: "inline-flex", base: "none" }}>
            {true && <Web3Status />}
            <Box w={4} />
            <HeaderMenu showWeb3={true} />
          </Flex>
        </Flex>
        <Box as="main">{children}</Box>
      </Box>
      {!isProduction() && <ReactQueryDevtools />}
    </>
  );
}

