import { Flex, Text, Heading, Spinner } from "@chakra-ui/react";
import useTotalPortfolioValue from "../hooks/useTotalPortfolioValue";
import { formatPrice } from "../helpers";
import AnimatedNumber from "animated-number-react";
import { GreenHeader } from "./common/GreenHeader";
import { useTranslation } from "react-i18next";
import { useStyles } from "../hooks/useStyles";
import { SETTINGS } from "../helpers/settings";

export default function OverviewHeader({ isLoading }) {
  const { t } = useTranslation();
  const locked = useTotalPortfolioValue();
  return (
    <GreenHeader>
      <TitleBox
        title={t("valueLocked")}
        isLoading={isLoading}
        value={locked.totalLocked}
      >
        ${" "}
        <AnimatedNumber value={locked.totalLocked} formatValue={formatPrice} />
      </TitleBox>

      <TitleBox
        title={t("totalGains")}
        isLoading={isLoading}
        value={locked.totalGains}
      >
        $ {locked.totalGains}
      </TitleBox>

      <TitleBox
        title={t("portfolioApy")}
        isLoading={isLoading}
        value={locked.totalApy}
        noBorder={true}
      >
        {locked.totalApy} %
      </TitleBox>
    </GreenHeader>
  );
}

const TitleBox = ({ children, title, isLoading, value, noBorder }) => {
  const { buttonBorderStyle, colorHighlighted } = useStyles();
  const tl = value?.toString()?.length;

  return (
    <Flex
      w={{ md: "33.333%", base: "full" }}
      minH={SETTINGS.HEADER_OVERVIEW_H}
      justify="center"
      borderRight={{ md: !noBorder && buttonBorderStyle, base: undefined }}
    >
      <Flex direction={{ md: "row", base: "column" }} w="full" h="full" align="flex-start" justify="space-between" my="4" mx="6">
        <Flex align="center" justify="center">
          <Text fontWeight="600" textTransform="uppercase">
            {title}
          </Text>
        </Flex>
        <Heading
          textAlign="center"
          as="h2"
          fontSize={fontSize(tl)}
          fontWeight="400"
          color={colorHighlighted}
          noOfLines="1"
        >
          {isLoading ? <Spinner size="md" /> : children}
        </Heading>
      </Flex>
    </Flex>
  );
};

const fontSize = (tl) => {
  if (tl <= 9) return "3rem";
  if (tl <= 15) return "2rem";
  if (tl <= 15) return "1.75rem";
  if (tl >= 25) return "1rem";
};
