import { Flex, Heading, Text, Skeleton } from "@chakra-ui/react";
import { SETTINGS } from "../../helpers/settings";
import { formatBNPercentage } from "../../helpers";
import { AddressLink } from "../common/AddressLink";
import { BoxTitle } from "../typography/BoxTitle";
import { useTranslation } from "react-i18next";

export const AgreementSettingsViewer = ({ agreement, isLoading }) => {
  const {
    id,
    managers,
    withdrawers,
    portfolio,
    allowedStrategies,
    name,
    allowedTokens,
    maxSlippage,
  } = agreement;

  return (
    <Flex w="full" justify="flex-start" minH="450px">
      <Flex
        w="full"
        align="flex-start"
        justify="flex-start"
        overflowX="scroll"
        maxW={SETTINGS.MAX_LAYOUT}
      >
        <Flex direction="column">
          <AddressBox
            title="portfolioAddress"
            address={id}
            isLoading={isLoading}
          />

          <AddressBox
            title="portfolioName"
            description={name}
            isLoading={isLoading}
          />
        </Flex>

        <Flex direction="column">
          <AddressList
            title="ownerAddress"
            addressList={withdrawers}
            isLoading={isLoading}
          />
        </Flex>

        <ManagersList
          title="managerAddress"
          list={managers}
          isLoading={isLoading}
        />

        <Flex direction="column">
          <SquareBox
            title="feeDeposit"
            value={formatBNPercentage(portfolio?.depositFee)}
            isLoading={isLoading}
          />
          <SquareBox
            title="performanceFee"
            value={formatBNPercentage(portfolio?.performanceFee)}
            isLoading={isLoading}
          />
          <AddressBox
            title="slippageType"
            description={formatBNPercentage(maxSlippage)}
            isLoading={isLoading}
          />
        </Flex>

        <AddressBox
          title="collectedFees"
          address={portfolio?.feeCollector}
          isLoading={isLoading}
        />
        <Flex direction="column">
          <AddressBox
            title="strategyType"
            description={allowedStrategies}
            isLoading={isLoading}
          />

          <AddressBox
            title="tokenSwapType"
            description={allowedTokens}
            isLoading={isLoading}
          />
        </Flex>
      </Flex>
    </Flex>
  );
};

const SquareBox = ({ title, value, isLoading }) => {
  return (
    <SimpleBox isLoading={isLoading} title={title}>
      <Heading size="md" fontSize="1.25rem" fontWeight="600">
        {value}
      </Heading>
    </SimpleBox>
  );
};

const AddressList = ({ title, addressList, isLoading }) => {
  return (
    <SimpleBox isLoading={isLoading} title={title}>
      {addressList &&
        addressList.length &&
        addressList?.map((address) => {
          return <TextLine key={address} address={address} />;
        })}
    </SimpleBox>
  );
};

const AddressBox = ({ title, address, description, isLoading }) => {
  return (
    <SimpleBox isLoading={isLoading} title={title}>
      {description && (
        <Text
          key={address}
          textAlign="flex-start"
          fontSize="1.25rem"
          fontWeight="600"
        >
          {description}
        </Text>
      )}
      {address && <TextLine address={address} />}
    </SimpleBox>
  );
};

const SimpleBox = ({ title, isLoading, children }) => {
  const { t } = useTranslation();

  return (
    <Flex
      pb={4}
      px={{ base: 4, md: 6, lg: 8 }}
      direction="column"
      justify="flex-start"
      align="flex-start"
      w="full"
      mb={4}
      minH="95px"
    >
      <Flex direction="column" align="flex-start">
        <BoxTitle fontSize="1.15rem" fontWeight="600">
          {t(title)}:
        </BoxTitle>
        {isLoading ? <Skel /> : children}
      </Flex>
    </Flex>
  );
};

const TextLine = ({ address }) => {
  if (!address) return "";
  return (
    <Text
      key={address}
      textAlign="flex-start"
      fontSize="1.15rem"
      fontWeight="600"
    >
      <AddressLink fontSize="1.25rem" address={address} fontWeight="600" />
    </Text>
  );
};

const Skel = () => {
  return <Skeleton w="70%" height="25px" />;
};

const ManagersList = ({ title, list, isLoading }) => {
  return (
    <SimpleBox isLoading={isLoading} title={title}>
      {list &&
        list.length &&
        list?.map((manager) => {
          return <TextLine address={manager?.id} />;
        })}
    </SimpleBox>
  );
};
