import { useCallback, useEffect, useState } from "react";
import { Flex, Select, Spinner } from "@chakra-ui/react";
import { APPLICATION_MODAL } from "../context/appContext";
import { useModalOpen, useModalParams } from "../hooks";
import { useManagerState } from "../context/managerContext";
import TokenInput from "../components/TokenInput";
import useToken from "../hooks/useToken";
import { useStyles } from "../hooks/useStyles";
import { SETTINGS } from "../helpers/settings";
import useAgreement from "../hooks/useAgreement";
import { useWithdrawCallback } from "../hooks/useWithdrawCallback";
import { shortenAddress, useEthers } from "@usedapp/core";
import { isAddress, updateLocalTransactionState } from "../helpers";
import useAvailableBalance from "../hooks/useAvailableBalance";
import { BoxContainer } from "../components/common/BoxContainer";
import StButton from "../components/StButton";
import { useTranslation } from "react-i18next";

const WITHDRAW_VIEWS = {
  START: "start",
  SELECTED: "selected", // once we selected an strategy
  MINING: "mining",
  LOADING: "loading",
  ERROR: "error",
};

export default function WithdrawFromVault() {
  const { t } = useTranslation();
  const { inputBorderStyle } = useStyles();
  const { chainId } = useEthers();
  const [{ errorMessage, txHash }, setState] = useState({
    errorMessage: undefined,
    txHash: undefined,
  });
  const [tokenAmounts, setTokenAmounts] = useState({});
  const [view, setView] = useState(WITHDRAW_VIEWS.START);
  const [selectedWallet, setSelectedWallet] = useState();
  const [overBalance, setOverBalance] = useState(false);
  const withdraFromVaultModalOpen = useModalOpen(
    APPLICATION_MODAL.WITHDRAW_FROM_VAULT
  );
  // here we receive the asset selected
  const modalParams = useModalParams();
  const [pendingError, setPendingError] = useState();
  const token = useToken(modalParams);
  const managerState = useManagerState();
  const { portfolioSelected } = managerState;
  const { data: agreement, isLoading } = useAgreement();
  const availableBalance = useAvailableBalance(token, token?.decimals);

  const { state: stateCallback, callback: withdrawCallback } =
    useWithdrawCallback(
      portfolioSelected.address,
      token[chainId],
      tokenAmounts?.amount,
      selectedWallet
    );

  const handleWithdraw = useCallback(() => {
    setView(WITHDRAW_VIEWS.LOADING);
    // setState({ errorMessage: undefined, txHash: undefined });
    withdrawCallback();
  }, [portfolioSelected, withdrawCallback, tokenAmounts]);

  // resetting view
  useEffect(() => {
    if (withdraFromVaultModalOpen) {
      setPendingError(false);
      setView(WITHDRAW_VIEWS.START);
    }
  }, [withdraFromVaultModalOpen]);

  const handleInputSelect = (event) => {
    setSelectedWallet(event.target.value);
    setView(WITHDRAW_VIEWS.SELECTED);
  };

  const handleInputTokenChange = (token, amount, reset = false) => {
    if (token === null && amount === null) {
      setTokenAmounts({});
    } else {
      setTokenAmounts({ token: token, amount: amount });
    }
  };

  useEffect(() => {
    // console.log("stateCallback", stateCallback);
    updateLocalTransactionState(stateCallback, setState, setView);
  }, [stateCallback]);

  return (
    <>
      <Flex direction="column" w="full" px="4">
        <BoxContainer title="Select Owner wallet to withdraw">
          {isLoading ? (
            <Spinner />
          ) : (
            <Select
              placeholder="Select Address to Withdraw"
              size="lg"
              w="full"
              rounded="0"
              border={inputBorderStyle}
              onChange={handleInputSelect}
            >
              {portfolioSelected &&
                agreement?.withdrawers &&
                agreement?.withdrawers.map((wl, i) => {
                  return (
                    <option key={`${wl}-${i}`} value={isAddress(wl)}>
                      Wallet {i + 1}: {shortenAddress(wl)}
                    </option>
                  );
                })}
            </Select>
          )}
        </BoxContainer>

        <BoxContainer title="Select Withdraw Amount">
          <Flex direction="column" w="full" rounded="lg" justify="flex-start">
            {modalParams && (
              <TokenInput
                startToken={modalParams}
                onChange={handleInputTokenChange}
                balance={availableBalance}
                setOverBalance={setOverBalance}
              />
            )}
          </Flex>
        </BoxContainer>
      </Flex>
      <StButton
        bg={SETTINGS.VIOLET_LIGHT}
        color={SETTINGS.VIOLET_LIGHTER}
        onClick={handleWithdraw}
        isDisabled={
          !selectedWallet ||
          overBalance ||
          !(view === WITHDRAW_VIEWS.START || view === WITHDRAW_VIEWS.SELECTED)
        }
      >
        {overBalance ? t("noBalance") : t("confirmWithdraw")}
      </StButton>
    </>
  );
}
