import { useContractFunction, useEthers } from "@usedapp/core";
import { Contract } from "@ethersproject/contracts";
import { useMemo } from "react";
import { useContractBatchFunction } from "./useContractBatchFunction";
import VaultABI from "../constants/abis/IVault.json";
import { VAULT_ADDRESS } from "../constants";
import { utils } from "ethers";
import useTransactionCheckBalance from "./useTransactionCheckBalance";
import useCompositionOptions from "./useCompositionOptions";
import { TRANSACTION_TYPE } from "../constants";
import { useQueryVault } from "./useQueryVault";

const useTransactionComposition = (
  tk,
  decimals = 18,
  amount = "0",
  same = true,
  tx,
  strategy_type
) => {
  const { chainId } = useEthers();
  const vaultInterface = new utils.Interface(VaultABI);
  const contract = new Contract(VAULT_ADDRESS[chainId], vaultInterface);
  const {
    txType: comp_type,
    agreementBalance,
    vaultBalance,
    totalBalance,
  } = useTransactionCheckBalance(tk, decimals, amount);

  const intAmount =
    strategy_type === TRANSACTION_TYPE.EXIT ? tx?.ratio : amount;

  const {
    simpleComposition,
    readsOutput,
    arrayCall,
    transactionName,
    functionName,
    queryPos,
    composition,
  } = useCompositionOptions(
    contract,
    tx,
    intAmount,
    same,
    comp_type,
    strategy_type
  );

  const {
    state: queryState,
    query: queryVault,
    error,
  } = useQueryVault(contract);

  const { state: stateFunction, send: sendFunction } = useContractFunction(
    contract,
    functionName,
    {
      transactionName: transactionName,
    }
  );

  const { state: stateBatch, send: sendBatch } = useContractBatchFunction(
    contract,
    {
      transactionName: transactionName,
    }
  );

  return useMemo(() => {
    console.log(`
      ========================================
      account: ${tx.account}
      strategy: ${tx.strategy}
      tokenIn: ${tx.tokenIn}
      tokenOut: ${tx.tokenOut}
      same: ${same}
      amount: ${tx.amount}
      slippage: ${tx.slippage}
      onlyAmountToDeposit: ${tx.onlyAmountToDeposit}
      ----------------------------------------
      agreementBalance: ${agreementBalance}
      vaultBalance: ${vaultBalance}
      totalBalance: ${totalBalance}
      comp_type: ${comp_type}
      tk: ${tk}
      decimals: ${decimals}
      amount: ${amount}
      ----------------------------------------
      queryPos: ${queryPos}
      queryRes: ${queryState?.result}
      ----------------------------------------
      simpleComposition: ${simpleComposition}
      functionName: ${functionName}
      transactionName: ${transactionName}
      arrayCall: ${JSON.stringify(arrayCall)}
      readsOutput: ${readsOutput}
      composition: ${composition}
      ========================================
    `);

    return {
      state: simpleComposition ? stateFunction : stateBatch,
      send: async function onSend() {
        return simpleComposition
          ? sendFunction(...composition, {
              gasLimit: 3500000,
            })
          : sendBatch(arrayCall, readsOutput, {
              gasLimit: 3500000,
            });
      },
      query: async function onQuery() {
        return queryVault(arrayCall, readsOutput, {
          gasLimit: 3500000,
        });
      },
      // added the position from array result.
      queryState: { ...queryState, queryPos: queryPos },
    };
  }, [
    simpleComposition,
    stateFunction,
    stateBatch,
    sendFunction,
    tx,
    sendBatch,
    arrayCall,
    readsOutput,
    queryVault,
    queryState,
    error,
    decimals,
  ]);
};

export default useTransactionComposition;
