import { useMemo } from "react";
import { useEthers } from "@usedapp/core";
import { TRANSACTION_TYPE, VAULT_ADDRESS } from "../constants";
import useToken from "./useToken";
import { parseBN } from "../helpers";
import useTransactionComposition from "./useTransactionComposition";

// returns a function that will execute a join in strategy,
export function useWithdrawCallback(
  portfolioAddress,
  token = "",
  amount = "0",
  recipient = ""
) {
  const { account, chainId } = useEthers();
  const tokenInfo = useToken(token); // token info to get decimals
  const txAmount = parseBN(amount, tokenInfo.decimals);
  
  // console.log("useWithdrawCallback", token, amount, recipient);
  const tx = {
    account: portfolioAddress,
    tokenOut: token.toString(),
    amount: txAmount,
    recipient: recipient.toString(),
  };

  const { state, send, query, queryState } = useTransactionComposition(
    token,
    tokenInfo.decimals,
    amount,
    true,
    tx,
    TRANSACTION_TYPE.WITHDRAW
  );

  return useMemo(() => {
    return {
      state: state,
      queryCallback: async function onQuery() {
        return query();
      },
      queryState,
      callback: async function onWithdraw() {
        return send();
      },
    };
  }, [state, queryState, query, send, account, chainId]);
}